import React from "react";

import img_url from '../../../../custom_assets/default_user.jpg'

const apiService = require("../../services/api.service");
const commonService = require("../../services/common.service")

class LiveclassAteendees extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      liveclass_id: this.props.Liveclass_id,
      attendees: [],
      _token:'',
      attendessShow:false
    };
  }
  componentDidMount() {
    if (commonService.userData() !== null) {
      this.setState({_token:commonService.userData().data._token},()=>{
        this.getAttendess();
      })
    }
    
  }
  async getAttendess() {
   
    let formData = new FormData();
    formData.append('_token', decodeURIComponent(this.state._token))
    formData.append('current_page', 1)
    formData.append('per_page', 50)
    formData.append('eventid', this.state.liveclass_id)
    const headers = {
      'Content-Type': 'multipart/form-data',
    }
    let portfolio_authtoken = commonService.getAuthtoken()
    if (portfolio_authtoken !== null) {
      headers['Authorization'] = 'Bearer ' + portfolio_authtoken
    }
   
    await apiService
      .getAttendeeList_liveclass_data(headers, formData)
      .then((data) => {
        if (data.data.status === 200) {
          this.setState({ attendees: data.data.data.event_attendees },()=>{
            this.setState({attendessShow:true})
          })
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  attendeeShow = () => {
    if (this.state.attendees.length === 0) {
     return <span>no Attendees</span>
    }
    else {
      return this.state.attendees.map((val, i) => {
        if (i < 3) {

          return (
            <li key={i}>
              <a href="javascript:void(0);">
                {
                  val.image_url === "" || undefined || null ?
                  <img src={img_url} alt="user img" />:

                <img src={val.image_url} alt="event img" />
                }
              </a>
            </li>
          );
        }
      });
    }
  };
  RemainngAttendees = () => {
    if (this.state.attendees.length > 3) {
      return (
        <li className="event-attendees-count">
          <a href="#">+{this.state.attendees.length - 3}</a>
        </li>
      );
    }
  };
  render() {
    return (
      <ul className="event-attendees-list">
        {
          !this.state.attendessShow ?
          <div className="load-more-loader"></div>:
          <>
        {this.attendeeShow()}
        {this.RemainngAttendees()}
        </>

        }
      </ul>
    );
  }
}
export default LiveclassAteendees;
