import React from "react";
import { userData, getSubdomain } from "./services/common.service";
import NotFound from "../shared_components/notFound";
import Notfound_withoutlogin from '../shared_components/Notfound_withoutlogin';
import { config } from "../../config";
var getConfig = () => {
    let isLegacy = localStorage.getItem('legacy')
    var conf = config
    console.log(conf, 'isLegacy1')
    if (isLegacy !== null) {
        conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO_LEGACY
    } else {
        conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO
    }
    console.log(conf, 'isLegacy2')
    return conf
}
const APP_BASEURL_IFRAME = getConfig().myportfolio.APP_BASEURL_IFRAME

export default class evidenceBank extends React.Component {

    state = {
        finalUrl: '',
        isLoggedIn: false,
        notLearnerLogged: false
    }
    componentDidMount() {
        if (userData() !== null) {
            this.setState({ isLoggedIn: true });
            this.user_data()
        } else {
            this.setState({ isLoggedIn: false });
        }
    }
    user_data = () => {
        this.setState({
            loginType: userData().data.loginType,
            token: decodeURIComponent(userData().data._token),
            subdomain: getSubdomain()
        }, () => {
            if (this.state.loginType === "learner") {
                this.setState({ notLearnerLogged: false })
                let url = `https://${this.state.subdomain}${APP_BASEURL_IFRAME}/learner/evidence-bank?mst-token="${this.state.token}`
                this.setState({
                    finalUrl: url
                })
                console.log(url)
            } else {
                this.setState({ notLearnerLogged: true })
            }
        })
    }
    gotResponse(val) {
        if (val === 'success')
            this.componentDidMount();
    }
    render() {
        return (
            <>
                {
                    this.state.isLoggedIn ?
                        <>
                            {
                                this.state.notLearnerLogged ?
                                    <Notfound_withoutlogin message="Sorry, you need to login as an Apprentice to access your Evidence bank" />
                                    :
                                    <>
                                        <div className="filter-bar-block">

                                            <div className="filter-bar-holder">

                                            </div>
                                        </div>

                                        <div class="container-fluid courses-block" style={{ padding: '74px 0px 0px', background: '#f4f5fa' }}>
                                            <div>
                                                <iframe src={this.state.finalUrl} title="My Evidence Bank"
                                                    style={{ width: '100%', height: '87vh' }}></iframe>
                                            </div>
                                        </div>

                                    </>
                            }
                        </> :
                        <NotFound message="Sorry, you need to login to Appreticeship Application to access your Programmes" domain="myportfolio" response={(val) => this.gotResponse(val)} />
                }
            </>
        )
    }
}