import React, { Component } from 'react';
import PropTypes from 'prop-types';
import * as moment from 'moment'
import { config } from "../../../../config";
import * as microsoftTeams from "@microsoft/teams-js";

const calender_appid = config.index.APP_ID;
const calender_baseUrl = config.index.APP_BASEURL;
const calender_entityId = "calendar";
const calender_entityLabel = "calendar";
class LiveSessions extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPage: 1,
            perPage: 5,
            liveEvents: this.props.liveEvents
        }
    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    componentWillReceiveProps(nextProps) {
        console.log(this.props)
    }

    handleClickplus = () => {
        this.setState({
            currentPage: this.state.currentPage + 1
        });
    };
    handleClickminus = () => {
        this.setState({
            currentPage: this.state.currentPage - 1
        });
    };

    componentWillUnmount() {

    }
    handleActiveEventsClass = (event) => {
        // console.log(event)
        // let event_duration = event.event_duration
        // let start_time = moment(event.event_time).format()
        // let end_time = moment(event.event_time).add(event_duration , 'minutes').format()
        // let current_time = 
        // console.log(start_time,'starttimeee',end_time,'end timeee',moment(new Date()).format())
        // if(start_time <= current_time  && current_time <=end_time ){

        // }
        // if(event.event_time === '2021-04-30 08:45:00'){
        // event.event_time = moment(new Date()).format()
        if (event.event_live_status === '1') {
            console.log(event)
            let event_duration = event.event_duration
            let start_time = moment(event.event_time).format()
            let end_time = moment(event.event_time).add(event_duration, 'minutes').format()
            let current_time = moment(new Date()).format()
            console.log(start_time, 'starttimeee', end_time, 'end timeee', moment(new Date()).format())
            if ((start_time <= current_time) && (current_time <= end_time)) {
                console.log('bg-green')
                return "bg-green"
            }
        }
        // if (event.event_live_status === '1') {
        //     return "bg-green"
        // }
    }
    utcToLocal = (utc) => {
        var localTime = moment.utc(utc).toDate();

        return moment(localTime).format('MMM Do YYYY h:mm a')
    }
    GetFormattedDateString(date) {
        if (date !== undefined && date !== "") {
            var myDate = new Date(date);
            var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][myDate.getMonth()];
            var week = ['Sun', 'Mon', 'Tues', 'Wed', 'Thu', 'Fri', 'Sat'][myDate.getDay()];
            var str = week + ", " + myDate.getDate() + " " + month
            return str;
        }
        return "";
    }
    viewCalender = () => {
        console.log('calender')

        microsoftTeams.executeDeepLink(
            `https://teams.microsoft.com/l/entity/${calender_appid}/${calender_entityId}?webUrl=${calender_baseUrl}programe-list&label=${calender_entityLabel}`
        );
    }
    render() {
        const { liveEvents, currentPage, perPage } = this.state;
        const indexOfLastTodo = currentPage * perPage;
        const indexOfFirstTodo = indexOfLastTodo - perPage;
        const currentEvents = liveEvents.slice(indexOfFirstTodo, indexOfLastTodo);
        const renderEvents = currentEvents.map((event, i) => {
            return <tr key={i} className={this.handleActiveEventsClass(event)}>
                <td scope="row">
                    <div class="info-wrap capitalize">
                        <div class="svg-wrap">
                            <svg width="16" height="15" viewBox="0 0 16 15" xmlns="http://www.w3.org/2000/svg">
                                <path d="M1.65849 1.94595C1.91717 0.807726 2.92907 0 4.09632 0H11.9034C13.0707 0 14.0826 0.807726 14.3412 1.94595L15.9321 8.94595C16.2877 10.5104 15.0986 12 13.4943 12H6.99996V11H13.4943C14.4569 11 15.1703 10.1062 14.957 9.16757L13.3661 2.16757C13.2109 1.48464 12.6038 1 11.9034 1H4.09632C3.39597 1 2.78883 1.48464 2.63362 2.16757L2.21716 4H1.49996C1.39406 4 1.2897 4.00659 1.18726 4.01937L1.65849 1.94595ZM6.94995 14H12.5C12.7761 14 13 13.7761 13 13.5C13 13.2239 12.7761 13 12.5 13H6.99996V13.5C6.99996 13.6712 6.98275 13.8384 6.94995 14ZM3 9C3.55228 9 4 8.55228 4 8C4 7.44772 3.55228 7 3 7C2.44772 7 2 7.44772 2 8C2 8.55228 2.44772 9 3 9ZM0 6.5C0 5.67157 0.671573 5 1.5 5H4.5C5.32843 5 6 5.67157 6 6.5V13.5C6 14.3284 5.32843 15 4.5 15H1.5C0.671573 15 0 14.3284 0 13.5V6.5ZM1.5 6C1.22386 6 1 6.22386 1 6.5V13.5C1 13.7761 1.22386 14 1.5 14H4.5C4.77614 14 5 13.7761 5 13.5V6.5C5 6.22386 4.77614 6 4.5 6H1.5Z" />
                            </svg>
                        </div>
                        {event?.event_name}
                    </div>
                </td>
                <td><div class="info-wrap"> {this.GetFormattedDateString(event?.event_time)}</div></td>
                <td><div class="info-wrap">{event?.event_duration === 0 ? '-' : event?.event_duration}</div></td>
                {
                    event?.event_live_status === '1' && event?.event_link &&
                    <td className="dashbordtag">
                        <a class="view-btn active" href={event?.event_link + '&callback=' + window.location.origin + '/tab'}>Join</a>
                    </td>
                }
                  {
                    event?.event_type === 'MSTeams' && event?.event_link &&
                    <td className="dashbordtag">
                        <a class="view-btn active" href={event?.event_link + '&callback=' + window.location.origin + '/tab'}>Join</a>
                    </td>
                }
                {/* event_type */}
            </tr>
        });

        return (
            <div className="tab-pane fade show active" id="live-sessions" role="tabpanel" aria-labelledby="sessions-tab">
                <div class="table-responsive sessions-table-list">
                    <table class="table">
                        <thead>
                            <tr>
                                <th scope="col">Name</th>
                                <th width="100px" scope="col">Date</th>
                                <th scope="col">Minutes</th>
                                <th scope="col"></th>
                            </tr>
                        </thead>
                        <tbody>
                            {renderEvents}
                        </tbody>
                    </table>
                </div>

                <div class="showing-pagination">
                    <a className={this.state.currentPage === 1 ? "opacity-arrow" : ''}
                        onClick={this.handleClickminus} >
                        <img src="assets/images/landing/arrow-left.svg" alt="arrow" />
                    </a>
                    <span>Showing {this.state.currentPage} of {" "}
                        {this.state.liveEvents.length / perPage > Math.round(this.state.liveEvents.length / 5)
                            ? Math.round(this.state.liveEvents.length / perPage) + 1
                            : Math.round(this.state.liveEvents.length / perPage)}
                    </span>
                    <a className={this.state.currentPage >= this.state.liveEvents.length / perPage ? "opacity-arrow" : ''}
                        onClick={this.handleClickplus}>
                        <img src="assets/images/landing/arrow-right.svg" alt="arrow" />
                    </a>
                </div>
                <span className="dashbordtag">

                    <a class="view-table-link" onClick={() => this.viewCalender()}>
                        <img src="assets/images/landing/calendar.svg" alt="calendar" />
                        View Calendar
                    </a>
                </span>
            </div>
        );
    }
}

LiveSessions.propTypes = {

};

export default LiveSessions;