import React from "react";
import { appToken } from "../../services/common.service";
import { userData } from "../../services/common.service";
import * as moment from "moment";

const apiService = require("../../services/api.service");
export default class CoursePopularity extends React.Component {
  state = {
    isLoggedin: false,
    coursePopularity: [],
    from: 'week',
    loader: false,
    token: ''

  }
  componentDidMount = () => {
    if (userData() !== null) {
      this.setState({ isLoggedIn: true, token: userData().access_token }, () => this.getCoursePopularity());
    } else this.setState({ isLoggedIn: false });
  }
  getCoursePopularity = async () => {
    this.setState({
      loader: true
    })
    const headers = {
      Authorization: appToken(),

    };
    headers["Access-token"] = this.state.token;

    await apiService.getCoursepopularity(headers, this.state.from)
      .then((response) => {
        if (response.data.status === 200) {
          this.setState({
            loader: false,
            coursePopularity: response.data.data
          })
          return
        } 
        else if(response.data.status === 204){
          this.setState({
            loader: false,
            coursePopularity: [],
            
          })
          return
         
        }else{
          this.setState({
            loader: false,
            coursePopularity: [],
            
          })
          return
        }
      })
      .catch((err) => {
        console.log(err)
      })
  }
  showWeek = (value_for_from) => {
    this.setState({ from: value_for_from }, () => this.getCoursePopularity())
  }
  render() {
    return (
      <div class="col-xs-12 col-sm-12 col-md-6 col-lg-4 col-xl-4 mb-20">
        <div class="course-thumb-list">
          <div class="admin-text-wrap">
            <h4>Course Popularity</h4>
            <span class="small-text">An overview of the most popular courses</span>
            <div class="admin-data-list admin-md-mr">
              <a style={{ cursor: 'pointer' }} class={this.state.from === 'week' ? 'active' : ''}
                onClick={() => this.showWeek('week')} >7 days</a>
              <a style={{ cursor: 'pointer' }} class={this.state.from === 'month' ? 'active' : ''}
                onClick={() => this.showWeek('month')}>30 days</a>
              <a style={{ cursor: 'pointer' }} class={this.state.from === 'year' ? 'active' : ''}
                onClick={() => this.showWeek('year')}>{moment().format('YYYY')}</a>
              <a style={{cursor: 'pointer'}} class={this.state.from === '' ? 'active' : ''} 
                  onClick={()=>this.showWeek('')}>YTD</a>
            </div>
            {
              this.state.loader ? (
                <div className="step step-3 active">
                  <div className="d-flex justify-content-center align-items-center spinner-content">
                    <div
                      style={{ marginTop: "20%" }}
                      className="spinner-border"
                      role="status"
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  </div>
                </div>
              ) : (
                  <React.Fragment>
                    {
                      this.state.coursePopularity.length !== 0 ? (
                        <React.Fragment>
                          {
                            this.state.coursePopularity.slice(0,5).map((course, i) => {
                              return (

                                <div class="custom-progress-wrap" key={i}>
                                  <div class="progress-holder">
                                    <div class="progress common-progress">
                                      <div class="progress-bar progress-bar-primary"
                                        role="progressbar" style={{ width: course?.users_enrollment_count + "%" }} aria-valuenow="25"
                                        aria-valuemin="0" aria-valuemax="100"></div>
                                    </div>
                                    <div class="progress-value">
                                      {parseInt(course?.users_enrollment_count) === 0 ? '0' : course?.users_enrollment_count}
                                    </div>
                                  </div>
                                  <span class="progress-info" title="Health and Safety in the Workplace: First Aid Awareness">{course?.cb_title}</span>
                                </div>
                              )
                            })
                          }
                          <span class="admin-total-info">
                            <span class="box bg-blue-1"></span>
                            Total number of course views
                        </span>
                        </React.Fragment>

                      ) : (
                          <p>No Results Found</p>
                        )
                    }

                  </React.Fragment>
                )
            }
          </div>
        </div>
      </div>
    )
  }
}