import React from "react";
import Carousel from 'react-elastic-carousel'
import * as moment from "moment";
import sidebarContext from '../sidebarContext'
import LiveclassAteendees from './liveclass_attendees'

import img_msteam_logo from '../../../../custom_assets/images/mteams-logo.svg.svg'
import img_video_marketing from '../../../../custom_assets/images/icon/video-marketing.svg'

export default class LiveEventSwiper extends React.Component {
    static contextType = sidebarContext
    constructor(props) {
        super(props)
        this.state = {

        }
    }
    componentDidMount() {
    }
    format(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('hh:mmA')
    }

    formatMM(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('MMM')
    }
    formatDD(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('DD')
    }
    formatYYYY(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('YYYY')
    }

    render() {
        return (
            <Carousel itemsToShow={2} pagination={false}>
                {this.props.liveClasses.map((val, i) => {

                    return (
                        <div key={i} className="swiper-wrapper" style={{ width: '100%', padding: '0px 5px' }}>
                            <div className="swiper-slide">
                                <div className="slider-wrap" onClick={() => this.context.sidebarToggle(val)}>
                                    <div className="slider-header">
                                        <div className="img-wrap">
                                            <img src={img_msteam_logo} alt="event img" />

                                        </div>
                                        <div className="event-video-icon">
                                            <img src={img_video_marketing} alt="event icon" />
                                        </div>
                                    </div>
                                    <div className="slider-body">
                                        <h4 title={val.lc_class_title} className="three-dot">{val.lc_class_title}</h4>
                                        <span>Starts at
                                                {" " + this.format(val.lc_start_time)}
                                        </span>

                                    </div>
                                    <div className="slider-footer">
                                        <div className="event-date-count">
                                            <span>{this.formatMM(val.lc_start_time)}</span>
                                            <strong>{this.formatDD(val.lc_start_time)}</strong>
                                            <span>{this.formatYYYY(val.lc_start_time)}</span>
                                        </div>
                                        <LiveclassAteendees Liveclass_id={val.id} />

                                    </div>
                                </div>
                            </div>
                        </div>)
                })}
            </Carousel>
        )

    }

}