import React, { Component } from 'react'
import Calendar from "../../../shared_components/calendar";
import { NavLink } from 'react-router-dom';
import { config } from '../../../../config';
import * as microsoftTeams from "@microsoft/teams-js";
import { appToken } from "../../../mykademy/services/common.service"
import LiveSessions from './liveSessions';
import Deadlines from './deadlines';

const calender_appid = config.index.APP_ID;
const calender_baseUrl = config.index.APP_BASEURL;
const calender_entityId = "calendar";
const calender_entityLabel = "calendar";
const commnService_mkdmy = require("../../../mykademy/services/common.service");
const commnService_mpflio = require("../../../myportfolio/services/common.service");
const commonService = require('../../../../services/common.service');
const mykademyApiService = require('../../../mykademy/services/api.service')
const portfolioApiService = require('../../../myportfolio/services/api.service')

export default class Upcoming extends Component {

    state = {
        events: [],
        livesessions: [],
        selected_view: 'livesessions',
        deadlines: [],
        accessToken: "",
        result_section: "results-section results--grid",
        open_calendar: false,
        openDropDownBtns: false,
        calendar_events: [],
        portfolio_auth_token: "",
        mykademy_events: [],
        myportfolio_events: [],
        initial_loader: true
    };

    componentDidMount() {
        this.loadEvents();
    }
    async loadEvents() {
        var all_mykademy_events = []
        var all_myportfolio_events = []
        if (commnService_mkdmy.userData() !== null) {
            let mk_events = await this.loadMykademyEvents()
            this.setState({ mykademy_events: mk_events, isMykademyLoggedin: true })
        }
        else {
            this.setState({ isMykademyLoggedin: false })
        }
        if (commnService_mpflio.userData() !== null) {
            let mp_events = await this.loadMyportfolioEvents()
            this.setState({ myportfolio_events: mp_events, isMyportfolioLoggedin: true })
        }
        else {
            this.setState({ isMyportfolioLoggedin: false })
        }
        if (this.state.mykademy_events.custom_events) {
            all_mykademy_events = [
                ...this.state.mykademy_events.assignments,
                ...this.state.mykademy_events.custom_events,
                ...this.state.mykademy_events.live_classes
            ]
        }
        if (this.state.myportfolio_events.live_classes) {
            all_myportfolio_events = this.state.myportfolio_events.live_classes
        }
        var arr = []
        if (all_mykademy_events.length !== 0) {
            all_mykademy_events.map((event) => {
                event['from'] = 'mykademy'
                return event
            })
            let myKademyUpCommingEvents = commonService.getUpcomingEvents(all_mykademy_events);
            if (myKademyUpCommingEvents.length !== 0) {
                console.log(myKademyUpCommingEvents,'myKademyUpCommingEvents')
                this.setState({ events: [...this.state.events, ...myKademyUpCommingEvents] })
            }
            arr = [...all_mykademy_events, ...arr]
        }
        if (all_myportfolio_events.length !== 0) {
            all_myportfolio_events.map((event) => {
                event['from'] = 'myportfolio'
                return event
            })
            let myportifolioUpCommingEvents = commonService.getUpcomingEvents(all_myportfolio_events);
            if (myportifolioUpCommingEvents.length !== 0) {
                console.log(myportifolioUpCommingEvents,'myportifolioUpCommingEvents')
                this.setState({ events: [...this.state.events, ...myportifolioUpCommingEvents] })
            }
            arr = [...all_myportfolio_events, ...arr]
        }
        console.log(this.state.events, 'this.state.events')

        let livesessions = commonService.getLiveSessions(this.state.events);
        let deadlines = commonService.getDeadlines(this.state.events);

        console.log(livesessions, 'livesessions')
        console.log(deadlines, 'deadlines')
        this.setState({
            calendar_events: arr,
            livesessions: livesessions,
            deadlines: deadlines
        })
        this.setState({ initial_loader: false })
    }

    async loadMykademyEvents() {
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': appToken()
        }
        let user = commnService_mkdmy.userData()
        if (user !== null) {
            headers['Access-token'] = user.access_token
        }
        return await mykademyApiService.getAllEvents(headers)
            .then((response) => {
                if (response.data.length !== 0)
                    return response.data.data
                else
                    return []
            })
            .catch((error) => {
                return []
            })
    }

    async loadMyportfolioEvents() {
        var portfolio_access_token = commnService_mpflio.userData().data._token
        var portfolio_auth_token = commnService_mpflio.getAuthtoken()
        const headers = {}
        headers["Content-Type"] = "multipart/form-data";
        headers["Authorization"] = "Bearer " + portfolio_auth_token;
        let formData = new FormData();
        formData.append("_token", decodeURIComponent(portfolio_access_token));
        return await portfolioApiService.getAllEvents(formData, headers)
            .then((response) => {
                console.log(response)
                if (response.data.length !== 0)
                    return response.data.data
                else
                    return []
            })
            .catch((error) => {
                return []
            })
    }

    getWeeksInMonth(a, b) {
        var c = [],
            d = new Date(b, a, 1),
            e = new Date(b, a + 1, 0),
            f = e.getDate();
        var g = 1;
        var h = 7 - d.getDay();
        while (g <= f) {
            c.push({
                start: g,
                end: h,
            });
            g = h + 1;
            h += 7;
            if (h > f) h = f;
        }
        return c;
    }

    GetFormattedDate(date) {
        if (date !== undefined && date !== "") {
            var todayTime = new Date(date);
            var month = todayTime.getMonth() + 1;
            var day = todayTime.getDate();
            var year = todayTime.getFullYear();
            return ` ${month}/${day}/${year} `;
        }
        return "";
    }

    GetFormattedTime(date) {
        if (date !== undefined && date !== "") {
            var todayTime = new Date(date);
            var hours = todayTime.getHours();
            var minutes = todayTime.getMinutes();
            return ` ${hours}:${minutes}`;
        }
        return "";
    }

    openDropDown() {
        this.setState({ openDropDownBtns: !this.state.openDropDownBtns });
        document.getElementById("grid").classList.remove("selected");
        document.getElementById("event").classList.remove("selected");
        document.getElementById("list").classList.remove("selected");
    }

    logout = () => {
        localStorage.clear()
        window.location.reload();
    }
    viewCalender() {
        console.log('calender')

        microsoftTeams.executeDeepLink(
            `https://teams.microsoft.com/l/entity/${calender_appid}/${calender_entityId}?webUrl=${calender_baseUrl}calendar-tab&label=${calender_entityLabel}`
          );
    }
    render() {
        return (
            <div class="col-lg-6">
                <div class="common-shadow-box">
                    {/* <a href="#" class="three-dots-dropdown">
                        <img src="assets/images/landing/three-dots.svg" alt="dot" />
                    </a> */}

                    <div class="welcome-text-wrap">
                        <h4>Upcoming</h4>
                        <p>Select each tab to view your upcoming Live Sessions and Deadlines</p>
                    </div>

                    <ul class="nav nav-tabs live-tabs" id="myTab" role="tablist">
                        <li class="nav-item" role="presentation" onClick={() => this.setState({ selected_view: 'livesessions' })}>
                            <button className={this.state.selected_view === 'livesessions' ? "nav-link active" : 'nav-link'} id="sessions-tab" type="button" role="tab" aria-controls="live-sessions" aria-selected="true">
                                <span class="live-name">Live Sessions</span>
                                <span class="count">{this.state.livesessions.length}</span>
                            </button>
                        </li>
                        <li class="nav-item" role="presentation" onClick={() => this.setState({ selected_view: 'deadlines' })}>
                            <button className={this.state.selected_view === 'deadlines' ? "nav-link active" : 'nav-link'} id="deadlines-tab" type="button" role="tab" aria-controls="live-deadlines" aria-selected="false">
                                <span class="live-name">Deadlines</span>
                                <span class="count">{this.state.deadlines.length}</span>
                            </button>
                        </li>
                    </ul>
                    <div class="tab-content live-tab-content" id="myTabContent">
                        {/* // loader start */}
                        {this.state.initial_loader &&
                            <div class="home-loading" style={{ height: "174px", marginTop: "130px", position: 'relative', left: '50%' }}>
                                <div class="d-flex justify-content-center align-items-center spinner-content">
                                    <div class="spinner-border" role="status">
                                        <span class="visually-hidden">Loading...</span>
                                    </div>
                                </div>
                            </div>
                        }
                        {/* // loader end */}
                        {this.state.selected_view === 'livesessions' && this.state.livesessions.length !== 0 && !this.state.initial_loader ?
                            <LiveSessions liveEvents={this.state.livesessions}></LiveSessions>
                            :
                            this.state.selected_view === 'livesessions' && !this.state.initial_loader &&
                            <div class="tab-pane fade active show" id="live-sessions" role="tabpanel" aria-labelledby="sessions-tab">
                                <div class="nothing-block">
                                    <img src="assets/images/landing/upcoming-img-01.png" alt="Upcoming" />
                                    <p>It looks like you have no upcoming sessions at the moment.</p>
                                </div>
                                <span className="dashbordtag">

                                    <span className="dashbordtag">

                                        <a class="view-table-link" onClick={() => this.viewCalender()}>
                                            <img src="assets/images/landing/calendar.svg" alt="calendar" />
                                        View Calender
                                        </a>
                                    </span>
                                </span>
                            </div>
                        }
                        {
                            this.state.selected_view === 'deadlines' && this.state.deadlines.length !== 0 && !this.state.initial_loader ?
                                <Deadlines deadlineEvents={this.state.deadlines}></Deadlines>
                                :
                                this.state.selected_view === 'deadlines' && !this.state.initial_loader &&
                                <div class="tab-pane fade active show" id="live-deadlines" role="tabpanel" aria-labelledby="deadlines-tab">
                                    <div class="nothing-block">
                                        <img src="assets/images/landing/upcoming-img-02.png" alt="Upcoming" />
                                        <p>Once you’re assigned any deadlines, you’ll find them here.</p>
                                    </div>

                                    <span className="dashbordtag">

                                        <a class="view-table-link" onClick={() => this.viewCalender()}>
                                            <img src="assets/images/landing/calendar.svg" alt="calendar" />
                                        View Calender
                                        </a>
                                    </span>
                                </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}
