import React, { lazy, Suspense } from 'react';
import './App.css';
import 'react-notifications-component/dist/theme.css'
import ReactNotification from 'react-notifications-component';


import * as microsoftTeams from "@microsoft/teams-js";
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";

import Privacy from "./Privacy";
import TermsOfUse from "./TermsOfUse";
import Tab from "./Tab";
import HomeDashboard from "./HomeDashboard";

// import Auth from "../components/auth/auth";



import Sidebar from './shared_components/siderbar'
import Demo from './mykademy/admin/demo';

// auth
import PortfolioAuth from "../components/auth/auth.myportfolio";
import MykademyAuth from "../components/auth/auth.mykademy";
import switchAcc from "./auth/auth.switch"
// auth end

// mykademy
import CourseList from "../components/mykademy/courses/courseList";
import CourseDetail from './mykademy/courses/courseDetail';
import AdminLeanerList from './mykademy/admin/adminLeanerlist';
import AdminCourseList from './mykademy/admin/adminCourseList';
import AdminVideoClassroom from './mykademy/admin/adminVideoclassroom'
import adminEnrollList from './mykademy/admin/adminEnrollList';
import LearnerSignup from './mykademy/admin/learnerSignup';
import AdminReporting from './mykademy/admin/adminReporting/adminReporting'
import AdminCourseManagement from './mykademy/admin/adminCourseManagement'
import LearnerEdit from './mykademy/admin/admin-memberEdit';
import AdminTutor from './mykademy/admin/adminTutor';
import AdminCreateTutor from './mykademy/admin/adminCreateTutor';




// mykademy end


import MarketPlace from './shared_components/marketPlace';


// portfolio
import AdminProgramList from './myportfolio/admin/adminProgramList';
import adminApprentices from './myportfolio/admin/adminApprentices';
import adminVideoClassRoom from './myportfolio/admin/adminVideoClassRoom';
import apprenticesDetailed from './myportfolio/admin/apprenticesDetailed';
import programeDetail from './myportfolio/programes/programmeDetail'
import evidenceBank from './myportfolio/evidencebank'
import adminReporting from './myportfolio/admin/adminReporting'
import registerApprentice from './myportfolio/admin/registerApprentice';
import superAdmin_Employee from './myportfolio/Superadmin/superAdmin_Employee'
import ProgrammeList from '../components/myportfolio/programes/programmeList'
import superAdminprgmlist from './myportfolio/admin/superAdmin'
import NewEmployer from './myportfolio/Superadmin/createnewEmployer'
import EditEnrollmentData from './myportfolio/admin/superAdmin/editEnrollmentData';
import AddProgramm from './myportfolio/admin/superAdmin/addProgramm';
import CalendarTab from './shared_components/plugins/calendarTab';
import AdminProgrammeDetail from './myportfolio/admin/adminProgrammeDetail'
import CommonMemberEdit from './shared_components/commonMemberedit'
import MpLearnerEdit from './myportfolio/admin/adminmemberEdit'
import AdminTutorlist from './myportfolio/admin/adminTutorlist';
import PortfolioAdminCreateTutor from './myportfolio/admin/adminCreateTutor'
import LearningProvider from './auth/onboarding/learningProvider';
import TalkToSales from './auth/talkToSales';
import AddSomeContent from './auth/onboarding/addSomeContent';
import AddSomeMembers from './auth/onboarding/addSomeMembers';
const Auth = lazy(() => import('../components/auth/auth'));
const SignUp = lazy(() => import('./auth/signup'));
/**
 * The main app which handles the initialization and routing
 * of the app.
 */
function App() {
  console.log("%cbuild on 8.30  9 july  2021 ", "color: green; font-size: 20px")

  // Initialize the Microsoft Teams SDK
  microsoftTeams.initialize();

  // Display the app home page hosted in Teams
  return (
    <Router >
      <Suspense fallback={
        <div class="step step-3 active">
          <div class="d-flex justify-content-center align-items-center spinner-content h-380">
            <div class="spinner-border" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          </div>
        </div>
      }>
        {/* <Router forceRefresh> */}
        <ReactNotification />
        <Route exact path="/" component={Tab} />
        <Route exact path="/privacy" component={Privacy} />
        <Route exact path="/termsofuse" component={TermsOfUse} />
        <Route exact path="/tab" component={Tab} />
        <Route exact path="/dashboard" component={HomeDashboard} />
        <Route exact path="/auth" component={Auth} />
        <Route exact path="/auth/signup" component={SignUp} />
        <Route exact path="/switch/:domain" component={switchAcc} />
        <Route exact path="/learning-provider" component={LearningProvider} />
        <Route exact path="/talk-to-sales" component={TalkToSales} />
        <Route exact path="/add-some-content" component={AddSomeContent} />
        <Route exact path="/add-some-members" component={AddSomeMembers} />

        
        {/* <Route exact path="/auth/after-signup" component={afterSignUp}/> */}

        <Route exact path="/auth-portfolio" component={PortfolioAuth} />
        <Route exact path="/auth-mykademy" component={MykademyAuth} />

        <Route exact path="/sidebar" component={Sidebar} />
        <Route exact path="/authentication/:token" component={Demo} />
        <Route exact path="/calendar-tab" component={CalendarTab} />

        <Route exact path="/course-list" component={CourseList} />
        <Route exact path="/course-detail/:course_id" component={CourseDetail} />

        <Route exact path="/mykademy-admin/admin-course-list" component={AdminCourseList} />
        <Route exact path="/mykademy-admin/admin-leaner-list" component={AdminLeanerList} />

        <Route exact path="/mykademy-admin/admin-videoclassroom" component={AdminVideoClassroom} />
        <Route exact path="/mykademy-admin/admin-leaner-list/:learner_id" component={adminEnrollList} />
        <Route exact path="/mykademy-admin/admin-leaner-list/signup/admin-learner-signup" component={LearnerSignup} />
        <Route exact path="/mykademy-admin" component={AdminReporting} />
        <Route exact path="/mykademy-admin/admin-course-management" component={AdminCourseManagement} />
        <Route exact path="/mykademy-admin/admin-leaner-list/edit/admin-member-edit/:learner_id" component={LearnerEdit} />
        
        <Route exact path="/mykademy-admin/admin-tutor" component={AdminTutor} />
        <Route exact path="/mykademy-admin/admin-tutor/create-tutor" component={AdminCreateTutor} />

        

        <Route exact path="/programe-list" component={ProgrammeList} />
        <Route exact path="/myportfolio-admin/admin-program-list" component={AdminProgramList} />
        <Route exact path="/myportfolio-admin/super-admin-program-list" component={superAdminprgmlist} />
        <Route exact path="/myportfolio-admin/super-admin-program-list/edit-enrolment-data" component={EditEnrollmentData} />
        <Route exact path="/myportfolio-admin/super-admin-program-list/add-programme" component={AddProgramm} />
        <Route exact path="/myportfolio-admin/admin-apprentices" component={adminApprentices} />
        <Route exact path="/myportfolio-admin/admin-videoclassroom" component={adminVideoClassRoom} />
        <Route exact path="/myportfolio-admin/admin-apprentices/detailed/:apprentice_id" component={apprenticesDetailed} />
        
        <Route exact path="/myportfolio-admin/admin-apprentices/programme_detailed/:programme_id" component={AdminProgrammeDetail} />

        <Route exact path="/programe-detail/:programe_id" component={programeDetail} />
        <Route exact path="/programe-detail" component={programeDetail} />
        <Route exact path="/evidence-bank" component={evidenceBank} />
        <Route exact path="/myportfolio-admin" component={adminReporting} />
        <Route exact path="/myportfolio-admin/admin-apprentices/register" component={registerApprentice} />
        
        <Route exact path="/myportfolio-admin/admin-tutors" component={AdminTutorlist} />
        <Route exact path="/myportfolio-admin/admin-tutors/create-tutor" component={PortfolioAdminCreateTutor} />

        
        <Route exact path="/myportfolio-admin/superadmin-employee" component={superAdmin_Employee} />

        <Route exact path="/myportfolio-admin/superadmin-employee/superadmin-create-employee" component={NewEmployer} />

        
        <Route exact path="/myportfolio-admin/admin-apprentices/edit/:learner_id" component={MpLearnerEdit} />

        <Route exact path="/memberEdit" component={CommonMemberEdit} />
        <Route exact path="/market-place" component={MarketPlace} />

        {/* ProgrammeList */}
        <Route exact path="/prglist" component={ProgrammeList} />
      </Suspense>
    </Router>
  );
}

export default App;
