import React from "react";
import * as microsoftTeams from "@microsoft/teams-js";



export default class Demo extends React.Component {
  state = {
    vcForm: false,
    learners: [],
    liveClasses: []
  };

  componentDidMount() {
    console.log(this.props.match.params.token)
    if (this.props.match.params.token) {
      // alert('got token')
      microsoftTeams.initialize();
      microsoftTeams.authentication.notifySuccess({ success: true, access_token: this.props.match.params.token });

    } else {
      // alert('not getting token')
      microsoftTeams.initialize();
      microsoftTeams.authentication.notifyFailure({ success: false, access_token: null });
    }
  }

  render() {
    return (
      <div></div>
    )
  }
}
