import React, { Component } from 'react';
import AdminNavbar from "../../shared_components/adminNavbar";
import { subdropdownData, MyportfolioAdminDropdownContents, } from "../../../config";
import { appToken,userData } from "../services/common.service";
import { NavLink } from "react-router-dom";
import country from "../country.json";
import { debounce } from "lodash";

import searchimg from "../../../assets/img/search.svg";
import mobileSearch from "../../../assets/img/mobile-search.svg";
import personimg from "../../../assets/img/landing/person.svg";
import callImg from "../../../assets/img/landing/call.svg";
import mailImg from "../../../assets/img/landing/mail.svg";
import locationImg from "../../../assets/img/landing/location.svg";
import defaultImg from "../../../assets/img/default_user.jpg";
import threeDotsImg from "../../../assets/img/landing/three-dots.svg";

const commonService = require('../services/common.service')
const contrydata = require('../../../services/countries_with_phone');
const myportfolioCommonService = require("../../myportfolio/services/common.service");
const apiService = require('../services/api.service')
class AdminTutor extends Component {
  state = {
    class: false,
    url: '',
    countries: contrydata.contries_with_phone_code,
    loader: false,
    emptyData: false,
    countriesFlagsData: country.data,
    page_limit:10,
    page:1,
    total_turors:0,
    allTutors:[],
    loadmore_loader:false,
    search_keyword:""
  }

  componentDidMount = () => {
    if (userData() !== null) {
      this.setState({ isLoggedIn: true,loader:true});
      this.getTutors()
    }else{
      this.setState({ isLoggedIn: false });

    }
  }
  toggleNavbar = () => {
    this.setState({ class: !this.state.class });
  };
  getTutors = async()=>{
    // this.setState({loader:true})
    console.log(this.state.page)
    let data = {
      page_limit:this.state.page_limit,
      page:this.state.page,
      role_types:'1,6',
      search_keyword:this.state.search_keyword

    }

    const headers = {
      Authorization: appToken(),
    };
   
      headers["Access-token"] = userData().access_token;
    await apiService.getMykademyTutors(headers,data)
    .then((response)=>{
      this.setState({loader:false})
      if(response.data.status === 200){
        this.setState({
          allTutors:this.state.allTutors.concat(response.data.data),
          total_turors:response.data.users_count,
          loadmore_loader:false
        })
        this.checkArrayEmpty(response.data.users_count)
      }else{
        this.checkArrayEmpty(0)

      }
     
    })
    .catch((err)=>{
      console.log(err)
    })

  }
  checkArrayEmpty = (val)=>{
    if(val === 0){
      this.setState({emptyData:true})
    }else{
      this.setState({emptyData:false})

    }
  }
  getCountryFlag = (countryname) => {
    console.log(country.data, "get");
    const flagurl = this.state.countriesFlagsData
      .filter((b) => {
        return b.name === countryname;
      })
      .map((b) => {
        return b.flag;
      });
    console.log(flagurl, "flagurl");
    return (
      <div class="info-wrap fs-12">
        <img
          class="flag-icon"
          src={flagurl[0] ?? "https://restcountries.eu/data/irn.svg"}
          alt="United Kingdom"
        />
        {countryname}
      </div>
    );
  };
  loadMore = ()=>{
    console.log('here')
    let page = this.state.page + 1
    this.setState({page:page,
      loadmore_loader:true

    },()=>this.getTutors())
  }
  
  searchTutor = debounce((text) => {
    
    let search = text.trim();
    this.setState({search_keyword:search})
    if (search === "") {
     
      this.setState({ allTutors: [], loader: true , page:1,page_limit:10 });
      this.getTutors();
    } else {
      this.setState({
        allTutors: [],
        loader: true,
        page:1,
        page_limit:10
      });
     
      
      this.getTutors();
    }
  }, 500);
  render() {
    if (!myportfolioCommonService.isAnyOneAdmin()) {
      MyportfolioAdminDropdownContents.two = "";
    }
		myportfolioCommonService.getRolls()
    return (
      <React.Fragment>
        <div class="filter-bar-block">
          <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
            <div class="bar1"></div>
            <div class="bar2"></div>
            <div class="bar3"></div>
          </a>
          <div class="header-btn-list">
            <NavLink to="/mykademy-admin/admin-tutor/create-tutor">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 14 14"
                width="11"
                height="11"
                style={{ enableBackground: "new 0 0 14 14" }}
                xmlSpace="preserve"
              >
                <path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
              </svg>
              Add Tutor
            </NavLink>
          </div>
          <div class="dropdown view-option common-option">
            <button
              class="btn dropdown-toggle"
              type="button"
              id="dropdownMenuButton1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              Add Option
            </button>
            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
           
              <NavLink className="dropdown-item"
                to="/mykademy-admin/admin-tutor/create-tutor">

                Add Learner
            </NavLink>

            </ul>
          </div>
          <div className="filter-bar-holder">

						<div className="input-group flex-nowrap filter-search">
							<input type="text" className="form-control" onChange={(e) => this.searchTutor(e.target.value)} placeholder="Find" aria-label="Find" aria-describedby="addon-wrapping" />
							<span className="input-group-text" id="addon-wrapping">
								<img src="/./assets/images/landing/search.svg" alt="search" />
							</span>
						</div>
						<a className="mobile-search-icon" >
							<img src="/./assets/images/landing/mobile-search.svg" alt="search" />
						</a>
					</div>

        </div>
        <div
          className={
            this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"
          }
        >
          <AdminNavbar
            class={this.state.class}
            data={subdropdownData}
            DropdownData={MyportfolioAdminDropdownContents}
          />
          <div class="container-fluid courses-block">
            {this.state.loader ? (
              <div className="step step-3 active">
                <div className="d-flex justify-content-center align-items-center spinner-content">
                  <div
                    style={{ marginTop: "25%" }}
                    className="spinner-border"
                    role="status"
                  >
                    <span className="visually-hidden">Loading...</span>
                  </div>
                </div>
              </div>
            ) : (
                <React.Fragment>
                  {this.state.emptyData ? (
                    <section className="m-t-20 results-section results--grid-null-item alert alert-danger">
                      <div>no Tutors found!</div>
                    </section>
                  ) : (
                      <React.Fragment>
                        <div class="course-list-block">
                          <h4>Tutors</h4>
                          <div class="table-responsive sessions-table-list apprentices-table-list" style={{ overflow: 'hidden', minHeight: '35vh' }}>
                            <table class="table">
                              <thead>
                                <tr>
                                  <th scope="col" width="200">
                                    <div class="thead-img-wrap">

                                      <img src={personimg} alt="person" />
                                Name
                              </div>
                                  </th>
                                  <th scope="col" width="150">
                                    <div class="thead-img-wrap">
                                      <img src={callImg} alt="Phone" />
                                Phone
                              </div>
                                  </th>
                                  <th scope="col" width="250">
                                    <div class="thead-img-wrap">
                                      <img src={mailImg} alt="Email" />
                                Email
                              </div>
                                  </th>
                                  <th width="200" scope="col">
                                    <div class="thead-img-wrap">
                                      <img src={locationImg} alt="Location" />
                                Location
                              </div>
                                  </th>

                                  <th width="40" scope="col"></th>
                                </tr>
                              </thead>
                              <tbody>
                              {this.state.allTutors.map((val, i) => {
                                return (
                                  <tr key={i}>
                                    <td scope="row">
                                      <div class="info-wrap">
                                     
                                        <img
                                          src={val?.image ?? defaultImg}
                                          alt="user"
                                        />
                                        {val?.name}
                                      </div>
                                    </td>
                                    <td>
                                      <div class="info-wrap fs-12">
                                        {val?.phone}
                                      </div>
                                    </td>
                                    <td>
                                      <div class="info-wrap fs-12">
                                        <a
                                          class="word-break"
                                          href={"mailto:" + val.email}
                                        >
                                          {val.email}
                                        </a>
                                      </div>
                                    </td>
                                    <td>
                                      {this.getCountryFlag(val?.country_name)}
                                
                                    </td>
                                   
                                    {/* <td>
                                      <div class="course-thumb-btn-wrap apprentices-table-dropdown">
                      
                                        <div class="dropdown tool-dropdown-wrap">
                                          <a
                                            class="dropdown-toggle three-dots-dropdown"
                                            href="#"
                                            role="button"
                                            id="dropdownMenuLink"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                          >
                                            <img src={threeDotsImg} alt="dot" />
                                          </a>

                                          <ul
                                            class="dropdown-menu"
                                            style={{marginTop:'0px'}}
                                            aria-labelledby="dropdownMenuLink"
                                          >
                                            <li>
                                              {this.conversion(val)}
                          
                                            </li>
                                            <li>
                                              {this.editMember(val.id)}
                            
											                      	</li>
                          
                          
                      
                          
                          
                      
                                          </ul>
                                        </div>
                                      </div>
                                    </td> */}
                                  </tr>
                                );
                              })}
                            </tbody>
                            </table>
                          </div>
                          {
                          this.state.total_turors < 9 ? (
                            <div></div>
                          ) : (
                            <div
                              className="program-listing__load-more"
                              style={{ marginTop: "0rem" }}
                            >
                              <button className="olive-flat-button olive-accent">
                                {this.state.loadmore_loader ? (
                               
                                  <div className="load-more-loader"></div>
                                ) : (
                                    <span
                                      className="olive-button-wrapper"
                                      onClick={()=>this.loadMore()}
                                    >
                                      Load more
                                    </span>
                                  )}
                              </button>
                            </div>
                          )}

                        </div>
                      </React.Fragment>
                    )
                  }
                </React.Fragment>
              )
            }
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default AdminTutor;

