import React, { Component } from 'react';
import { Notification } from "../../services/common.service";
import { NavLink } from 'react-router-dom';

const commonApiService = require('../../services/common_api.service')
const contrydata = require('../../services/countries_with_phone');

class TalkToSales extends Component {
    constructor(props) {
        super(props)
        this.state = {
            countries: contrydata.contries_with_phone_code,
            errors: {},
            fields: {},
            show_thanks: false

        }
    }

    componentDidMount() {
        console.log(this.state.countries)
    }
    validateFields = (showErrMessage) => {
        let fields = this.state.fields;
        let errors = {};
        let formIsValid = true;
        if (!fields["name"]) {
            formIsValid = false;
            errors["name"] = "Name cannot be empty";
        }
        if (!fields["email"]) {
            formIsValid = false;
            errors["email"] = "Email cannot be empty";
        }
        if (typeof fields["email"] !== "undefined") {
            let lastAtPos = fields["email"].lastIndexOf("@");
            let lastDotPos = fields["email"].lastIndexOf(".");
            if (
                !(
                    lastAtPos < lastDotPos &&
                    lastAtPos > 0 &&
                    fields["email"].indexOf("@@") === -1 &&
                    lastDotPos > 2 &&
                    fields["email"].length - lastDotPos > 2
                )
            ) {
                formIsValid = false;
                errors["email"] = "Enter a valid Email";
            }
        }
        if (!fields["phone_number"]) {
            formIsValid = false;
            errors["phone_number"] = "Phone Number cannot be empty";
        }
        if (isNaN(fields["phone_number"])) {
            formIsValid = false;
            errors["phone_number"] = "Enter a valid Phone Number";
        }
        // if (!fields["phone_number"].match(/^[0-9]{4,12}$/)) {
        //     formIsValid = false;
        //     errors["phone_number"] = "Enter a valid Phone Number"
        // }
        if (!fields["country_code"]) {
            formIsValid = false;
            errors["country_code"] = "Country cannot be empty";
        }

        if (!fields["company"]) {
            formIsValid = false;
            errors["company"] = "Company cannot be empty";
        }

        if (!fields["dial_code"]) {
            formIsValid = false;
            errors["dial_code"] = "code cannot be empty";
        }
        if (showErrMessage) {
            this.setState({ errors: errors });
        }
        return formIsValid;

    }
    handleChange(field, e) {
        console.log(field, e.target.value)
        let errors = this.state.errors
        errors[field] = ""
        let fields = this.state.fields;
        // if (field === 'country_code') {
        //     fields[field] = e.target.value.split('_')[0];
        //     fields['country'] = e.target.value.split('_')[1];
        // } else {
        fields[field] = e.target.value;
        // }
        this.setState({ fields, errors });
        console.log(fields)
    }
    submit = async (e) => {
        e.preventDefault()
        let isValid = this.validateFields(true)
        if (isValid) {
            this.setState({ show_thanks: true })
            await commonApiService.talkToSales(this.state.fields)
            console.log('hereeeeeee', this.state.fields)

        } else {
            Notification(
                "Please fill required fields !",
                "danger",
                "Fields Required"
            );
        }
    }

    render() {
        return (
            <React.Fragment>

                <div class="sales-banner">
                    <div class="container">
                        <div class="row">
                            <div class="col-xs-12 col-md-12 col-lg-6">
                                <div class="sales-info-holder">
                                    <h1>Let’s chat about a solution tailored to your company</h1>
                                    <p>Please complete the form and we’ll contact you to discuss the best solution.</p>
                                    <div class="img-wrap">
                                        <img src="assets/images/landing/laptop-01.png" alt="laptop" />
                                    </div>
                                </div>
                            </div>
                            {/* form start */}

                            <div class="col-xs-12 col-md-12 col-lg-6">
                                <form action="#" class="solution-form">
                                    <div class="row">
                                        <div class="col-sm-6 mb-3">
                                            <label for="name" class="form-label">Name</label>
                                            <input name="name" type="text" class="form-control" id="name" placeholder="Name"
                                                className={this.state.errors['name'] ? 'form-control input-error' : 'form-control'}
                                                onChange={this.handleChange.bind(this, "name")} />
                                            {this.state.errors['name'] &&
                                                <div style={{ color: '#ff3333' }}>{this.state.errors['name']}</div>
                                            }
                                        </div>
                                        <div class="col-sm-6 mb-3">
                                            <label for="workEmail" class="form-label">Work Email Address</label>
                                            <input name="email" type="email" class="form-control" id="workEmail" placeholder="name@email.com"
                                                className={this.state.errors['email'] ? 'form-control input-error' : 'form-control'}
                                                onChange={this.handleChange.bind(this, "email")} />
                                            {this.state.errors['email'] &&
                                                <div style={{ color: '#ff3333' }}>{this.state.errors['email']}</div>
                                            }
                                        </div>

                                        <div class="col-sm-6 mb-3">
                                            <label for="country" class="form-label">Country</label>
                                            <select name="country_code" class="form-select"
                                                onChange={this.handleChange.bind(this, "country_code")}
                                                className={this.state.errors['country_code'] ? 'form-control input-error' : 'form-control'} >

                                                <option value="">Choose country...</option>
                                                {this.state.countries.map((country, i) => (
                                                    <option key={i} value={country.code}>
                                                        {country.name}
                                                    </option>
                                                ))}
                                            </select>
                                            {/* <select class="form-select" aria-label="Default select example">
									<option selected>United Kingdom</option>
									<option value="1">Irland</option>
									<option value="2">London</option>
									<option value="3">Nepal</option>
								</select> */}
                                            {this.state.errors['country_code'] &&
                                                <div style={{ color: '#ff3333' }}>{this.state.errors['country_code']}</div>
                                            }
                                        </div>

                                        <div class="col-sm-6 mb-3">
                                            <label for="cName" class="form-label">Company</label>
                                            <input name="company" type="text" class="form-control" id="cName" placeholder="Company Name"
                                                className={this.state.errors['company'] ? 'form-control input-error' : 'form-control'}
                                                onChange={this.handleChange.bind(this, "company")} />
                                            {this.state.errors['company'] &&
                                                <div style={{ color: '#ff3333' }}>{this.state.errors['company']}</div>
                                            }
                                        </div>

                                        <div class="col-sm-6 mb-3">
                                            <label for="industry" class="form-label">Industry</label>
                                            <input name="industry" type="text" class="form-control" id="industry" placeholder="Industry"
                                                className={this.state.errors['industry'] ? 'form-control input-error' : 'form-control'}
                                                onChange={this.handleChange.bind(this, "industry")} />
                                            {this.state.errors['industry'] &&
                                                <div style={{ color: '#ff3333' }}>{this.state.errors['industry']}</div>
                                            }
                                        </div>

                                        <div class="col-sm-6 mb-3">
                                            <label for="pNumber" class="form-label">Phone Number</label>
                                            <div class="row">
                                                <div class="col-xs-6 col-sm-6 col-md-4 col-lg-6 col-xl-5  mb-3">
                                                    <select class="form-select" aria-label="Default select example"
                                                        onChange={this.handleChange.bind(this, "dial_code")}
                                                        name="dial_code"
                                                        className={this.state.errors['dial_code'] ? 'form-control input-error' : 'form-control'}>

                                                        <option value="">Choose code</option>
                                                        {this.state.countries.map((country, i) => (
                                                            <option key={i} value={country.dial_code}>
                                                                {country.dial_code}
                                                            </option>
                                                        ))}
                                                    </select>
                                                    {/* <select class="form-select" aria-label="Default select example">
                                                        <option selected>+44</option>
                                                        <option value="1">+376</option>
                                                        <option value="2">+237</option>
                                                        <option value="3">+1809</option>
                                                    </select> */}
                                                    {this.state.errors['dial_code'] &&
                                                        <div style={{ color: '#ff3333' }}>{this.state.errors['dial_code']}</div>
                                                    }
                                                </div>
                                                <div class="col-xs-6 col-sm-6 col-md-8 col-lg-6 col-xl-7">
                                                    <input name="phone_number" type="number" class="form-control" id="pNumber" placeholder="1234 567 890"
                                                        className={this.state.errors['phone_number'] ? 'form-control input-error' : 'form-control'}
                                                        onChange={this.handleChange.bind(this, "phone_number")} />
                                                    {this.state.errors['phone_number'] &&
                                                        <div style={{ color: '#ff3333' }}>{this.state.errors['phone_number']}</div>
                                                    }
                                                </div>
                                            </div>
                                        </div>

                                        <div class="col-sm-12">
                                            <label for="exampleFormControlTextarea1" class="form-label">Anything Else? (Optional)</label>
                                            <textarea name="text_area" class="form-control" id="exampleFormControlTextarea1" rows="6" placeholder="Please describe what you’re looking for with OLIVE VLE"
                                                onChange={this.handleChange.bind(this, "text_area")}></textarea>
                                        </div>

                                        <div class="talk-btn-wrap">
                                            <NavLink to="/tab" class="btn-rounded">
                                                Back
								            </NavLink>
                                            <button type="submit"
                                                onClick={this.submit}
                                                class="btn common-course-btn btn-vle-success" >Submit</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                            {/* form end */}
                        </div>
                    </div>
                </div>
                <div class="intro-footer">
                    <div class="container">
                        <p>&copy; 2021 Olive Group. All rights reserved.</p>
                        <a href="javascript:void(0);">Privacy Policy</a>
                    </div>
                </div>
                {this.state.show_thanks &&
                    <div style={{ display: 'block',background:"#f3f3f338" }} class="modal sales-submitted-modal" id="salesSubmittedModal" tabindex="-1" >
                        <div class="modal-dialog">
                            <div class="modal-content">
                                <div class="modal-body">
                                    <h4>Thanks for reaching out</h4>
                                    <p>We appreciate your time in telling us a little more about yourself. Someone from our sales team will be in touch shortly.</p>
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn common-course-btn btn-vle-success" onClick={() => this.setState({ show_thanks: false })} >Got it</button>
                                </div>
                            </div>
                        </div>
                    </div>
                }
            </React.Fragment>
        )
    }
}

export default TalkToSales;
