import { userData, getAuthtoken } from '../myportfolio/services/common.service';
import { Notification } from "../../services/common.service";
import AlertBox from "./modals/alert";

const contrydata = require('../../services/countries_with_phone');

const mykademyCommonService = require("../mykademy/services/common.service")
const myportfolioCommonService = require("../myportfolio/services/common.service");
const commonApiService = require('../../services/common_api.service')
