import React, { Component } from 'react'
import { isAdminMykademy, isAdminPortfolio } from "../../../../services/common.service";
import { userData } from '../../../mykademy/services/common.service';
import { userDataMykademy, userDataPortfolio,isOlicevle } from '../../../../services/common.service'
import { NavLink } from 'react-router-dom';

import MpViewProfile from '../../../myportfolio/profile/viewProfile';
import MpEditPersonalData from '../../../myportfolio/profile/editPersonalData';
import MpUpdatePassword from '../../../myportfolio/profile/updatePassword';

import MkViewProfile from '../../../mykademy/profile/viewProfile';
import MkEditPersonalData from '../../../mykademy/profile/editPersonalData';
import MkUpdatePassword from '../../../mykademy/profile/updatePassword';

import VleProfile from './vleProfile'
import EditVleProfile from './editVleProfile'
import UpdateVlepassword from './updateVlepassword'
export default class Profile extends Component {

	constructor(props) {
		super(props)
		this.state = {
			open_modal: false,
			show_profile: false,
			open_edit_personal_section: false,
			open_edit_security_section: false,
			isMykademyAdmin: isAdminMykademy(),
			isPortfolioAdmin: isAdminPortfolio(),
			mykademyUser: userDataMykademy(),
			// portfolioUser: userDataPortfolio(),
			image_url: '',
			fname: '',
			lname: '',
			email: '',
			activate_myportfolio: false,
			activate_mykademy: false,
			show_vle_profile:false,
			activate_vle:false,
			open_edit_vlepersonal_section: false,
			open_edit_vlesecurity_section: false,

		}
	}
	componentDidMount = () => {
		if(isOlicevle()){
			if(this.state.mykademyUser && userDataPortfolio()){
				this.setState({
						isOlivevle:true,
						isOliveportfolioUser:true,
						isOlivemykademyUser:true,
						image_url: userDataMykademy().user_data.us_image,
						fname: userDataPortfolio().data.member_details.first_name,
						lname: userDataPortfolio().data.member_details.last_name,
						email: userDataPortfolio().data.member_details.email
				})
			}else{
				if(userDataPortfolio()){
					this.setState({
						isOlivevle:true,
						isOliveportfolioUser:true,
						isOlivemykademyUser:false,
						image_url: userDataPortfolio().data.member_details.image_url,
						fname: userDataPortfolio().data.member_details.first_name,
						lname: userDataPortfolio().data.member_details.last_name,
						email: userDataPortfolio().data.member_details.email
					})	
				}
				else{

					this.setState({
						isOlivevle:true,
						isOliveportfolioUser:false,
						isOlivemykademyUser:true,
							image_url: userDataMykademy().user_data.us_image,
							fname: userDataPortfolio().data.member_details.first_name,
							lname: userDataPortfolio().data.member_details.last_name,
							email: userDataPortfolio().data.member_details.email
					})
				}

			}
			
		
			console.log('from olive vle')
		} else{

			if (userDataPortfolio()) {
				this.setState({
					isOlivevle:false,
					isOliveportfolioUser:false,
						isOlivemykademyUser:false,
					image_url: userDataPortfolio().data.member_details.image_url,
					fname: userDataPortfolio().data.member_details.first_name,
					lname: userDataPortfolio().data.member_details.last_name,
					email: userDataPortfolio().data.member_details.email
				})
			} else {
				this.setState({
					isOlivevle:false,
					isOliveportfolioUser:false,
						isOlivemykademyUser:false,
					image_url: userDataMykademy().user_data.us_image,
					fname: userDataMykademy().user_data.us_name,
					email: userDataMykademy().user_data.us_email
				})
			}
		}

	}
	logout = () => {
		localStorage.clear()
		window.location.reload();
	}
	updateStateFromChild = (val) => {
		this.setState(val)
		console.log(this.state)
	}
	profileClick = ()=>{
		console.log('clicked')
		this.setState({
			open_modal: true,
			show_vle_profile: true,
			activate_myportfolio: false,
			activate_mykademy: false,
			show_profile: false,
			activate_vle:true,
			image_url64:userDataPortfolio().data.member_details.image_url,
			image_url: userDataMykademy().user_data.us_image,
			fname: userDataPortfolio().data.member_details.first_name,
			lname: userDataPortfolio().data.member_details.last_name,
			email: userDataMykademy().user_data.us_email,
			country_code:userDataMykademy().user_data.us_country_code,
			country:userDataMykademy().user_data.us_country,
			phone:userDataMykademy().user_data.us_phone
		})
	}
	profileMykademyClick = () => {
		this.setState({
			activate_mykademy: true,
			activate_myportfolio: false,
			open_modal: true,
			show_profile: true,
			image_url: userDataMykademy().user_data.us_image,
			fname: userDataMykademy().user_data.us_name,
			email: userDataMykademy().user_data.us_email,
			country_code:userDataMykademy().user_data.us_country_code,
			country:userDataMykademy().user_data.us_country,

			phone:userDataMykademy().user_data.us_phone

		})

	}
	profilePortfolioClick = () => {
		this.setState({
			activate_myportfolio: true,
			activate_mykademy: false,
			open_modal: true,
			show_profile: true,
			image_url: userDataPortfolio().data.member_details.image_url,
			fname: userDataPortfolio().data.member_details.first_name,
			lname: userDataPortfolio().data.member_details.last_name,
			email: userDataPortfolio().data.member_details.email
		})

	}
	render() {
		const {open_edit_vlesecurity_section,open_edit_vlepersonal_section, show_vle_profile ,show_profile, open_modal, open_edit_personal_section, open_edit_security_section } = this.state;
		return (
			<div class="col-lg-6">
				<div class="common-shadow-box">
					<div class="welcome-img-wrap">
						<img src={this.state.image_url} alt="user" />
					</div>
					<div class="welcome-text-wrap">
						<h4>Welcome back, {this.state.fname}</h4>
						<p>It’s great to see you again!</p>
						<ul class="list-unstyled welcome-list">
							{
								this.state.isOlivevle ? (
									<React.Fragment>
										{
											this.state.isOliveportfolioUser && !this.state.isOlivemykademyUser ? (
												<li className="dashbordtag" onClick={this.profilePortfolioClick}>

												<a style={this.state.image_url === userDataPortfolio().data.member_details.image_url ? { fontWeight: '500' } : { fontWeight: 'normal' }}>
													<img src="assets/images/landing/profile.svg" alt="profile" />Profile
												</a>
											</li>	
											):null
										}{
											!this.state.isOliveportfolioUser && this.state.isOlivemykademyUser ?(
												<li className="dashbordtag" onClick={this.profileMykademyClick}>
												<a style={this.state.image_url === userDataMykademy().user_data.us_image ? { fontWeight: '500' } : { fontWeight: 'normal' }}>
													<img src="assets/images/landing/profile.svg" alt="profile" />Profile
												</a>
											</li>	
											):null
										}{
											this.state.isOliveportfolioUser && this.state.isOlivemykademyUser ?(

										<li className="dashbordtag" onClick={this.profileClick}>
									<a style={{ fontWeight: '500' }}>
										<img src="assets/images/landing/profile.svg" alt="profile" />Profile
									</a>
								</li>
											):null
										}
									</React.Fragment>

								):(
										<React.Fragment>
											{this.state.mykademyUser &&
								<li className="dashbordtag" onClick={this.profileMykademyClick}>
									<a style={this.state.image_url === userDataMykademy().user_data.us_image ? { fontWeight: '500' } : { fontWeight: 'normal' }}>
										<img src="assets/images/landing/profile.svg" alt="profile" />Mykademy Profile
									</a>
								</li>
							}
							{userDataPortfolio() &&
								<li className="dashbordtag" onClick={this.profilePortfolioClick}>
									<a style={this.state.image_url === userDataPortfolio().data.member_details.image_url ? { fontWeight: '500' } : { fontWeight: 'normal' }}>
										<img src="assets/images/landing/profile.svg" alt="profile" />Myportfolio Profile
									</a>
								</li>
							}
									</React.Fragment>
								)
							}
						
							<li className="dashbordtag">
								<a href="/calendar-tab">
									<img src="assets/images/landing/help.svg" alt="help" />Help
								</a>
							</li>
						</ul>
						<ul class="list-unstyled welcome-list">
							{this.state.isMykademyAdmin &&
								<li className="dashbordtag">
									<NavLink to={'/mykademy-admin'} title="Courses Management" >
										<img src="assets/images/landing/play-circle.svg" alt="Courses Management" /> Courses Management
                    				</NavLink>
								</li>
							}
							{this.state.isPortfolioAdmin &&
								<li className="dashbordtag">
									<NavLink to={'/myportfolio-admin'} title="Portfolio Management" >
										<img src="assets/images/landing/manage-portfolio.svg" alt="Portfolio Management" /> Portfolio Management
                    				</NavLink>
								</li>
							}
							<li className="dashbordtag"><a onClick={() => this.logout()} ><img src="assets/images/landing/sign-out.svg" alt="profile" />Sign Out</a></li>
						</ul>
					</div>
				</div>
				{/* profile pop-up */}
				{open_modal &&
					<div style={{ opacity: '1', display: 'block', overflowY: 'scroll' }} class="modal fade profile-modal" id="profileModal" aria-labelledby="exampleModalLabel" aria-hidden="true">
						<div class="modal-dialog modal-dialog-centered"	>
							<div class="modal-content">
								<div class="modal-body">
									{this.state.activate_mykademy &&
										<div class="personal-profile">
											{show_profile &&
												<MkViewProfile phone={this.state.phone} country={this.state.country} email={this.state.email} country_code={this.state.country_code}  image_url={this.state.image_url} name={this.state.fname} updateState={this.updateStateFromChild}></MkViewProfile>
											}
											{open_edit_personal_section &&
												<MkEditPersonalData phone={this.state.phone} country={this.state.country} email={this.state.email} country_code={this.state.country_code}  image_url={this.state.image_url} name={this.state.fname}  updateState={this.updateStateFromChild} ></MkEditPersonalData>
											}
											{open_edit_security_section &&
												<MkUpdatePassword image_url={this.state.image_url} updateState={this.updateStateFromChild}></MkUpdatePassword>
											}
										</div>
									}
									{this.state.activate_myportfolio &&
										<div class="personal-profile">
											{show_profile &&
												<MpViewProfile fname={this.state.fname} lname={this.state.lname} image_url={this.state.image_url} email={this.state.email} updateState={this.updateStateFromChild}></MpViewProfile>
											}
											{open_edit_personal_section &&
												<MpEditPersonalData fname={this.state.fname} lname={this.state.lname} image_url={this.state.image_url} email={this.state.email} updateState={this.updateStateFromChild}></MpEditPersonalData>
											}
											{open_edit_security_section &&
												<MpUpdatePassword name={this.state.user_name} image_url={this.state.image_url} updateState={this.updateStateFromChild}></MpUpdatePassword>
											}
										</div>
										// :
										// <div class="personal-profile">
										// 	{show_profile &&
										// 		<MpViewProfile name={this.state.user_name} image_url={this.state.image_url} email={this.state.email} updateState={this.updateStateFromChild}></MpViewProfile>
										// 	}
										// </div>
									}
									{this.state.activate_vle && 
										<div class="personal-profile">

										{show_vle_profile &&
											<VleProfile fname={this.state.fname} lname={this.state.lname} image_url={this.state.image_url} email={this.state.email} updateState={this.updateStateFromChild}></VleProfile>
										} 
										{open_edit_vlepersonal_section &&
										<EditVleProfile country_code={this.state.country_code} phone={this.state.phone} country={this.state.country} image_url64={this.state.image_url64} fname={this.state.fname} lname={this.state.lname} image_url={this.state.image_url} email={this.state.email} updateState={this.updateStateFromChild }></EditVleProfile>
										}
										{open_edit_vlesecurity_section &&
											<UpdateVlepassword  image_url={this.state.image_url} updateState={this.updateStateFromChild}></UpdateVlepassword>
										}
										</div>
									}
								</div>
							</div>
						</div>
					</div>
				}
				{/* profile pop-up */}
			</div>
		)
	}
}
