import React from "react";
import Calendar from "../shared_components/calendar";
import { NavLink } from 'react-router-dom';
import { config } from '../../config';
import { appToken } from "../mykademy/services/common.service"
const commnService_mkdmy = require("../mykademy/services/common.service");
const commnService_mpflio = require("../myportfolio/services/common.service");
const commonService = require('../../services/common.service');
const mykademyApiService = require('../mykademy/services/api.service')
const portfolioApiService = require('../myportfolio/services/api.service')

export default class Events extends React.Component {

  state = {
    events: [],
    accessToken: "",
    result_section: "results-section results--grid",
    open_calendar: false,
    openDropDownBtns: false,
    calendar_events: [],
    portfolio_auth_token: "",
    mykademy_events: [],
    myportfolio_events: [],
    initial_loader: true
  };

  componentDidMount() {
    this.loadEvents();
  }

  async loadEvents() {
    var all_mykademy_events = []
    var all_myportfolio_events = []
    if (commnService_mkdmy.userData() !== null) {
      let mk_events = await this.loadMykademyEvents()
      this.setState({ mykademy_events: mk_events, isMykademyLoggedin: true })
    }
    else {
      this.setState({ isMykademyLoggedin: false })
    }
    if (commnService_mpflio.userData() !== null) {
      let mp_events = await this.loadMyportfolioEvents()
      this.setState({ myportfolio_events: mp_events, isMyportfolioLoggedin: true })
    }
    else {
      this.setState({ isMyportfolioLoggedin: false })
    }
    if (this.state.mykademy_events.custom_events) {
      all_mykademy_events = [
        ...this.state.mykademy_events.assignments,
        ...this.state.mykademy_events.custom_events,
        ...this.state.mykademy_events.live_classes
      ]
    }
    if (this.state.myportfolio_events.live_classes) {
      all_myportfolio_events = this.state.myportfolio_events.live_classes
    }
    var arr = []
    if (all_mykademy_events.length !== 0) {
      let myKademyUpCommingEvents = commonService.getUpcomingEvents(all_mykademy_events);
      if (myKademyUpCommingEvents.length !== 0) {
        this.setState({ events: [...this.state.events, ...myKademyUpCommingEvents] })
      }
      arr = [...all_mykademy_events, ...arr]
    }
    if (all_myportfolio_events.length !== 0) {
      let myportifolioUpCommingEvents = commonService.getUpcomingEvents(all_myportfolio_events);
      if (myportifolioUpCommingEvents.length !== 0) {
        this.setState({ events: [...this.state.events, ...myportifolioUpCommingEvents] })
      }
      arr = [...all_myportfolio_events, ...arr]
    }
    this.setState({ calendar_events: arr })
    this.setState({ initial_loader: false })
  }

  async loadMykademyEvents() {
    const headers = {
      'Content-Type': 'application/json',
      'Authorization': appToken()
    }
    let user = commnService_mkdmy.userData()
    if (user !== null) {
      headers['Access-token'] = user.access_token
    }
    return await mykademyApiService.getAllEvents(headers)
      .then((response) => {
        if (response.data.length !== 0)
          return response.data.data
        else
          return []
      })
      .catch((error) => {
        return []
      })
  }

  async loadMyportfolioEvents() {
    var portfolio_access_token = commnService_mpflio.userData().data._token
    var portfolio_auth_token = commnService_mpflio.getAuthtoken()
    const headers = {}
    headers["Content-Type"] = "multipart/form-data";
    headers["Authorization"] = "Bearer " + portfolio_auth_token;
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(portfolio_access_token));
    return await portfolioApiService.getAllEvents(formData, headers)
      .then((response) => {
        console.log(response)
        if (response.data.length !== 0)
          return response.data.data
        else
          return []
      })
      .catch((error) => {
        return []
      })
  }

  getWeeksInMonth(a, b) {
    var c = [],
      d = new Date(b, a, 1),
      e = new Date(b, a + 1, 0),
      f = e.getDate();
    var g = 1;
    var h = 7 - d.getDay();
    while (g <= f) {
      c.push({
        start: g,
        end: h,
      });
      g = h + 1;
      h += 7;
      if (h > f) h = f;
    }
    return c;
  }

  gridListLayout(className) {
    if (className === " results--list") {
      this.setState({ open_calendar: false });
      document.getElementById("grid").classList.remove("selected");
      document.getElementById("event").classList.remove("selected");
      document.getElementById("list").classList.add("selected");
    } else if (className === " results--grid") {
      this.setState({ open_calendar: false });
      document.getElementById("grid").classList.add("selected");
      document.getElementById("event").classList.remove("selected");
      document.getElementById("list").classList.remove("selected");
    } else {
      this.setState({ open_calendar: true });
      document.getElementById("grid").classList.remove("selected");
      document.getElementById("event").classList.add("selected");
      document.getElementById("list").classList.remove("selected");
    }
    this.setState({ result_section: "results-section" + className, openDropDownBtns: false });
  }

  GetFormattedDate(date) {
    if (date !== undefined && date !== "") {
      var todayTime = new Date(date);
      var month = todayTime.getMonth() + 1;
      var day = todayTime.getDate();
      var year = todayTime.getFullYear();
      return ` ${month}/${day}/${year} `;
    }
    return "";
  }

  GetFormattedTime(date) {
    if (date !== undefined && date !== "") {
      var todayTime = new Date(date);
      var hours = todayTime.getHours();
      var minutes = todayTime.getMinutes();
      return ` ${hours}:${minutes}`;
    }
    return "";
  }

  openDropDown() {
    this.setState({ openDropDownBtns: !this.state.openDropDownBtns });
    document.getElementById("grid").classList.remove("selected");
    document.getElementById("event").classList.remove("selected");
    document.getElementById("list").classList.remove("selected");
  }

  logout = () => {
    localStorage.clear()
    window.location.reload();
  }
  switch(domain) {
    return (
      <NavLink to={`/switch/${domain}`} className="dropdown-item unlock-account" title="Sign in to another account">
        <img src="assets/images/lock.png" alt="lock" /> LMS Administration
      </NavLink>
    )
  }
  switch2(domain) {
    return (
      <NavLink to={`/switch/${domain}`} className="dropdown-item" title="Sign in to another account">
        <img src="assets/images/lock.png" alt="lock" />MyPortfolio Administration
      </NavLink>
    )
  }

  showAdminButtons() {
    return (
      <div className="dropdown admin-dropdown">
        <button onClick={() => this.openDropDown()} id="user_" className="dropdown-toggle show" type="button">
          <i className="fa fa-user-o" aria-hidden="true"></i>
        </button>
        {this.state.openDropDownBtns && (
          <ul className="dropdown-menu admin-dropdown-menu show" aria-labelledby="dropdownMenuButton1"
            style={{ margin: "0px", position: "absolute", inset: "0px auto auto 0px", transform: "translate(-175px, 36px)" }}
            data-popper-placement="bottom-end"
          >
            {commnService_mpflio.isAdmin() === true && commnService_mkdmy.isAdmin() === false && (
              <>
                <li>
                  <a href="admin-apprentices" className="dropdown-item">
                    <img src="assets/images/unlock.png" alt="unlock" />
                    MyPortfolio Administration
                  </a>

                </li>
                <li>
                  {this.switch('Mykademy')}
                </li>
              </>
            )}
            {commnService_mpflio.isAdmin() === true && commnService_mkdmy.isAdmin() === true && (
              <>
                <li>
                  <a href="admin-apprentices" className="dropdown-item">
                    <img src="assets/images/unlock.png" alt="unlock" />
                    MyPortfolio Administration
                  </a>

                </li>
                <li>
                  <a href="admin-leaner-list" className="dropdown-item unlock-account" >
                    <img src="assets/images/unlock.png" alt="unlock" />
                    LMS Administration
                  </a>
                </li>
              </>
            )}
            {commnService_mkdmy.isAdmin() === true && commnService_mpflio.isAdmin() === false && (
              <>
                <li>
                  {this.switch2('MyPortfolio')}
                </li>
                <li>
                  <a href="admin-leaner-list" className="dropdown-item unlock-account" >
                    <img src="assets/images/unlock.png" alt="unlock" />
                    LMS Administration
                  </a>
                </li>
              </>
            )}
            <li>
              <a onClick={() => this.logout()} className="dropdown-item" style={{ cursor: "pointer", paddingRight: '137px' }}>
                <img src={window.location.origin + "/assets/images/logout.svg"} alt="Logout" />
                  Logout
                </a>
            </li>
          </ul>
        )}
      </div>
    );
  }
  checkLiveClassExpired(val, i) {
    if (val.event_live_status === "1") {
      return (
        <div key={i} className="courses-list_grid h-100">
          <div className="courses-list_grid__image event">
            <div>
              <img src="assets/images/icon/video-marketing.svg" className="live red" alt="title" />
            </div>
          </div>
          <div className="courses-list_grid__info heading">
            <h3>
              <i className="fa fa-calendar-check-o" aria-hidden="true"></i>{" "}
              {this.GetFormattedDate(val.event_time)}
              <i className="fa fa-clock-o" aria-hidden="true"></i>
              {this.GetFormattedTime(val.event_time)}
            </h3>
            <h5>{val.event_name}</h5>
            <h5>
              <strong>Duration</strong>: {val.event_duration}
            </h5>
            <h5>
              <strong>Attendees</strong>: {val.event_total_attendies}
            </h5>
            <h5>
              <strong>Tutor</strong>: {val.event_tutor}
            </h5>
          </div>
          {
              val?.integration === 'microsoft_teams'|| val?.event_type === 'MSTeams'?

            <div class="courses-list_grid__cta center d-flex align-items-center">
              <a href={val?.event_link} target="_blank" class="btn btn-light btn-sm">Join</a>
            </div>:null
            }
        </div>
      );
    } else {
      return (
        <div key={i} className="courses-list_grid h-100">
          <div className="courses-list_grid__image event">
            <div>
              <img src="assets/images/icon/video-marketing.svg" alt="title" />
            </div>
          </div>
          <div className="courses-list_grid__info heading">
            <h3>
              <i className="fa fa-calendar-check-o" aria-hidden="true"></i>{" "}
              {this.GetFormattedDate(val.event_time)}{" "}
              <i className="fa fa-clock-o" aria-hidden="true"></i> 16:00
            </h3>
            <h5>{val.event_name}</h5>
            <h5>
              <strong>Duration</strong>: {val.event_duration}
            </h5>
            <h5>
              <strong>Attendees</strong>: {val.event_total_attendies}
            </h5>
            <h5>
              <strong>Tutor</strong>: {val.event_tutor}
            </h5>
           

          </div>
          {
              val?.integration === 'microsoft_teams'|| val?.event_type === 'MSTeams'?

            <div class="courses-list_grid__cta center d-flex align-items-center">
              <a href={val?.event_link} target="_blank" class="btn btn-light btn-sm">Join</a>
            </div>:null
            }
        </div>
      );
    }
  }
  render() {
    if (this.state.initial_loader) {
      return <div className="bg-main">
        <div style={{ textAlign: 'center' }}>
          <img alt="view more" style={{ width: '45px', height: '100vh' }} src="custom_assets/loader.svg" />
        </div>
      </div>
    }
    else
      return (
        <main className="olive-program program-listing courses-listing">
          <div className="program-listing__container">
            <div className="program-listing__content">
              <div className="program-listing__programs-wrapper">
                <div className="program-listing__filter-controls-wrapper d-flex justify-content-between">
                  <div className="program-listing__program-counter">
                    Results: {this.state.events.length} Upcoming Sessions
                </div>
                  <button id="list" onClick={(e) => this.gridListLayout(" results--list")} className="btn-switch--calendar_grid-list_view option--list">
                    <i className="fa fa-bars"></i>
                  </button>
                  <button
                    id="grid"
                    onClick={(e) => this.gridListLayout(" results--grid")}
                    className="btn-switch--calendar_grid-list_view option--grid selected"
                  >
                    <i className="fa fa-th-large"></i>
                  </button>
                  <button
                    id="event"
                    onClick={(e) => this.gridListLayout(" event--calendar")}
                    className="btn-switch--calendar_grid-list_view option--calendar"
                  >
                    <i className="fa fa-calendar"></i>
                  </button>
                  {this.showAdminButtons()}
                </div>
                {
                  this.state.open_calendar ?
                    <section className="results-section event--calendar">
                      <Calendar events={this.state.calendar_events} />
                    </section>
                    :
                    this.state.events.length === 0 ?
                      (
                        <section style={{ marginTop: '15%' }} className="results-section results--grid-null-item alert alert-danger">
                          <div>Sorry you do not have any active event!</div>
                        </section>
                      )
                      :
                      (
                        <section className={this.state.result_section}>
                          {
                            this.state.events.map((val, i) => {
                              if (val.event_type === "live_class") {
                                return this.checkLiveClassExpired(val, i);
                              }
                              else if (val.event_type === "assignment") {
                                return (
                                  <div key={i} className="courses-list_grid h-100">
                                    <div className="courses-list_grid__image event">
                                      <div>
                                        <img
                                          src="assets/images/icon/assginment.svg"
                                          alt="title"
                                        />
                                      </div>
                                    </div>
                                    <div className="courses-list_grid__info heading">
                                      <h3>
                                        <i className="fa fa-calendar-check-o" aria-hidden="true"></i>
                                        {this.GetFormattedDate(val.event_time)}
                                      </h3>
                                      <h5>{val.event_name}</h5>
                                      <h5>Attachment</h5>
                                    </div>
                                    <div className="courses-list_grid__cta center d-flex align-items-center">
                                      <a href={val.event_link} target="_self" className="btn btn-light btn-sm"> View  </a>
                                    </div>
                                  </div>
                                )
                              } else if (val.event_type === "custom_event") {
                                return (
                                  <div key={i} className="courses-list_grid h-100">
                                    <div className="courses-list_grid__image event">
                                      <div href="#">
                                        <img src="assets/images/icon/custom-event.svg" alt="title" />
                                      </div>
                                    </div>
                                    <div className="courses-list_grid__info heading">
                                      <h3>
                                        <i className="fa fa-calendar-check-o" aria-hidden="true"  ></i>
                                        {this.GetFormattedDate(val.event_time)}
                                      </h3>
                                      <h5>{val.event_name}</h5>
                                    </div>
                                    <div className="courses-list_grid__cta center d-flex align-items-center">
                                      <a href={val.event_link} target="_self" className="btn btn-light btn-sm" >  Join  </a>
                                    </div>
                                  </div>
                                )
                              } else {
                                return <div key={i} className="courses-list_grid h-100">
                                  <div className="courses-list_grid__image event">
                                    <div href="#">
                                      <img src="assets/images/icon/custom-event.svg" alt="title" />
                                    </div>
                                  </div>
                                  <div className="courses-list_grid__info heading">
                                    <h3>
                                      <i className="fa fa-calendar-check-o" aria-hidden="true"  ></i>
                                      {this.GetFormattedDate(val.event_time)}
                                    </h3>
                                    <h5>{val.event_name}</h5>
                                  </div>
                                  <div className="courses-list_grid__cta center d-flex align-items-center">
                                    <a href={val.event_link} target="_self" className="btn btn-light btn-sm">
                                      Join
                                </a>
                                  </div>
                                </div>
                              }
                            })
                          }
                        </section>
                      )
                }
              </div >
            </div >
          </div >
        </main >
      );
  }
}
