import React, { Component } from 'react'
import { config } from "../../../config";
import * as microsoftTeams from "@microsoft/teams-js";
import { userData, isAdmin } from "../../myportfolio/services/common.service";
import "../../myportfolio/portfolio.css";

const myportfolioApis = require("../../myportfolio/services/api.service");

const portfolio_entityId = "myprograms";
const portfolio_entityLabel = "myprograms";
var getConfig = () => {
    let isLegacy = localStorage.getItem('legacy')
    var conf = config
    if (isLegacy !== null) {
        conf.myportfolio.TENANTID = process.env.REACT_APP_TENANTID_LEGACY
    } else {
        conf.myportfolio.TENANTID = process.env.REACT_APP_TENANTID
    }
    return conf
}
const portfolio_app_id = config.index.APP_ID;
const portfolio_baseUrl = config.index.APP_BASEURL;
const TENANTID = getConfig().myportfolio.TENANTID
export default class Newsfeed extends Component {
    state = {
        memberdetails: {},
        isLoggedIn: false,
        token: "",
        newsFeed: "",
        initial_loader: true,
        role: "",
    };
    componentDidMount() {
        if (userData() !== null) {
            this.setState({ org_name: `Latest updates from ${userData().data.member_details.organisation_details.name}` })
            this.setState({ isLoggedIn: true });
            setTimeout(() => {
                this.getNewsfeed();
                console.log("nd");
            }, 2000);
        } else {
            this.setState({ org_name: `No news to show` })

            this.setState({ isLoggedIn: false });
        }
    }

    async getNewsfeed() {
        let user_data = userData();
        this.setState({
            token: localStorage.getItem("portfolio_authtoken"),
        });
        const headers = {
            "access-token": decodeURIComponent(this.state.token),
            clientid: config.myportfolio.NEWSFEED_CLIENT_ID,
            clientsecret: config.myportfolio.NEWSFEED_CLIENT_SECRET,
            "Content-Type": "application/x-www-form-urlencoded",
        };
        if (user_data.data.loginType === "admin") {
            this.setState({ role: "admin" });
        } else if (user_data.data.loginType === "employer") {
            this.setState({ role: "admin" });
        } else {
            this.setState({ role: "learner" });
        }

        let urlData = new URLSearchParams();
        urlData.append("userId", user_data.data.member_details.id);
        urlData.append("role", this.state.role);
        urlData.append("tenantId", TENANTID);
        urlData.append("organisationId", user_data.data.member_details.organisation_details.id);

        await myportfolioApis
            .getNewsfeed(headers, urlData)
            .then((response) => {
                this.setState({ initial_loader: "" });
                console.log(response.data.data);
                this.setState({ newsFeed: response.data.data });
            })
            .catch((error) => {
                this.setState({ initial_loader: "" });
                console.log(error);
            });
    }

    logOut() {
        localStorage.removeItem("portfolio_user");
        microsoftTeams.executeDeepLink(
            `https://teams.microsoft.com/l/entity/${portfolio_app_id}/${portfolio_entityId}?webUrl=${portfolio_baseUrl}/programe-list&label=${portfolio_entityLabel}`
        );
        this.setState({ isLoggedIn: false });
    }
    gotResponse(val) {
        if (val === 'success')
            this.componentDidMount();
    }
    render() {
        return (
            <div class="col-lg-6">
                <div class="common-shadow-box">
                    {/* <a href="#" class="three-dots-dropdown">
                        <img src="assets/images/landing/three-dots.svg" alt="dot" />
                    </a> */}

                    <div class="welcome-text-wrap">

                        <h4>News</h4>
                        <p>{this.state.org_name} </p>


                    </div>
                    {
                        this.state.isLoggedIn ?
                            <div class="common-media common-media-box">
                                <iframe
                                    src={this.state.newsFeed}
                                    title="newsfeed iframe"
                                    style={{ width: '100%', marginLeft: "0%", height: '74vh' }}
                                ></iframe>
                            </div> : null
                    }

                </div>
            </div>

        )
    }
}
