import React, { PureComponent } from 'react';
import { userData, is_super_org_admin_prgms } from "../../services/common.service";
import { config, subdropdownData, MyportfolioAdminDropdownContents, MykademyAdminDropdownContents } from "../../../../config";
import AdminNavbar from "../../../shared_components/adminNavbar";
import { NavLink } from "react-router-dom";
var getConfig = () => {
    let isLegacy = localStorage.getItem('legacy')
    var conf = config
    console.log(conf, 'isLegacy1')
    if (isLegacy !== null) {
      conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO_LEGACY
    } else {
      conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO
    }
    console.log(conf, 'isLegacy2')
    return conf
  }
const APP_BASEURL_IFRAME = getConfig().myportfolio.APP_BASEURL_IFRAME
const mykademyCommonService = require("../../../mykademy/services/common.service");
const myportfolioCommonService = require("../../services/common.service")

export default class AddProgramm extends PureComponent {
    state = {
        subdomain: localStorage.getItem('subdomain'),
        user_token: '',
        registerUrl: '',
        is_superAdmin: is_super_org_admin_prgms(),
        class: false,
    };
    componentDidMount() {
        this.setState({ user_token: userData().data._token },
            () => this.superAdminprgmlist()
        )


    }
    superAdminprgmlist = () => {
        var url = "";
        if (this.state.user_token !== undefined) {
            url = `https://${this.state.subdomain}${APP_BASEURL_IFRAME}/admin/programs/add?mst-token=` + decodeURIComponent(this.state.user_token)

        }
        this.setState({
            prgmUrl: url,
        })

    }
    toggleNavbar = () => {
        this.setState({ class: !this.state.class })
    }
    render() {
        if (!mykademyCommonService.isAdmin()) {
            MykademyAdminDropdownContents.one = "";
        }
        myportfolioCommonService.getRolls()
        return (
            <>
                <div class="filter-bar-block">
                    <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
                        <div class="bar1"></div>
                        <div class="bar2"></div>
                        <div class="bar3"></div>
                    </a>
                    {this.state.is_superAdmin &&
                        <div class="header-btn-list">
                            <NavLink to="/myportfolio-admin/super-admin-program-list/add-programme">
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 14 14"
                                    width="11"
                                    height="11"
                                    style={{ enableBackground: "new 0 0 14 14" }}
                                    xmlSpace="preserve"
                                >
                                    <path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
                                </svg>
                            Add Programme {" "}
                            </NavLink>
                            <NavLink to="/myportfolio-admin/super-admin-program-list/edit-enrolment-data">
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 14 14"
                                    width="11"
                                    height="11"
                                    style={{ enableBackground: "new 0 0 14 14" }}
                                    xmlSpace="preserve"
                                >
                                    <path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
                                </svg>
                                Edit Enrolment Data
                    </NavLink>


                        </div>
                    }
                </div>
                <div className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>
                    <AdminNavbar
                        class={this.state.class}
                        data={subdropdownData}
                        DropdownData={MykademyAdminDropdownContents}
                    />
                    <div class="container-fluid courses-block" style={{ padding: '60px 0px 0px' }}>
                        <div>
                            {
                                this.state.loader ?
                                    <div className="row">
                                        <div style={{ textAlign: "center" }}>
                                            <img
                                                style={{ width: "45px", height: "100vh" }}
                                                src="custom_assets/loader.svg"
                                                alt="loader"
                                            />
                                        </div>
                                    </div> :
                                    <iframe src={this.state.prgmUrl} title="super org admin programmes listing"
                                        style={{ width: '100%', height: '87vh', border: '0px' }}>
                                    </iframe>
                            }
                        </div>
                    </div>
                </div>
            </>
        )
    }
}
