export const userData = () => {
    let user = localStorage.getItem('mykademy_user')
    if (user !== null) {
        return JSON.parse(user)
    } else {
        return null
    }
}

export const getMykademySubdomain = () => {
    let domain = localStorage.getItem('mykademy_subdomain')
    if (domain !== null) {
        return domain
    } else {
        return null
    }
}

export const isAdmin = () => {
    let users = localStorage.getItem('mykademy_user')
    if (users !== null) {
        let parsed = JSON.parse(users)
        if (parsed.user_data.us_role_id === "1" || "6") {
            return true;
        } else {
            return false;
        }
    } else {
        return false
    }
}
export const getAdminData = () => {
    let users = localStorage.getItem('mykademy_user')
    if (users !== null) {
        let parsed = JSON.parse(users)
        if (parsed.user_data.us_role_id === "1") {
            return {
                id: parsed.user_data.id,
                name: parsed.user_data.us_email,
                email: parsed.user_data.us_email
            };
        } else {
            return null;
        }
    } else {
        return null
    }
}
export const appToken = () => {
    let app_token = localStorage.getItem('mykademy_app_token')
    if (app_token !== null) {
        return app_token
    } else {
        return null
    }
}
export const isTutor = () => {
    let users = localStorage.getItem('mykademy_user')
    if (users !== null) {
        let parsed = JSON.parse(users)
        if (parsed.user_data.us_role_id === "6") {
          return true
        } else {
            return false;
        }
    } else {
        return false
    }
}

