import React from "react";
import * as microsoftTeams from "@microsoft/teams-js";
import { getVcToken } from "../../../services/common.service";
import AdminNavbar from "../../shared_components/adminNavbar";
import { Notification } from "../../../services/common.service";
import AdminCreateVideoClassroom from "./Vc/createVideoClassRoom";
import {
	MykademyAdminDropdownContents,
	MyportfolioAdminDropdownContents,
	subdropdownData,
} from "../../../config";
import LiveEventSwiper from "./Vc/liveEventSwiper";
import * as moment from "moment";
import VcDataRangeCalendar from "./Vc/VcDateRangeCalendar";
import VcChart from "./Vc/VcChart";
import VcLineChart from "./Vc/VcLineChart";
import Adminvideoclassroom_sidebar from "./Adminvideocalssroom_sidebar";
import sidebarContext from "./sidebarContext";

import img_msteam_logo from "../../../custom_assets/images/mteams-logo.svg.svg";
import img_view_more_loader from "../../../custom_assets/loader.svg";
import Calendar from "./Vc/calendar";
import AlertBox from "../../shared_components/modals/alert";

const SiderbarProvider = sidebarContext.Provider;
const apiService = require("../services/api.service");
const mykademyCommonService = require("../../mykademy/services/common.service");
const myportfolioCommonService = require("../services/common.service");
const commonApiService = require('../../../services/common_api.service')

export default class adminVideoclassNameRoom extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			vcForm: false,
			class: false,
			vc_token_isvalid: false,
			videoClassroomLoader: true,
			liveClassesForSwiper: [],
			liveClassesAll: [],
			liveClasses: [],
			activeClass_add: false,
			sidebarData: {},
			selected_event: {},

		};

	}


	toggleNavbar = () => {
		this.setState({ class: !this.state.class });
	};

	async componentDidMount() {

console.log('11111')


		if (getVcToken()) {
console.log('222222222')

			let ttl = new Date(getVcToken().ttl);
			let end = ttl.setMinutes(ttl.getMinutes() + 50);
			let currentTime = new Date();
			if (new Date(currentTime) < new Date(end)) {
				this.setState({ vc_token_isvalid: true });
			}
		}
		if (myportfolioCommonService.userData() !== null) {
console.log('3333333333')

			console.log(myportfolioCommonService.userData());
			// this.setState({ user_token: myportfolioCommonService.userData().data._token });
			this.getLiveClasses(myportfolioCommonService.userData().data._token);
		}
	}
	async getLiveClasses(accesstoken) {
console.log('444444444')

		let formData = new FormData();
		formData.append("_token", decodeURIComponent(accesstoken));
		formData.append("current_page", 1);
		formData.append("per_page", 100);
		formData.append("type", "MSTeams");
		let fromDate = moment(new Date()).format("YYYY-MM-DD");
		formData.append("fromdate", fromDate);
		// let todate = moment(fromDate, "YYYY-MM-DD").add(7, "days");
		// formData.append("todate", moment(todate).format("YYYY-MM-DD"));
		// formData.append('occuopation_id', programId)
		// formData.append('organisation_id', myportfolioCommonService.getAdminOrganizationId())
		// if (search !== "") {
		//     formData.append('search', search)
		// }
		const headers = {
			"Content-Type": "multipart/form-data",
		};
		let portfolio_authtoken = myportfolioCommonService.getAuthtoken();
		if (portfolio_authtoken !== null) {
			headers["Authorization"] = "Bearer " + portfolio_authtoken;
		}
		await apiService.getLiveClasses(headers, formData).then((response) => {
			console.log(response);
			if (response.status === 200) {
				let calendarData = response.data.data.live_classes.map((event) => {
					event['title'] = event.event_name
					event['start'] = moment.utc(event.event_time).local().format();
					event['textColor'] = '#46465a'
					event['event_id'] = event.id
					event['classNames'] = ['fc-common', 'fc-blue']
					return event
				})
				this.setState({
					liveClasses: calendarData,
					liveClassesAll: response.data.data.live_classes,
					// liveClassesForSwiper: calendarData,
					videoClassroomLoader: false,
					// liveClassesForSwiper: response.data.data.live_classes.sort(function (
					// 	a,
					// 	b
					// ) {
					// 	var dateA = new Date(a.event_time);
					// 	var dateB = new Date(b.event_time);
					// 	return dateA - dateB;
					// }),
				});
			}
		});
	}
	openLogin() {
		microsoftTeams.authentication.authenticate({
			url: process.env.REACT_APP_TEAMS_AUTH_URL,
			width: 500,
			height: 504,
			successCallback: async (result) => {
				if (result.access_token) {
					Notification("Authentication Successfull", "success", "Success");
					let ttl = new Date();
					let vc_token_data = {
						token: result.access_token,
						ttl: ttl,
					};
					localStorage.setItem("vc_auth", JSON.stringify(vc_token_data));
					this.setState({ vc_token_isvalid: true });
				} else {
					Notification("Authentication Failed!.", "danger", "Warning");
				}
			},
			failureCallback: (reason) => {
				Notification("Authentication Failed!.", "danger", "Warning");
			},
		});
	}

	closeForm(e) {
		console.log('8888888888',e)
		if (e.status === 'edit') {
			Notification("Meeting updated successfully", "success", "Success");
			this.setState({ vcForm: false });
			this.componentDidMount();
		} else if (e.status) {
			Notification("Meeting created successfully", "success", "Success");
			this.setState({ vcForm: false });
			this.componentDidMount();
		} else {
			this.setState({ vcForm: false });
		}
	}

	openVCForm() {
		this.setState({ vcForm: !this.state.vcForm });
	}
	dateRangeCallback = (e) => {
		this.setState({ videoClassroomLoader: true });
		setTimeout(() => {
			this.setState({ videoClassroomLoader: false });
		}, 500);
		let g = this.state.liveClassesAll.filter((val) => {
			let rangeEnd = e[1].setHours(23, 59);
			if (
				e[0] <= new Date(val.event_time) &&
				rangeEnd >= new Date(val.event_time)
			) {
				return true;
			} else {
				return false;
			}
		});
		this.setState({ liveClassesForSwiper: g });
	};
	gg = () => {
		if (getVcToken()) {
			let ttl = new Date(getVcToken().ttl)
			let end = ttl.setMinutes(ttl.getMinutes() + 50)
			let currentTime = new Date()
			if (new Date(currentTime) < new Date(end)) {
				this.setState({ vcForm: true })
			} else {
				this.openLogin()
			}
		} else {
			this.openLogin()
		}
	}
	authorizeMsTeams() {
		if (getVcToken()) {
			let ttl = new Date(getVcToken().ttl)
			let end = ttl.setMinutes(ttl.getMinutes() + 50)
			let currentTime = new Date()
			if (new Date(currentTime) < new Date(end)) {
				return true
			} else
				return false
		} else {
			return false
		}
	}
	deleteEvent = async (event_id, title) => {
		// console.log(event_id)
		if (this.authorizeMsTeams()) {
			this.setState({
				show_delete_popup: true,
				selected_event: { id: event_id, title: title, message: <span>Are you sure you want to delete  <b className="capitalize"> {title} </b>  ?</span> },
			})
		} else {
			this.openLogin()
		}
	}
	modalCallback = (status) => {
		this.setState({ show_delete_popup: false, activeClass_add: !status })
		if (status) {
			this.deleteTeamsEvent()

		}
	}
	deleteTeamsEvent = async () => {
		let event = this.state.selected_event
		this.setState({ apiLoader: true })
		console.log(this.state.selected_event)
		let access_token = getVcToken().token
		const headers = {
			"Authorization": 'Bearer ' + access_token,
			"Content-Type": 'application/json'
		};
		await commonApiService.getTeamsEventById(event.id, 'myportfolio')
			.then(async (response) => {
				let teams_event_id = response.data.data.teams_event_id
				await apiService
					.deleteMsTeamsMeeting(headers, teams_event_id)
					.then((response) => {
						console.log('response', response)
						// this.props.callbackFn({ status: 'edit', response: response })
						this.deleteMyportfolioLiveClass(event.id)
					})
					.catch((err) => {
						console.log('err', err)
						this.setState({ apiLoader: false })
					})
			})
			.catch((err) => {
				console.log(err)
			})
	}
	deleteMyportfolioLiveClass = async (event_id) => {
		let formData = new FormData();
		let accesstoken = myportfolioCommonService.userData().data._token
		formData.append("_token", decodeURIComponent(accesstoken));
		formData.append("eventId", event_id);
		// let todate = moment(fromDate, "YYYY-MM-DD").add(7, "days");
		// formData.append("todate", moment(todate).format("YYYY-MM-DD"));
		// formData.append('occuopation_id', programId)
		// formData.append('organisation_id', myportfolioCommonService.getAdminOrganizationId())
		// if (search !== "") {
		//     formData.append('search', search)
		// }
		const headers = {
			"Content-Type": "multipart/form-data",
		};
		let portfolio_authtoken = myportfolioCommonService.getAuthtoken();
		if (portfolio_authtoken !== null) {
			headers["Authorization"] = "Bearer " + portfolio_authtoken;
		}
		await apiService.deleteMyportfolioLiveClass(formData, headers)
			.then((response) => {
				console.log(response);
				if (response.status === 200) {
					let filtered = this.state.liveClasses.filter((item) => item.id !== this.state.selected_event.id)
					this.setState({ liveClasses: filtered })
					this.setState({ apiLoader: false })
				}
			})
			.catch((err) => {
				this.setState({ apiLoader: false })
				console.log(err)
				// this.props.callbackFn({ status: false, response: err })
			})
	}
	render() {
		if (!mykademyCommonService.isAdmin()) {
			MykademyAdminDropdownContents.one = "";
		}
		myportfolioCommonService.getRolls()

		return this.state.vcForm ? (
			<AdminCreateVideoClassroom
				// learners={this.state.learners}
				callbackFn={(e) => this.closeForm(e)}
			/>
		) : (
				<SiderbarProvider
					value={{
						sidebarState: this.state.activeClass_add,
						sidebarToggle: (data) =>
							this.setState({
								activeClass_add: true,
								sidebarData: data,
							}),
						ToggleBack: () =>
							this.setState({
								activeClass_add: !this.state.activeClass_add,
							}),
					}}
				>
					<div class="filter-bar-block">
						{this.state.show_delete_popup &&
							<AlertBox
								btn1="Cancel"
								btn2="Delete"
								message={this.state.selected_event.message}
								type="alert"
								callback={this.modalCallback}>
							</AlertBox>
						}
						{this.state.apiLoader &&
							<div class="loading">foregroung loader&#8230;</div>
						}
						<a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
							<div class="bar1"></div>
							<div class="bar2"></div>
							<div class="bar3"></div>
						</a>
						<div class="header-btn-list">
							{!this.state.vc_token_isvalid ?
								<a onClick={() => this.openLogin()} style={{ cursor: 'pointer', float: 'right' }}>
									<img className="ms-logo" src={img_msteam_logo} alt="logo" />
									Login to create video Classroom</a>
								:
								<a style={{ cursor: 'pointer', float: 'right' }} onClick={() => this.openVCForm()}>
									<img className="ms-logo" src={img_msteam_logo} alt="logo" />
									Create Video Classroom</a>
							}
						</div>
						<div className="dropdown view-option common-option">
							<button className="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
								Add Option
                        </button>
							<ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
								<li>
									{!this.state.vc_token_isvalid ?
										<a className="dropdown-item" onClick={() => this.openLogin()} style={{ cursor: 'pointer', float: 'right' }}>
											Login to create video Classroom</a>
										:
										<a className="dropdown-item" style={{ cursor: 'pointer', float: 'right' }} onClick={() => this.openVCForm()}>
											Create Video Classroom</a>
									}


								</li>
							</ul>
						</div>
					</div>
					<div
						className={
							this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"
						}
					>
						<AdminNavbar
							class={this.state.class}
							data={subdropdownData}
							DropdownData={MykademyAdminDropdownContents}
						/>
						<div className="container-fluid courses-block">
							<div id="video-classroom" >
								<div className="video-classroom-block">
									<div className="row">
										<div className="col-xs-12 col-sm-12 col-md-7 col-lg-8">
											<h4 className="events-title">Upcoming Video Classrooms</h4>
											{this.state.liveClasses.length !== 0 &&

												<Calendar key={17777546} liveClasses={this.state.liveClasses} dateRangeCallback={this.dateRangeCallback}></Calendar>
											}
											{this.state.liveClasses.length === 0 &&

												<Calendar key={14343434} liveClasses={this.state.liveClasses} dateRangeCallback={this.dateRangeCallback}></Calendar>
											}
										</div>
										<div className="col-xs-12 col-sm-12 col-md-5 col-lg-4">
											<h4 className="events-title">Statistics</h4>
											<div className="course-thumb-list height-0 mb-20">
												<VcLineChart liveClasses={this.state.liveClasses} />
											</div>
											<div className="course-thumb-list height-0 mb-20">
												<VcChart liveClasses={this.state.liveClasses} />
											</div>
										</div>
									</div>
								</div>
								<Adminvideoclassroom_sidebar deleteEvent={this.deleteEvent} vc_sidebar_callback={this.gg} {...this.state.sidebarData} />
							</div>
						</div>
					</div>
				</SiderbarProvider>
			);
	}
}
