    checkIsEditVC() {
        let event_id = localStorage.getItem('mp_event_edit')

        if (event_id !== null) {
            // event_id = JSON.parse(event_id)
            // this.populateEventToFields(event)
            this.setState({ isEditVc: true })
            if (myportfolioCommonService.userData() !== null) {
                this.setState({ user_token: myportfolioCommonService.userData().data._token }, async () => {
                    // await this.getProgramelist();
                    // await this.getStudentList("", null);
                    await this.getEventById(event_id, myportfolioCommonService.userData().data._token)
                });
            }
        } else {
            if (myportfolioCommonService.userData() !== null) {
                this.setState({ user_token: myportfolioCommonService.userData().data._token }, async () => {
                    // await this.getProgramelist();
                    // await this.getStudentList("", null);
                    await this.getApprentices();
                });
            }

            this.getOutlookCalendarDatas()
        }
    }
    getEventById = async (event_id, user_token) => {
        this.setState({ apiLoader: true });
        localStorage.removeItem('mk_event_edit')
        console.log(event_id)
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(user_token))
        formData.append('event_id', event_id)
        const headers = {}
        let portfolio_authtoken = myportfolioCommonService.getAuthtoken()
        if (portfolio_authtoken !== null) {
            headers['Authorization'] = 'Bearer ' + portfolio_authtoken
        }
        await myportfolioApiService.getEventById(headers, formData)
            .then((response) => {
                console.log(response)
                if (response.status === 200) {
                    this.setState({ event: response.data.data })
                    this.populateEventToFields(response.data.data)
                }

            }).catch((err) => {
                console.log(err)
            })
    }
    add_minutes = (dt, minutes) => {
        return new Date(dt.getTime() + minutes * 60000);
    }
    async populateEventToFields(event) {
        if (myportfolioCommonService.userData() !== null) {
            this.setState({ user_token: myportfolioCommonService.userData().data._token }, async () => {
                // await this.getProgramelist();
                await this.getStudentList("", null);
            });
        }
        console.log(event)
        let values = this.state.values
        values['title'] = event.live_classes.event_name
        let x = moment.utc(event.live_classes.event_time).local()
        values['start_localDateTime'] = moment(x).format('YYYY-MM-DD HH:mm')
        values['start'] = new Date(moment(x).format('YYYY-MM-DD HH:mm')).toISOString()
        values['end'] = this.add_minutes(new Date(), 60).toISOString()
        // let y = moment.utc(event.data.event_time).local()
        values['end_localDateTime'] = moment(this.add_minutes(new Date(), 60)).format('YYYY-MM-DD HH:mm')
        console.log(values)
        let attendees = event.event_attendees.map((item) => {
            let obj = {}
            obj['name'] = item.name;
            obj['email'] = item.email;
            obj['id'] = item.id;
            return obj
        })
        var attendee = []
        var attendee_ids = []
        attendees.map((item, i) => {
            let j = {
                "emailAddress": {
                    "address": item.email,
                    "name": item.name
                },
                "type": "required"
            }
            attendee.push(j)
            let adminuser = myportfolioCommonService.getAdminData()

            if (item.email !== adminuser.email) {
                attendee_ids.push({ id: +(item.id), role: 'user' })
            } else {
                console.log(item)
            }
        })
        values['attendees'] = attendee;
        values['attendees_ids'] = attendee_ids;
        if (event.live_classes.program_id !== 0) {
            this.setState({ selected_program: event.live_classes.program_id })
            this.getProgramelist()
            this.populateProgram(event.live_classes.program_id)
        }
        if (event.live_classes.occupation_id !== 0) {
            this.setState({ selected_phase: event.live_classes.occupation_id })
            this.getPhases(event.live_classes.program_id)
            this.populateProgram(event.live_classes.occupation_id)
        }
        this.setState({ values, selectedValues: attendees })
        localStorage.removeItem('mp_event_edit')

    }
