    async getStudentList(search, programId) {
        this.setState({ apiLoader: true });
        this.setState({ usersearching: true })
        // await this.getStudentList("", null);
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(this.state.user_token))
        formData.append('current_page', 1)
        formData.append('per_page', 50)
        if (programId !== null) {
            formData.append('occupation_id', programId)
        }
        formData.append('organisation_id', myportfolioCommonService.getAdminOrganizationId())
        if (search !== "") {
            formData.append('search', search)
        }
        const headers = {
            'Content-Type': 'multipart/form-data',
        }
        let portfolio_authtoken = myportfolioCommonService.getAuthtoken()
        if (portfolio_authtoken !== null) {
            headers['Authorization'] = 'Bearer ' + portfolio_authtoken
        }
        await myportfolioApiService.getapprenticeStudents(headers, formData)
            .then((response) => {
                this.setState({ usersearching: false })
                this.setState({ apiLoader: false });
                let x = JSON.parse(response.data.members_details[0].organisation_details)
                if (response.data.total_rows !== "0") {
                    let res = response.data.members_details.map((val) => {
                        return {
                            name: val.first_name + ' ' + val.last_name,
                            email: val.email,
                            id: val.id
                        }
                    })
                    this.setState({
                        objectArray: res
                    })
                } else {
                    this.setState({ apprenticeList: [] })
                }
            }).catch((err) => {
                this.setState({ apiLoader: false });
                this.setState({ apprenticeList: [], usersearching: false })
                console.log(err)
            })
    }
