    onSearch = debounce((query) => {

        if (query === "") {
            if (this.state.isDisabled_program_list_area) {
                this.getApprentices('')
            } else {
                this.getStudentList('', this.state.selected_program);
            }
        } else {
            if (this.state.isDisabled_program_list_area) {
                this.getApprentices(query)
            } else {
                this.getStudentList(query, this.state.selected_program);
            }
        }
    }, 500);
