    getOutlookCalendarDatas = async (e) => {
        let access_token = getVcToken().token
        const headers = {
            "Authorization": 'Bearer ' + access_token
        };
        if (access_token !== null) {
            await mykademyApiService
                .getOutlookCalendarDatas(headers)
                .then(async (response) => {
                    if (response.status === 200) {
                        let idObj = response.data.value.find((item) => item.isDefaultCalendar === true && item.name === "Calendar")
                        this.setState({ calendar_id: idObj.id })
                    }
                })
                .catch((err) => {
                    console.log(err)
                })
        }

    }
    formSubmit = async (e) => {
        e.preventDefault()
        if (!this.state.open_phase_area && !this.state.isDisabled_program_list_area) {
            Notification('The selected Apprenticeship Programme does not have phase for creating a video session. Please contact your administrator', "warning", "Warning")
            return
        } else {
            const isValid = this.validateFields(true);
            if (isValid) {
                this.setState({ isApiCalling: true })
                var body = {
                    "subject": this.state.values.title,
                    // "body": {
                    // "contentType": "HTML",
                    // "content": "Happy holidays!"
                    // },
                    "start": {
                        "dateTime": this.state.values.start,
                        "timeZone": "UTC"
                    },
                    "end": {
                        "dateTime": this.state.values.end,
                        "timeZone": "UTC"
                    },
                    "location": {
                        "displayName": "OLIVE VLE"
                    },
                    "attendees": this.state.values.attendees,
                    "isOnlineMeeting": true
                }
                if (this.state.isEditVc) {
                    console.log(body)
                    await this.updateMyPortfolioLiveClass(body)
                    return
                }
                let access_token = getVcToken().token
                const headers = {
                    "Authorization": 'Bearer ' + access_token
                };
                console.log('this.state.values= ', this.state.values)
                // let joinUrl = "https://teams.microsoft.com/l/meetup-join/19%3ameeting_MzIwMjJkYTgtMGNlZi00NGUzLWEyY2ItODY3Yjk2YTk1MDMz%40thread.v2/0?context=%7b%22Tid%22%3a%22f8e38117-9f7d-4b4f-96ab-b05d8a5cabbf%22%2c%22Oid%22%3a%222b84466d-c019-420a-a464-858e04cf267d%22%7d"
                // await this.createMyportfolioLiveClass(joinUrl)

                // return
                if (access_token !== null) {
                    let calendar_id = this.state.calendar_id
                    await mykademyApiService
                        .createMsTeamsMeeting(headers, body, calendar_id)
                        .then(async (response) => {
                            if (response.data.onlineMeeting.joinUrl !== null) {
                                await this.createMyportfolioLiveClass(response.data.onlineMeeting.joinUrl, response.data.id)
                            } else {
                                alert('Sorry!. Unable to generate meeting url')
                            }
                        })
                        .catch((err) => {
                            console.log(err)
                            this.props.callbackFn({ status: false, response: err })
                        })
                }
            } else {
                Notification("Please fill required fields!", "warning", "Warning")
            }
        }
    }
    updateMyPortfolioLiveClass = async (body) => {
        console.log(body)
        // body.start.dateTime = new Date(body.start.dateTime).toISOString()
        console.log(body)
        let access_token = getVcToken().token
        const headers = {
            "Authorization": 'Bearer ' + access_token,
            "Content-Type": 'application/json'
        };
        await commonApiService.getTeamsEventById(this.state.event.live_classes.id, 'myportfolio')
            .then(async (response) => {
                let teams_event_id = response.data.data.teams_event_id
                await mykademyApiService
                    .updateMsTeamsMeeting(headers, body, teams_event_id)
                    .then((response) => {
                        console.log('response', response)
                        this.updateEvent()
                        // this.props.callbackFn({ status: 'edit', response: response })
                    })
                    .catch((err) => {
                        console.log('err', err)

                    })
            })
            .catch((err) => {
                console.log(err)
            })

    }
