import React from "react";
import { userData, appToken, getAuthtoken } from "../services/common.service";
import NotFound from "../../shared_components/notFound";
import { debounce } from "lodash";
import HalfDoughNut from "./halfDoughNut";
import DoughNut from "./doughNut";
import ProgrammeJump from "./programmeJump";
import ProgressBar from "./progressBar";
import * as microsoftTeams from "@microsoft/teams-js";

import serchImg from '../../../assets/img/landing/search.svg'
import mobilesearchImg from '../../../assets/img/landing/mobile-search.svg'
import initialLoader from '../../../custom_assets/loader.svg'
import defaultbannerimg from '../../../custom_assets/group-3@3x.png'
import threedotimg from '../../../assets/img/landing/three-dots.svg'
import loadmoreLoaderimg from '../../../custom_assets/Chasing arrows.gif'
import norecordImg from '../../../assets/img/landing/portfolio-img.png'
const apiService = require("../services/api.service");
var search = "";
export default class ProgrammeList extends React.Component {
  state = {
    gridView: true,
    isLoggedIn: false,
    auth_token: "",
    token: "",
    memberdetails: {},
    curentpage: 1,
    per_page: 8,
    member_apprenticeship_details: [],
    query: "",
    clickLimit: "0",
    initial_loader: true,
    progressDetails: [],
    progressLoader: true,
    innerLoader: true,
    clickCount: 0,
    loadmore_loader: false,
    member_details: [],
  };
  handleSearch = debounce((text) => {
    this.setState({ innerLoader: true, clickCount: 0 });
    search = text.trim();
    console.log(search);
    if (search === "") {
      this.setState({
        member_apprenticeship_details: [],
        per_page: 8,
        curentpage: 1,
      });
      this.programmes();
    } else {
      this.setState({
        member_apprenticeship_details: [],
        per_page: 0,
        curentpage: 0,
      });

      this.programmesSearch();
    }
  }, 500);
  componentDidMount() {
    if (userData() !== null) {
      let event_link = localStorage.getItem('event_link')
      if (localStorage.getItem('programe_id') !== null) {
        let programe_id = localStorage.getItem('programe_id')
        this.programeDetail(programe_id)
      } else if (event_link !== null) {
        window.location.href = "programe-detail/event"
      }
      // microsoftTeams.initialize();
      // microsoftTeams.getContext((context) => {
      //   let programe_id = context.subEntityId;
      //   console.log("programe_id", programe_id);
      //   console.log("context.subEntityId", context["subEntityId"]);
      //   if (context.subEntityId) {
      //     this.programeDetail(context.subEntityId);
      //   }
      // });
      this.setState({
        innerLoader: true,
        isLoggedIn: true,
        initial_loader: true,
        progressLoader: true,
      });
      this.programmeProgress();
      this.programmes();
    } else {
      this.setState({
        innerLoader: false,
        isLoggedIn: false,
        progressLoader: true,
      });
    }
  }
  programmesSearch = async () => {
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    if (this.state.auth_token !== null) {
      headers["Authorization"] = "Bearer " + getAuthtoken();
    }
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(userData().data._token));
    formData.append("search", search);
    await apiService
      .listSearchPrograme(headers, formData)
      .then((response) => {
        this.setState({
          member_apprenticeship_details:
            response.data.member_apprenticeship_details,
          innerLoader: false,
          clickLimit: response.data.total_member_apprenticeships,
          progressLoader: false,
          loadmore_loader: false,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  };
  programmes = async () => {
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    if (this.state.auth_token !== null) {
      headers["Authorization"] = "Bearer " + getAuthtoken();
    }
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(userData().data._token));
    formData.append("current_page", this.state.curentpage);
    formData.append("per_page", this.state.per_page);

    await apiService
      .listPrograme(headers, formData)
      .then((response) => {
        if (response.status === 200) {
          if (response.data.status === 'error') {
            alert(response.data.message + 'please Sign In again!')
            localStorage.clear()
            window.location.reload()
            // window.location.href = window.location.origin+'/tab'
            return
          }
          this.setState({ isLoading: true });
          console.log(response.data.member_apprenticeship_details,'get member detilasss')
          this.setState({
            member_apprenticeship_details:
              response.data.member_apprenticeship_details,
            clickLimit: response.data.total_member_apprenticeships,
            innerLoader: false,
            progressLoader: false,
            loadmore_loader: false,
          });
        }
      })
      .catch((error) => {
        console.log(error, "get error");
      });
  };
  loadMoreClick = async () => {
    this.setState({ loadmore_loader: true });
    this.state.clickCount++;
    let currentPage = this.state.clickCount + 1;
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    if (this.state.auth_token !== null) {
      headers["Authorization"] = "Bearer " + getAuthtoken();
    }
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(userData().data._token));
    formData.append("current_page", currentPage);
    formData.append("per_page", this.state.per_page);
    await apiService
      .programsLoadMore(headers, formData)
      .then((response) => {
        console.log(response.data.member_apprenticeship_details);
        if (response.data.member_apprenticeship_details.length !== 0) {
          this.setState({ isLoading: true });
          this.setState({
            member_apprenticeship_details: this.state.member_apprenticeship_details.concat(
              response.data.member_apprenticeship_details
            ),
            loadmore_loader: false,
          });
        } else {
          this.setState({ clickCount: 0 });
          this.programmes();
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  programmeProgress = async () => {
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    if (this.state.auth_token !== null) {
      headers["Authorization"] = "Bearer " + getAuthtoken();
    }
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(userData().data._token));
    formData.append("memberid", userData().data.member_details.id);

    await apiService
      .programmeProgress(headers, formData)
      .then((response) => {
        console.log(response.data.status, 'get from progressss')
        if (response.status === 200) {
          if (response.data.status === 'error') {
            alert(response.data.message + 'please Sign In again!')
            localStorage.clear()
            window.location.href = window.location.origin + '/tab'
            return
          }
          console.log(response.data.member_details,'get member detilasss')
          let data = {
            total_members: response.data.member_details,
            completed:
              response.data.total_member_apprenticeship_programs_completed,
            not_started:
              response.data.total_member_apprenticeship_programs_not_started,
            started: response.data.total_member_apprenticeship_programs_started,
            total: response.data.member_details.length
          };
          this.setState(
            {
              progressDetails: data,
              member_details: response.data.member_details,
              progressLoader: false,
              loadmore_loader: false,
              initial_loader: false,
            },
            () => {
              console.log(this.state.progressDetails, "ressprogress");
            }
          );
        }
      })

      .catch((error) => {
        console.log(error, "get error");
      });
  };

  gotResponse(val) {
    if (val === "success") this.componentDidMount();
  }
  programeDetail = (programe_id) => {
    console.log(programe_id, "get id");
    window.location.href = `programe-detail/${programe_id}`;
  };
  gridToList = () => {
    this.setState({
      gridView: true,
    });
  };
  listToGrid = () => {
    this.setState({
      gridView: false,
    });
  };

  render() {
    console.log(this.state.member_details, "get");
    return (
      <div>
        {!this.state.isLoggedIn ? (
          <NotFound
            message="Sorry, you need to login to Appreticeship Application to access your Programmes"
            domain="myportfolio"
            response={(val) => this.gotResponse(val)}
          />
        ) : (
            // loadding
            <React.Fragment>
              {this.state.initial_loader ? (
                <div className="bg-main">
                  <div style={{ textAlign: "center" }}>
                    <img
                      alt="view more"
                      style={{ width: "45px", height: "100vh" }}
                      src={initialLoader}
                    />
                  </div>
                </div>
              ) : (
                  <>
                    {this.state.member_apprenticeship_details.length !== 0 ? (
                      <>
                        <div className="filter-bar-block">
                          <div className="filter-bar-holder">
                            <div className="grid-wrap">
                              <button
                                className={
                                  this.state.gridView
                                    ? "grid-color btn-grid"
                                    : "btn-grid"
                                }
                                onClick={this.gridToList}
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  viewBox="0 0 16 16"
                                  width="16"
                                  height="16"
                                >
                                  <path d="M5.5,9h-4C0.7,9,0,9.7,0,10.5v4C0,15.3,0.7,16,1.5,16h4C6.3,16,7,15.3,7,14.5v-4C7,9.7,6.3,9,5.5,9z M6,14.5C6,14.8,5.8,15,5.5,15h-4C1.2,15,1,14.8,1,14.5v-4C1,10.2,1.2,10,1.5,10h4C5.8,10,6,10.2,6,10.5V14.5z" />
                                  <path d="M5.5,0h-4C0.7,0,0,0.7,0,1.5v4C0,6.3,0.7,7,1.5,7h4C6.3,7,7,6.3,7,5.5v-4C7,0.7,6.3,0,5.5,0z M6,5.5C6,5.8,5.8,6,5.5,6h-4C1.2,6,1,5.8,1,5.5v-4C1,1.2,1.2,1,1.5,1h4C5.8,1,6,1.2,6,1.5V5.5z" />
                                  <path d="M14.5,0h-4C9.7,0,9,0.7,9,1.5v4C9,6.3,9.7,7,10.5,7h4C15.3,7,16,6.3,16,5.5v-4C16,0.7,15.3,0,14.5,0z M15,5.5C15,5.8,14.8,6,14.5,6h-4C10.2,6,10,5.8,10,5.5v-4C10,1.2,10.2,1,10.5,1h4C14.8,1,15,1.2,15,1.5V5.5z" />
                                  <path d="M14.5,9h-4C9.7,9,9,9.7,9,10.5v4c0,0.8,0.7,1.5,1.5,1.5h4c0.8,0,1.5-0.7,1.5-1.5v-4C16,9.7,15.3,9,14.5,9z M15,14.5c0,0.3-0.2,0.5-0.5,0.5h-4c-0.3,0-0.5-0.2-0.5-0.5v-4c0-0.3,0.2-0.5,0.5-0.5h4c0.3,0,0.5,0.2,0.5,0.5V14.5z" />
                                </svg>
                              </button>
                              <button
                                className={
                                  !this.state.gridView
                                    ? "grid-color btn-grid"
                                    : "btn-grid"
                                }
                                onClick={this.listToGrid}
                              >
                                <svg
                                  xmlns="http://www.w3.org/2000/svg"
                                  viewBox="0 0 16 13.1"
                                  width="16"
                                  height="16"
                                >
                                  <path d="M0.8,1.5h10.4l0.1,0c0.4,0,0.6-0.4,0.6-0.7c0-0.4-0.3-0.8-0.8-0.8H0.8L0.6,0C0.3,0.1,0,0.4,0,0.8C0,1.2,0.3,1.5,0.8,1.5z" />
                                  <path d="M15.2,6H0.8l-0.1,0C0.3,6.1,0,6.4,0,6.8c0,0.4,0.3,0.8,0.8,0.8h14.5l0.1,0c0.4,0,0.6-0.4,0.6-0.7C16,6.4,15.6,6,15.2,6z" />
                                  <path d="M10.2,11.6H0.8l-0.1,0c-0.4,0-0.6,0.4-0.6,0.7c0,0.4,0.3,0.8,0.8,0.8h9.4l0.1,0c0.4,0,0.6-0.4,0.6-0.7C10.9,11.9,10.6,11.6,10.2,11.6z" />
                                </svg>
                              </button>
                            </div>
                            <div className="input-group flex-nowrap filter-search">
                              <input
                                type="text"
                                className="form-control"
                                placeholder="Find"
                                aria-label="Find"
                                onChange={(e) => this.handleSearch(e.target.value)}
                                aria-describedby="addon-wrapping"
                              />
                              <span
                                className="input-group-text"
                                id="addon-wrapping"
                              >
                                <img
                                  src={serchImg}
                                  alt="search"
                                />
                              </span>
                            </div>

                            <a className="mobile-search-icon">
                              <img
                                src={mobilesearchImg}
                                alt="search"
                              />
                            </a>
                          </div>
                        </div>
                        <div className="container-fluid courses-block">
                          <div className="row mb-20 ">
                            <HalfDoughNut
                              progressDetails={this.state.progressDetails}
                            />
                            <DoughNut progressDetails={this.state.progressDetails} />
                            <ProgressBar
                              progressDetails={this.state.progressDetails}
                            />
                            <ProgrammeJump />
                          </div>
                          {
                            this.state.innerLoader ? (
                              <div className="step step-3 active">
                                <div className="d-flex justify-content-center align-items-center spinner-content">
                                  <div style={{ marginTop: '10%' }} className="spinner-border" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                  </div>
                                </div>
                              </div>
                            ) :
                              (
                                <React.Fragment>
                                  {
                                    this.state.member_apprenticeship_details.length !== 0 ? (

                                      <div >
                                        <div>
                                          {this.state.gridView ? (
                                            <>
                                              <div className="row">
                                                {this.state.member_apprenticeship_details.map(
                                                  (val, i) => {
                                                    return (
                                                      <div
                                                        key={i}
                                                        className="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20"
                                                      >
                                                        <div className="course-thumb-list">
                                                          <div className="img-wrap">
                                                            <img
                                                              src={
                                                                val.apprenticeship_details
                                                                  ?.occupation?.banner_image ??
                                                                defaultbannerimg
                                                              }
                                                              alt="thumb images"
                                                            />
                                                          </div>
                                                          <div className="detail-text-wrap">
                                                            <div className="course-title programme_tittle">
                                                              <h4 className="same-height">
                                                                {
                                                                  val.apprenticeship_details
                                                                    .occupation.name
                                                                }
                                                              </h4>
                                                              {/* <a className="three-dots-dropdown">
                                                              <img
                                                                src={threedotimg}
                                                                alt="dot"
                                                              />
                                                            </a> */}
                                                            </div>
                                                            <span className="progress-completed">
                                                              Progress:
                                              {`${val?.progress ?? 0}%`}
                                                            </span>
                                                            <div className="progress common-progress">
                                                              <div
                                                                className="progress-bar progress-bar-primary"
                                                                role="progressbar"
                                                                style={{
                                                                  width: `${val?.progress ?? 0}%`,
                                                                }}
                                                                aria-valuenow="25"
                                                                aria-valuemin="0"
                                                                aria-valuemax="100"
                                                              ></div>
                                                            </div>
                                                            <div className="course-thumb-btn-wrap">
                                                              <a
                                                                onClick={() =>
                                                                  this.programeDetail(val.id)
                                                                }
                                                                className="btn common-course-btn btn-primary resumebtn"
                                                              >
                                                                {`${val?.progress ?? 0}` === "0"
                                                                  ? "Start"
                                                                  : "View"}
                                                              </a>
                                                              <span className="course-status">
                                                                {`${val?.progress ?? 0}` === "0"
                                                                  ? "Not Started"
                                                                  : "In Progress"}
                                                              </span>
                                                            </div>
                                                          </div>
                                                        </div>
                                                      </div>
                                                    );
                                                  }
                                                )}
                                              </div>
                                              <div className="program-listing__load-more">
                                                {this.state.clickLimit ===
                                                  this.state.member_apprenticeship_details.length.toString() ? (
                                                    <div
                                                      className="program-listing__load-more"
                                                      style={{ marginTop: "0%" }}
                                                    ></div>
                                                  ) : (
                                                    <button className="olive-flat-button olive-accent">
                                                      {this.state.loadmore_loader ? (
                                                        <img
                                                          style={{ width: "27px" }}
                                                          alt="loading"
                                                          src={loadmoreLoaderimg}
                                                        />
                                                      ) : (
                                                          <span
                                                            onClick={() => this.loadMoreClick()}
                                                            className="olive-button-wrapper"
                                                          >
                                                            {" "}
                                        Load more{" "}
                                                          </span>
                                                        )}
                                                    </button>
                                                  )}
                                              </div>
                                            </>
                                          ) : (
                                              <div className="course-list-block">
                                                <h4>Programmes</h4>
                                                <div className="table-responsive sessions-table-list">
                                                  <table className="table">
                                                    <thead>
                                                      <tr>
                                                        <th scope="col">Name</th>
                                                        <th width="300" scope="col">
                                                          Progress
                                      </th>
                                                        <th width="90" scope="col">
                                                          Status
                                      </th>
                                                        <th width="240" scope="col"></th>
                                                      </tr>
                                                    </thead>
                                                    <tbody>
                                                      {this.state.member_apprenticeship_details.map(
                                                        (val, i) => {
                                                          return (
                                                            <tr key={i}>
                                                              <td scope="row">
                                                                <div className="info-wrap">
                                                                  <img
                                                                    className="prglistimg"
                                                                    src={
                                                                      val.apprenticeship_details
                                                                        ?.occupation
                                                                        ?.banner_image ??
                                                                      defaultbannerimg
                                                                    }
                                                                    alt=""
                                                                  />
                                                                  {
                                                                    val.apprenticeship_details
                                                                      .occupation.name
                                                                  }
                                                                </div>
                                                              </td>
                                                              <td>
                                                                <div className="custom-progress-wrap">
                                                                  <div className="progress-holder">
                                                                    <div className="progress common-progress">
                                                                      <div
                                                                        className="progress-bar progress-bar-primary"
                                                                        role="progressbar"
                                                                        style={{
                                                                          width: `${val?.progress ?? 0
                                                                            }%`,
                                                                        }}
                                                                        aria-valuenow="25"
                                                                        aria-valuemin="0"
                                                                        aria-valuemax="76"
                                                                      ></div>
                                                                    </div>
                                                                    <div className="progress-value">{`${val?.progress ?? 0
                                                                      }%`}</div>
                                                                  </div>
                                                                </div>
                                                              </td>
                                                              <td>
                                                                <div className="info-wrap">
                                                                  {" "}
                                                                  {`${val?.progress ?? 0}` === "0"
                                                                    ? "Not Started"
                                                                    : "In Progress"}
                                                                </div>
                                                              </td>
                                                              <td>
                                                                <div className="course-thumb-btn-wrap">
                                                                  <a
                                                                    onClick={() =>
                                                                      this.programeDetail(val.id)
                                                                    }
                                                                    className="btn common-course-btn btn-primary resumebtn"
                                                                  >
                                                                    {`${val?.progress ?? 0}` === "0"
                                                                      ? "Start"
                                                                      : "View"}
                                                                  </a>
                                                                </div>
                                                              </td>
                                                            </tr>
                                                          );
                                                        }
                                                      )}
                                                    </tbody>
                                                  </table>
                                                </div>
                                                <div className="program-listing__load-more">
                                                  {this.state.clickLimit ===
                                                    this.state.member_apprenticeship_details.length.toString() ? (
                                                      <div
                                                        className="program-listing__load-more"
                                                        style={{ marginTop: "0%" }}
                                                      ></div>
                                                    ) : (
                                                      <button className="olive-flat-button olive-accent">
                                                        {this.state.loadmore_loader ? (
                                                          <img
                                                            style={{ width: "27px" }}
                                                            alt="loading"
                                                            src={loadmoreLoaderimg}
                                                          />
                                                        ) : (
                                                            <span
                                                              onClick={() => this.loadMoreClick()}
                                                              className="olive-button-wrapper"
                                                            >Load more
                                                            </span>
                                                          )}
                                                      </button>
                                                    )}
                                                </div>
                                              </div>
                                            )}
                                        </div>
                                      </div>
                                    ) : (
                                        <div style={{ textAlign: 'center' }}>


                                          <h4 >No Results Found</h4>

                                        </div>
                                      )
                                  }

                                </React.Fragment>
                              )
                          }
                        </div>


                      </>
                    ) : (
                        <div class="empty-block">
                          <div class="img-wrap">
                            <img src={norecordImg} alt="courses" />
                          </div>
                          <h4>Currently, you’re not enrolled in any programmes.</h4>
                          <p>To setup your Portfolio, please speak with your admin.</p>
                        </div>
                      )}
                  </>
                )}
            </React.Fragment>

            //  loadding end
          )}
      </div>
    );
  }
}
