import React from "react";
import { userData, is_super_org_admin_prgms } from "../services/common.service";
import { getAuthtoken, is_super_org_admin } from "../services/common.service";
import AdminNavbar from "../../shared_components/adminNavbar";
import { debounce } from "lodash";
import { subdropdownData, MyportfolioAdminDropdownContents,MykademyAdminDropdownContents } from "../../../config";
import SuperAdminprgmlist from './superAdmin'
import Notfound_withoutlogin from '../../shared_components/Notfound_withoutlogin';

const apiService = require("../services/api.service");
const mykademyCommonService = require("../../mykademy/services/common.service");
const myportfolioCommonService = require("../services/common.service")

var query = "";

export default class AdminProgramList extends React.Component {

  state = {
    user_token: "",
    curentpage: 1,
    per_page: 10,
    programs: [],
    initial_loader: true,
    query: "",
    emptyData: false,
    class: false,
    loadmore_loader: false,
    isSuperadmin: false
  };

  handleSearch = debounce((text) => {
    query = text.trim();
    if (query === "") {
      this.setState({
        programs: [],
        initial_loader: true,
        per_page: 6,
        curentpage: 1,
      });
      this.getProgramelist();
    } else {
      this.setState({
        programs: [],
        initial_loader: true,
        per_page: 0,
        curentpage: 0,
      });
      this.searchPrograms(query);
    }
  }, 500);

  componentDidMount = () => {
    if (userData() !== null) {
      if (is_super_org_admin_prgms()) {
        this.setState({ isSuperadmin: true })

      } else {
        this.setState({ isSuperadmin: false })

        this.setState({ user_token: userData().data._token }, () => {
          this.getProgramelist();
        });
      }
    }
  };

  async getProgramelist() {
    let formData = new FormData();
    formData.append("_token", decodeURIComponent(this.state.user_token));
    formData.append("current_page", this.state.curentpage);
    formData.append("per_page", this.state.per_page);
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    let portfolio_authtoken = getAuthtoken();
    if (portfolio_authtoken !== null) {
      headers["Authorization"] = "Bearer " + portfolio_authtoken;
    }

    if (is_super_org_admin()) {
      await apiService
        .getProgrameList_superorg_admin(headers, formData)
        .then((response) => {
          this.setState({
            programs: this.state.programs.concat(
              response.data.occupations_details
            ),
            total_rows: response.data.total_rows,
            initial_loader: false,
            loadmore_loader: false
          });
          this.checkArray(response.data.total_occupations_count);
        })
        .catch((err) => {
          this.setState({ initial_loader: false, loadmore_loader: false });
          console.log(err)
        });
    } else {

      await apiService
        .getProgrameList_admin(headers, formData)
        .then((response) => {
          this.setState({
            programs: this.state.programs.concat(
              response.data.apprenticeships_details
            ),
            total_rows: response.data.total_rows,
            initial_loader: false,
            loadmore_loader: false
          });
          this.checkArray(response.data.total_apprenticeship_count);
        })
        .catch((err) => {
          this.setState({ initial_loader: false, loadmore_loader: false });
          console.log(err)
        });
    }
  }

  async searchPrograms(query) {
    const headers = {
      "Content-Type": "multipart/form-data",
    };

    let portfolio_authtoken = getAuthtoken();
    if (portfolio_authtoken !== null) {
      headers["Authorization"] = "Bearer " + portfolio_authtoken;
    }

    if (is_super_org_admin()) {
      await apiService
        .getProgrameList_superorg_admin_search(
          headers,
          query,
          decodeURIComponent(this.state.user_token),
          this.state.curentpage,
          this.state.per_page
        )
        .then((response) => {
          this.setState({
            programs: this.state.programs.concat(
              response.data.occupations_details
            ),
            total_rows: response.data.total_rows,
            initial_loader: false,
            loadmore_loader: false
          });
          this.checkArray(response.data.total_occupations_count);
        })
        .catch((err) => {
          this.setState({ initial_loader: false, loadmore_loader: false });
          console.log(err)
        });
    } else {

      await apiService
        .getProgrameList_admin_search(
          headers,
          query,
          decodeURIComponent(this.state.user_token),
          this.state.curentpage,
          this.state.per_page
        )
        .then((response) => {

          this.setState({
            programs: this.state.programs.concat(
              response.data.apprenticeships_details
            ),
            total_rows: response.data.total_rows,
            initial_loader: false,
            loadmore_loader: false
          });
          this.checkArray(response.data.total_apprenticeship_count);
        })
        .catch((err) => {
          this.setState({ initial_loader: false, loadmore_loader: false });
          console.log(err)
        });
    }
  }

  programsLoadMore() {
    this.setState(
      {
        curentpage: this.state.curentpage + 1,
        loadmore_loader: true
      },
      () => {
        this.getProgramelist();
      }
    );
  }

  checkArray = (val) => {

    if (val === "0") {
      this.setState({ emptyData: true });
    } else {
      this.setState({ emptyData: false });
    }

  };

  handleOnInputChange = (event) => {
    this.setState({ query: event });
  };

  toggleNavbar = () => {
    this.setState({ class: !this.state.class })
  }

  weeklyImg = (val) => {
    if (val === 1) {
      return <img src="/./assets/images/weekly-delivery.svg" alt="" title="Weekly Delivery" />
    } else {
      return <img src="/./assets/images/apprenticeship-delivery.svg" alt="" title="Apprenticeship Delivery" />
    }
  }

  render() {
    if (!mykademyCommonService.isAdmin()) {
			MykademyAdminDropdownContents.one = "";
      }
		myportfolioCommonService.getRolls()
      
    return (
      <>
        <div class="filter-bar-block">
          <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
            <div class="bar1"></div>
            <div class="bar2"></div>
            <div class="bar3"></div>
          </a>
         {
           !mykademyCommonService.isTutor() &&
          <div class="dropdown view-option common-option">
            <button class="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
              Add Option
        </button>
       
        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
        <li><a class="dropdown-item" href="#">Add Programme</a></li>
        <li><a class="dropdown-item" href="#">Edit Enrolment Data</a></li>
      </ul>
        
            
          </div>
         }

          <div class="filter-bar-holder">

            <div class="input-group flex-nowrap filter-search">
              <input type="text" class="form-control" placeholder="Find" onChange={(e) => this.handleSearch(e.target.value)} aria-label="Find" aria-describedby="addon-wrapping" />
              <span class="input-group-text" id="addon-wrapping">
                <img src="/./assets/images/landing/search.svg" alt="search" />
              </span>
            </div>
            <a class="mobile-search-icon" href="javascript:void(0);"><img src="/./assets/images/landing/mobile-search.svg" alt="search" /></a>
          </div>
        </div>
        <div className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>

          <AdminNavbar class={this.state.class} data={subdropdownData} DropdownData={MykademyAdminDropdownContents} />
          {this.state.initial_loader ? (
            <div style={{ textAlign: "center" }}>
              <img
                style={{ width: "45px", height: "100vh" }}
                src="/./custom_assets/loader.svg"
                alt="loader"
              />
            </div>
          ) : (
            <div class="container-fluid courses-block">
              {!this.state.emptyData ?(
              <div class="course-list-block">
                <h4>Programmes</h4>
                <div class="table-responsive sessions-table-list programmes-table-list">
                  <table class="table">
                    <thead>
                      <tr>
                        <th scope="col">Name</th>
                        <th width="90" scope="col">Apprentices</th>
                        <th width="90" scope="col">Phases</th>
                        <th width="90" scope="col">Status</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.programs.map((val, i) => {
                        return (
                          <tr>
                            <td scope="row">
                              <div  class="info-wrap">
                                <img height="37px" src={val?.banner_image ?? "img-default.png"} alt="" />
                                {val.name}
                              </div>
                            </td>
                            <td><div class="info-wrap">{val.member_count}</div></td>
                            <td><div class="info-wrap">{val.total_phase_count}</div></td>
                            <td><div class="info-wrap">Published</div></td>
                            
                          </tr>
                        )
                      })
                      }
                    </tbody>
                  </table>
                </div>

                <div className="program-listing__load-more load-more-wrap">
                  {this.state.total_rows < 6 ||
                    this.state.emptyData ||
                    this.state.initial_loader ? (
                    <div></div>
                  ) : (
                    <button
                      className="olive-flat-button olive-accent"

                    >
                      {
                        this.state.loadmore_loader ?
                          <img style={{ width: '27px' }} alt="loading" src="/./custom_assets/Chasing arrows.gif" />
                          :
                          <span onClick={() => this.programsLoadMore()} className="olive-button-wrapper"> Load more </span>
                      }
                    </button>
                  )}
                </div>
              </div>
            ):(
              <div class="course-list-block">
                not found
              </div>
            )}
            </div>
          )}
        </div>

      </>

    )
  }
}
