import React, { Component } from 'react'
import { Notification } from '../../../../services/common.service';
import { appToken, userData } from "../../../mykademy/services/common.service"

import {getAuthtoken,portfolioUser} from "../../../myportfolio/services/common.service"
const myportfolioApiservice = require('../../../myportfolio/services/api.service')
const mykademyApiservice = require('../../../mykademy/services/api.service')

let error_style = {
    marginBottom: '15px',
    paddingLeft: '13px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: '1.33',
    letterSpacing: 'normal',
    textAlign: 'left',
    color: '#c4314b',
}
let inputErr = {
    border: ' solid 1px #c4314b',
    marginBottom: '5px',
    background: 'none',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'right calc(.375em + .1875rem) center',
    backgroundSize: 'calc(.75em + .375rem) calc(.75em + .375rem)'
}
export default class UpdateVlepassword extends Component {
    constructor(props) {
		super(props)
		this.state = {
            user: {},
            errors: {},
            apiLoader:false
        }
    }

    update = () => {
        this.props.updateState({ show_vle_profile: true })
        this.props.updateState({ open_edit_vlesecurity_section: false })
    }
    close = () => {
        this.props.updateState({ show_vle_profile: true })
        this.props.updateState({ open_edit_vlesecurity_section: false })
    }
    inputhandle = (field, e) => {
        let addValues = this.state.user;
        let errors = this.state.errors;
        addValues[field] = e.target.value
        errors[field] = ""
        
        addValues[field] = e.target.value;

        this.setState({ addValues, errors });
       
    }
    formSubmit = async(e)=>{
        e.preventDefault();
        const isValid = this.validateFields(true)
        if(isValid){
            //portfoio update 
            this.setState({apiLoader:true})
            const headers = {
                'Content-Type': 'multipart/form-data',
            }
            
            if (getAuthtoken() !== null) {
                headers['Authorization'] = 'Bearer ' + getAuthtoken()
            }
            let formData = new FormData();
            formData.append("_token", decodeURIComponent(portfolioUser().data._token));
            formData.append('old_password', this.state.user.cPassword)
            formData.append('new_password', this.state.user.nPassword)
            formData.append('confirm_password', this.state.user.cfPassword)
            await myportfolioApiservice.changePassword(headers, formData)
            .then((response)=>{
                console.log(response.data)
                if(response.data.status === 'Success'){
                    this.updateonMk()
                    //  Notification("Password updated successfully", "success", "Success");
            // setTimeout(()=>{

                // this.update()
            // },3000)

                }else{
                    let errors = {}
                    if(response.data.message === 'Your password should not be your last 5 passwords'){
                        errors['nPassword'] = response.data.message
                        this.setState({ errors: errors,apiLoader:false })  
                    }else{

                        errors['cPassword'] = response.data.message
                        this.setState({ errors: errors,apiLoader:false })
                    }
                    // this.setState({databaseerror:response.data.message})
            Notification("Error", "warning", "Warning");
            this.setState({apiLoader:false })

                    console.log('errorrr')
                }
            
            }).catch((err)=>{
                console.log(err)
            })
        }
    }
    updateonMk = async()=>{
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        let params = {
            "password": btoa(this.state.user.nPassword),
            "current_password": btoa(this.state.user.cPassword)
        }
        await mykademyApiservice
            .updatePassword(headers, params)
            .then((response) => {

            console.log(response.data.metadata)
                if(response.data.metadata.error===false){
            this.setState({apiLoader:false})

                    Notification("Password updated successfully", "success", "Success");
                    setTimeout(()=>{
                       this.update()
                   },3000)
                }else{
                    let errors = {}
                    if(response.data.metadata.message === "Incorrect Current Password "){
                        errors['cPassword'] = response.data.metadata.message
                        this.setState({ errors: errors,apiLoader:false })  
                    }else{

                        errors['nPassword'] =response.data.metadata.message
                        this.setState({ errors: errors,apiLoader:false })
                    }
                    // this.setState({databaseerror:response.data.message})
                     Notification("Error", "warning", "Warning");
                     this.setState({apiLoader:false })

                    console.log('errorrr')
                
                }
                 
            })
            .catch((error) => {
                console.log(error)
            });

    }
    validateFields = (showErrMessage) => {
        let fields = this.state.user;
        let errors = {};
        let formIsValid = true;
        //current password
        if (!fields['cPassword']) {
            formIsValid = false;
            errors['cPassword'] = "Current Password cannot be empty"
        } 
        //new password
        if (!fields["nPassword"]) {
            formIsValid = false;
            errors["nPassword"] = "New Password cannot be empty";
        }else if(!fields['nPassword'].match(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/)){
            formIsValid = false;
            errors['nPassword'] = 'Password must contain 8 characters with at least a lowercase, uppercase ,number and a special character'
        }
        //confirm password
        if (!fields["cfPassword"]) {
            formIsValid = false;
            errors["cfPassword"] = "Confirm Password cannot be empty";
        }
        if(fields['nPassword']!== fields['cfPassword']){
            formIsValid = false;
            errors["nPassword"] = "New Password and Confirm password are not Matching";
        }
      
        if (showErrMessage) {
            this.setState({ errors: errors },()=>console.log(this.state.errors.Password));
        }
        return formIsValid;
    }
    render(){
        return(
            <div id="security-step" className="step active">
                 {this.state.apiLoader &&
                <div class="loading">foregroung loader&#8230;</div>
            }
            <div className="profile-header">
                <div className="img-wrap">
                    <img src={this.props.image_url} alt="" />
                </div>
                <h4>Profile</h4>
            </div>

            <div className="profile-update">
                <span>Security</span>

            </div>

            <form className="profile-form" onSubmit={this.formSubmit}>
                <div className="input-wrap">
                    <div className="form-group">
                        <label for="cPassword">Current Password</label>
                        <input type="password"  name="cPassword" 
                        className="form-control"
                        style={this.state.errors['cPassword'] ? inputErr : {}}
                        onChange={this.inputhandle.bind(this, "cPassword")}
                        placeholder=""  />
                          {this.state.errors['cPassword'] &&
                            <p style={error_style}>{this.state.errors['cPassword']}</p>
                        }
                    </div>
                    <div className="form-group">
                        <label for="nPassword">New Password</label>
                        <input type="password" name="nPassword" 
                        className="form-control" 
                        style={this.state.errors['nPassword'] ? inputErr : {}}
                        onChange={this.inputhandle.bind(this, "nPassword")}
                        placeholder=""  />
                    </div>
                    {this.state.errors['nPassword'] &&
                            <p style={error_style}>{this.state.errors['nPassword']}</p>
                        }

                    <div className="form-group">
                        <label for="cfPassword">Confirm New Password</label>
                        <input type="password"  name="cfPassword" 
                        className="form-control"
                        style={this.state.errors['cfPassword'] ? inputErr : {}}
                        onChange={this.inputhandle.bind(this, "cfPassword")}
                        placeholder=""  />
                    </div>
                    {this.state.errors['cfPassword'] &&
                            <p style={error_style}>{this.state.errors['cfPassword']}</p>
                        }
                </div>
                {
                    !this.state.formIsValid &&
                <div className="error-msg" style={{color:'red'}}>{this.state.errors.Password}</div>
                    }
                {/* <p style={error_style}>{this.state.databaseerror}</p> */}

                <div className="profile-modal-foo">
                    <button onClick={() => this.close()} type="button" className="btn common-course-btn btn-secondary" data-tab="profile-step">Close</button>
                    <button  type="submit" className="btn common-course-btn btn-primary" data-tab="profile-step">Update</button>
                </div>
            </form>
        </div>
        )
    }
}