import React from 'react';
import './App.css';
import * as microsoftTeams from "@microsoft/teams-js";
import { userData } from '.././components/mykademy/services/common.service';
import Events from './shared_components/events'
import logo from "../assets/img/olive.png";
import HomeGettingStarted from './shared_components/dashboard/homeGettingStarted';
import { tellMeMoreData } from '../config'
import { config } from '../config';
import { NavLink } from 'react-router-dom';
var myportfolioService = require('.././components/myportfolio/services/common.service')
var mykademyApiService = require('.././components/mykademy/services/api.service')

class Tab extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      login: false,
      signup: false,
      isMykademyLoggedin: false,
      isMyportfolioLoggedin: false,
      initial_loader: false,
      is_option_choosed: false
    }
  }
  listB2bCourses = async (account_token) => {
    const headers = {
        "Authorization": "N3NuNx7INQ7B7RNe7lNNNv7BNwNcNqNcN.NtNM7ZNWNm7Q79NzNONt7J75NcNh7dNs7SNXNLNc7I7Z7-NsNWNUNwNRN.7v727lNC7JNq7-7kN_7vNxNQNON0NvNRNCNGNq7U3539636238636365313966623334613032643233383939363337323238626337jwNpjVN-jLNdNJNdN1NrjoNUNWNV7IN7N3NtNz7ZNeNe7zN5jDjMjsNnNvNBNFNrjW7CNnNjNkNyjeNEjWjnjvjWjJNYNWNFNajKNpjANENvj8Npjojxj.jTjEjCjoNnNlN_jCNpjJ"
    };
    let req_params = "orderby_item=created_date&orderby_direction=DESC&from=2021-02-25"
    await mykademyApiService.listCourseOnboarding(headers, req_params)
        .then((response) => {
            console.log("response", response)

        })
        .catch((error) => {
            console.log("error", error)

        })
}
  async componentDidMount() {
    this.listB2bCourses()
    if (userData() !== null) {
      this.setState({ isMykademyLoggedin: true })
    }
    else this.setState({ isMykademyLoggedin: false })
    if (myportfolioService.userData() !== null) {
      this.setState({ isMyportfolioLoggedin: true })
    }
    else
      this.setState({ isMyportfolioLoggedin: false })
  }

  showDashboard = () => {
    const appid = config.index.APP_ID;
    const entityId = 'index'
    const entityLabel = 'index';
    const baseUrl = config.index.APP_BASEURL;
    microsoftTeams.executeDeepLink(
      `https://teams.microsoft.com/l/entity/${appid}/${entityId}?webUrl=${baseUrl}dashboard&label=${entityLabel}`
    );
    console.log('deep link : ', `https://teams.microsoft.com/l/entity/${appid}/${entityId}?webUrl=${baseUrl}dashboard&label=${entityLabel}`)
  }
  openCommonLogin() {

    this.setState({ login: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth',
      width: 576,
      height: 504,
      successCallback: async (result) => {
        console.log(result)
        this.setState({ login: false })
        if (userData() !== null) {
          this.showDashboard()
          this.setState({ isMykademyLoggedin: true })
        }
        if (myportfolioService.userData() !== null) {
          this.showDashboard()
          this.setState({ isMyportfolioLoggedin: true })
        }
      },
      failureCallback: (reason) => {
        console.log(reason)
        this.setState({ isMykademyLoggedin: false, login: false })
      }
    });
  }
  openSignUp() {
    this.setState({ signup: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth/signup',
      width: 769,
      height: 600,
      successCallback: async (result) => {
        console.log(result)
        this.setState({ signup: false })
        if (userData() !== null) {
          this.showDashboard()
          this.setState({ isMykademyLoggedin: true })
        }
        if (myportfolioService.userData() !== null) {
          this.showDashboard()
          this.setState({ isMyportfolioLoggedin: true })
        }
      },
      failureCallback: (reason) => {
        console.log(reason)
        this.setState({ isMykademyLoggedin: false, signup: false })
      }
    });
  }

  logOut() {
    localStorage.clear()
    this.setState({ isMykademyLoggedin: false })
  }
  chooseOption = (item) => {
    localStorage.setItem('selected_option', JSON.stringify(item))
    this.setState({ is_option_choosed: true })
  }
  render() {

    return (
      <div>
        { this.state.tellme_more &&
          <div style={{ overflow: 'scroll', top: '10%', display: 'block', opacity: '1' }} class="modal fade tell-me-modal" id="tellMeModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered">
              <div class="modal-content">
                <div class="modal-body">
                  <h4>A little about you</h4>
                  <p>Please let us know which of the below best describes you, then select the continue button. Don’t worry, you can easily change this.</p>
                  <div class="row">
                    <div class="col-md-6">
                      {tellMeMoreData.map((item, i) => {
                        return (
                          <div key={i} class="little-info" onMouseOver={() => this.setState({ description: item.description ?? '' })} onMouseOut={() => this.setState({ description: '' })}>
                            <div class="common-tickbox">
                              <input onClick={() => this.chooseOption(item)} type="radio" id={"check" + (i + 1)} name="occupation" />
                              <label htmlFor={"check" + (i + 1)} ></label>
                            </div>
                            {item.icon}
                            <span class="flex-1">{item.label}</span>
                          </div>
                        )
                      })}
                    </div>
                    <div class="col-md-6">
                      {!this.state.description ?
                        <div class="little-active-text before-hover">
                          <img src="assets/images/landing/question-hover.svg" alt="icons" />
                          <p>Hover over an option on the left to find out more.</p>
                        </div>
                        :
                        <div class="little-hover-text before-hover">
                          <p>{this.state.description ?? ''}</p>
                        </div>
                      }
                    </div>
                  </div>
                  <div class="modal-btn-wrap text-right">
                    <a href="javascript:void(0);" class="btn btn-secondary" data-bs-dismiss="modal" onClick={() => this.setState({ tellme_more: false })}>Cancel</a>
                    {this.state.is_option_choosed ?
                      <NavLink to="/learning-provider" style={{ color: '#1E1E1E' }} class="btn btn-countinue btn-vle-success">Continue</NavLink>
                      :
                      <a href="javascript:void(0);" class="btn btn-opacity btn-countinue" disabled="disabled" style={{ cursor: 'not-allowed' }}>Continue</a>
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>
        }
        {
          this.state.isMykademyLoggedin === false && this.state.isMyportfolioLoggedin === false ?
            <>
              <div class="intro-banner">
                <div class="container">
                  <div class="intro-logo-holder">
                    <a href="javascript:void(0);" class="intro-banner-logo">
                      <img src="assets/images/landing/olivevle-logo.svg" alt="Olive Logo" />
                    </a>
                  </div>
                  <div class="row">
                    <div class="col-xs-12 col-md-12 col-lg-6">
                      <div class="intro-welcome-wrap">
                        <h2>Welcome</h2>
                        <p>To get started, please select one of the following:</p>

                        <ul class="intro-welcome-list">
                          <li>
                            <div class="intro-info-wrap">
                              <img src="assets/images/landing/star.svg" alt="new account" />
                                  I’m new and don’t have an account
								</div>
                            <a class="btn common-course-btn btn-vle-success" href="javascript:void(0);" onClick={() => this.setState({ tellme_more: true })} style={{ color: '#1E1E1E' }}>Tell me more</a>
                          </li>
                        </ul>
                        <a onClick={() => this.openCommonLogin()} class="btn-rounded mr-10 clr-white " style={{ cursor: 'pointer' }}  >
                          Existing Member? Sign in
							<img src="assets/images/landing/key_shape.png" alt="Existing Member? Sign in " />
                        </a>
                        <NavLink to="/talk-to-sales" class="btn-rounded clr-white" style={{ cursor: 'pointer' }} >
                          Talk to sales
							<img src="assets/images/landing/comment_shape.png" alt="talk to sales" />
                        </NavLink>
                      </div>
                    </div>
                    <div class="col-xs-12 col-md-12 col-lg-6">
                      <div class="welcome-img-holder">
                        <img src="assets/images/landing/table-01.png" alt="" />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="service-banner">
                <div className="container">
                  <div className="row">
                    <div className="col-xs-12 col-md-12 col-lg-6">
                      <div className="img-wrap">
                        <img src="assets/images/landing/table-02.png" alt="" />
                      </div>
                    </div>
                    <div className="col-xs-12 col-md-12 col-lg-6">
                      <h2>Access to OLIVE’s extensive training library, fully integrated into Teams.</h2>
                      <p>A few additional sentences appear here describing the app’s course and portfolio capabilities. There will be a paragraph of text here that corresponds and elaborates on the heading which appears above. This can be listed over a few sentences, but ideally should just be the one paragraph.</p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="intro-footer">
                <div class="container">
                  <p>&copy; 2021 Olive Group. All rights reserved.</p>
                  <a href="javascript:void(0);">Privacy Policy</a>
                </div>
              </div>
            </>
            :
            <div className="bg-main">
              <HomeGettingStarted />
            </div>
        }
      </div>
    );
  }
}
export default Tab;