  customTooltips = (tooltip) => {
    console.log(tooltip,'tooltipppppppppppp')
    const _chart = document.getElementById("myChart2").getContext("2d");

    // Tooltip Element
    let tooltipEl = document.getElementById('chartjs-tooltip');
    if (!tooltipEl) {
      tooltipEl = document.createElement('div');
      tooltipEl.id = 'chartjs-tooltip';
      tooltipEl.innerHTML = '<table></table>';
      _chart.canvas.parentNode.appendChild(tooltipEl);
    }
    // Hide if no tooltip
    if (tooltip.opacity === 0) {
      tooltipEl.style.opacity = 0;
      return;
    }
    // Set caret Position
    tooltipEl.classList.remove('above', 'below', 'no-transform');
    if (tooltip.yAlign) {
      tooltipEl.classList.add(tooltip.yAlign);
    } else {
      tooltipEl.classList.add('no-transform');
    }
    function getBody(bodyItem) {
      return bodyItem.lines;
    }
    // Set Text
    if (tooltip.body) {
      console.log(tooltip.title,'sdfsdf')
      const titleLines = tooltip.title || [];
      const bodyLines = tooltip.body.map(getBody);
      let innerHtml = '<thead>';
      titleLines.forEach(function (title) {
        innerHtml += '<tr><th>' + title + '</th></tr>';
      });
      innerHtml += '</thead><tbody>';
      bodyLines.forEach(function (body, i) {
        const colors = tooltip.labelColors[i];
        let style = 'background:' + colors.backgroundColor;
        style += '; border-color:' + colors.borderColor;
        style += '; border-width: 2px';
        const span = '<span class="chartjs-tooltip-key" style="' +
          style +
          '"></span>';
        // let boxStyle = 'background:' + colors.backgroundColor;
        // boxStyle += '; width: 15px';
        // boxStyle += '; height: 15px';
        // boxStyle += '; position: absolute';
        // let boxSpan = '<span class="chartjs-tooltip-box" style="' +
        //   boxStyle +
        //   '"></span>';
        let boxCountStyle = 'margin-left:4px';
        let boxSpanCount = '<span class="chartjs-tooltip-box" style="' +
          boxCountStyle +
          '">' + body + '</span>';
        innerHtml += '<tr><td>' + span   +'total attendees :'+ boxSpanCount + '</td></tr>';
      });
      innerHtml += '</tbody>';
      const tableRoot = tooltipEl.querySelector('table');
      tableRoot.innerHTML = innerHtml;
    }
    const positionY = _chart.canvas.offsetTop;
    const positionX = _chart.canvas.offsetLeft;
    // Display, position, and set styles for font
    tooltipEl.style.opacity = 1;
    tooltipEl.style.left = positionX + tooltip.caretX - 100 + 'px';
    tooltipEl.style.top = positionY + tooltip.caretY - 20 + 'px';
    tooltipEl.style.fontFamily = tooltip._bodyFontFamily;
    tooltipEl.style.fontSize = tooltip.bodyFontSize + 'px';
    tooltipEl.style.fontStyle = tooltip._bodyFontStyle;
    tooltipEl.style.padding = tooltip.yPadding +
      'px ' +
      tooltip.xPadding +
      'px';
  };
