import React from "react";
import { userData, getAuthtoken } from "../../services/common.service";
import * as moment from 'moment'

var Chart = require('chart.js');
const api_service = require("../../services/api.service");

export default class VcChart extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            user_token: '',
            isApiLoading: true
        }
    }
    componentDidMount = () => {
        if (userData() !== null) {

            this.setState({ user_token: userData().data._token },
                () => {
                    this.getVcBarChartData();
                })
        }
    }
    utcToLocal = (utc) => {
        var localTime = moment.utc(utc).toDate();

        return moment(localTime).format('MMM Do YYYY h:mm a')
    }
    async getVcBarChartData() {
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(this.state.user_token))
        formData.append('type', 'MSTeams')
        formData.append('per_page', '6')
        formData.append('current_page', 1)

        const headers = {
            'Content-Type': 'multipart/form-data'
        }
        let portfolio_authtoken = getAuthtoken()
        if (portfolio_authtoken !== null) {
            headers['Authorization'] = 'Bearer ' + portfolio_authtoken
        }
        await api_service
            .getVcBarChartData(headers, formData)
            .then((response) => {
                this.setState({ isApiLoading: false }, () => {

                    console.log(response.data.data)
                    let event_total_attendies = [], titile = [], eventDate = [];
                    response.data.data.live_classes.sort(function (a, b) {
                        var dateA = new Date(a.event_start_time);
                        var dateB = new Date(b.event_start_time);
                        return dateA - dateB;
                    });
                    response.data.data.live_classes.map((val) => {
                        event_total_attendies.push(val.event_total_attendees)
                        titile.push(val.event_name)
                        console.log(new Date(val.event_start_time).toUTCString(), 'dates')
                        eventDate.push(this.utcToLocal(val.event_start_time))
                    })

                    var ctx = document.getElementById('myChart');
                    var option = {
                        scales: {
                            xAxes: [{
                                ticks: {
                                    callback: function (value) {
                                        if (value.length > 8) {
                                            return value.substr(0, 8) + '...';
                                        } else {
                                            return value
                                        }

                                    },
                                }
                            }],
                            yAxes: [{
                                ticks: {
                                    beginAtZero: true,
                                    callback: function(value) {if (value % 1 === 0) {return value;}}

                                }
                            }]
                        },
                        responsive: true,
                        maintainAspectRatio: false,
                        tooltips: {
                            enabled: true,
                            mode: 'label',
                            callbacks: {
                                title: function (tooltipItems, data, eventDate) {
                                    var idx = tooltipItems[0].index;
                                    return 'Title: ' + data.labels[idx];
                                },
                                footer: function (tooltipItem) {
                                    return eventDate[tooltipItem[0].index];
                                }

                            }
                        },
                    };
                    var myBarChart = new Chart(ctx, {
                        type: 'bar',
                        data: {
                            labels: titile,
                            datasets: [{
                                label: 'Attendees',
                                data: event_total_attendies,
                                backgroundColor: "#7B83EB"
                            }]
                        },
                        options: option
                    });
                })
            })
            .catch((error) => {
                console.log(error);
                this.setState({ isApiLoading: false })
            });
    }

    render() {
        return (
            <div className="chat-wrap">
                <div class="statistics-holder">
                    <div class="statistics-text-wrap">
                    <h4>Upcoming Popular Events</h4>
                    </div>

                </div>
                <div className="line-chat-height">
                    <div className="chartjs-size-monitor">
                        <div class="chartjs-size-monitor-expand">
                            <div class="">
                            </div>
                        </div>
                        <div class="chartjs-size-monitor-shrink">
                            <div class="">
                            </div>
                        </div>
                    </div>
                    {this.state.isApiLoading &&
                        <div className="step step-3 active">
                            <div className="d-flex justify-content-center align-items-center spinner-content">
                                <div
                                    style={{ marginTop: "45%" }}
                                    className="spinner-border"
                                    role="status"
                                >
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                            </div>
                        </div>
                    }
                    <canvas id="myChart" className="chartjs-render-monitor" width="470" height="250" style={{ display: 'block', width: '473px', height: '250px', background: 'white' }}></canvas>
                </div>

            </div>

        )

    }

}