    async loadEvents() {
        var all_mykademy_events = []
        var all_myportfolio_events = []
        if (commnService_mkdmy.userData() !== null) {
            this.setState({ initial_loader: true })
            let mk_events = await this.loadMykademyEvents()
            this.setState({ mykademy_events: mk_events, isMykademyLoggedin: true })
        }
        else {
            this.setState({ isMykademyLoggedin: false })
        }
        if (commnService_mpflio.userData() !== null) {
            this.setState({ initial_loader: true })
            let mp_events = await this.loadMyportfolioEvents()
            this.setState({ myportfolio_events: mp_events, isMyportfolioLoggedin: true })
        }
        else {
            this.setState({ isMyportfolioLoggedin: false })
        }
        if (this.state.mykademy_events.custom_events) {
            all_mykademy_events = [
                ...this.state.mykademy_events.assignments,
                ...this.state.mykademy_events.custom_events,
                ...this.state.mykademy_events.live_classes
            ]
        }
        if (this.state.myportfolio_events.live_classes) {
            all_myportfolio_events = this.state.myportfolio_events.live_classes
        }
        var arr = []
        if (all_mykademy_events.length !== 0) {
            all_mykademy_events.map((event) => {
                event['from'] = 'mykademy'
                return event
            })
            let myKademyUpCommingEvents = commonService.getUpcomingEvents(all_mykademy_events);
            if (myKademyUpCommingEvents.length !== 0) {
                this.setState({ events: [...this.state.events, ...myKademyUpCommingEvents] })
            }
            arr = [...all_mykademy_events, ...arr]
        }
        if (all_myportfolio_events.length !== 0) {
            all_myportfolio_events.map((event) => {
                event['from'] = 'myportfolio'
                return event
            })
            let myportifolioUpCommingEvents = commonService.getUpcomingEvents(all_myportfolio_events);
            if (myportifolioUpCommingEvents.length !== 0) {
                this.setState({ events: [...this.state.events, ...myportifolioUpCommingEvents] })
            }
            arr = [...all_myportfolio_events, ...arr]
        }
        console.log(arr, 'arrrrrr')
        let filtered_array = []
        filtered_array = arr.filter((event) => {
            event['title'] = event.event_name
            event['start'] = moment.utc(event.event_time).local().format();
            event['textColor'] = '#46465a'
            event['event_id'] = event.id
            event['classNames'] = ['fc-common', 'fc-blue']
            // live class condition
            // for myportfolio event type must be MSTeams//
            // for mykademy event type should be live_class or integration = microsoft_teams
            // if (event.event_type !== "live_class" && event.event_type !== "MSTeams") {
            //     event['classNames'] = ['fc-common', 'fc-blue']
            //     return event
            // } else if (event.event_type === "MSTeams") {
            //     event['classNames'] = ['fc-common', 'fc-red']
            //     return event
            // } else if (event.integration) {
            //     if (event.event_type === "live_class" && event.integration === "microsoft_teams") {
            //         event['classNames'] = ['fc-common', 'fc-red']
            return event
            //     }
            // }
        })
        console.log(filtered_array, 'arrrrrr')
        this.setState({ calendarEvents: filtered_array })
        this.setState({ initial_loader: false })
    }
    async loadMykademyEvents() {
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': appToken()
        }
        let user = commnService_mkdmy.userData()
        if (user !== null) {
            headers['Access-token'] = user.access_token
        }
        return await mykademyApiService.getAllEvents(headers)
            .then((response) => {
                if (response.data.length !== 0)
                    return response.data.data
                else
                    return []
            })
            .catch((error) => {
                return []
            })
    }

    async loadMyportfolioEvents() {
        var portfolio_access_token = commnService_mpflio.userData().data._token
        var portfolio_auth_token = commnService_mpflio.getAuthtoken()
        const headers = {}
        headers["Content-Type"] = "multipart/form-data";
        headers["Authorization"] = "Bearer " + portfolio_auth_token;
        let formData = new FormData();
        formData.append("_token", decodeURIComponent(portfolio_access_token));
        return await portfolioApiService.getAllEvents(formData, headers)
            .then((response) => {
                console.log(response)
                if (response.data.length !== 0)
                    return response.data.data
                else
                    return []
            })
            .catch((error) => {
                return []
            })
    }

    async getAttendessMykademy(id) {
        this.setState({ attendiesApiLoader: true })
        let data = {
            event_id: id,
            event_type: "live_class",
            integration: "microsoft_teams",
        };
        const headers = {
            "Content-Type": "application/json",
            Authorization: appToken(),
        };
        let user = commnService_mkdmy.userData()
        if (user !== null) {
            headers['Access-token'] = user.access_token
        }
        await mykademyApiService
            .getAttendeeList_liveclass(headers, data)
            .then((data) => {
                data.data.data.attendees.map((item) => {
                    item['name'] = item.ll_learner_name
                    item['image_url'] = item.ll_learner_image_url
                    return item
                })
                this.setState({ attendees: data.data.data.attendees, attendiesApiLoader: false })
            })
            .catch((err) => {
                this.setState({ attendiesApiLoader: false })
                console.log(err);
            });
    }
    async getAttendessMyportfolio(id) {
        this.setState({ attendiesApiLoader: true })

        var portfolio_access_token = commnService_mpflio.userData().data._token
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(portfolio_access_token))
        formData.append('current_page', 1)
        formData.append('per_page', 80)
        formData.append('eventid', id)
        var portfolio_auth_token = commnService_mpflio.getAuthtoken()
        const headers = {}
        headers["Authorization"] = "Bearer " + portfolio_auth_token;
        console.log(headers)
        for (var pair of formData.entries()) {
            console.log(pair[0] + " - " + pair[1]);
        }
        await portfolioApiService
            .getAttendeeList_liveclass_data(headers, formData)
            .then((data) => {
                if (data.data.status === 200) {
                    this.setState({ attendees: data.data.data.event_attendees, attendiesApiLoader: false })
                }
            })
            .catch((err) => {
                this.setState({ attendiesApiLoader: false })

                console.log(err);
            });
    }
