    getDate = (dayString) => {
        const today = new Date();
        const year = today.getFullYear().toString();
        let month = (today.getMonth() + 1).toString();

        if (month.length === 1) {
            month = "0" + month;
        }
        console.log(dayString.replace("YEAR", year).replace("MONTH", month))
        return dayString.replace("YEAR", year).replace("MONTH", month);
    }
    handleEventClick = async (info) => {
        this.setState({ show_detail: !this.state.show_detail, attendees: [] })
        this.setState({ show_detail_data: info.event.extendedProps })
        console.log(info.event);
        console.log(info.event.extendedProps)
        if (info.event.extendedProps['from'] === 'mykademy') {
            if (info.event.extendedProps['integration'])
                if (info.event.extendedProps['event_type'] === 'live_class' && info.event.extendedProps['integration'] === "microsoft_teams") {
                    await this.getAttendessMykademy(info.event.extendedProps['event_id'])
                }
        }
        if (info.event.extendedProps['from'] === 'myportfolio') {
            if (info.event.extendedProps['event_type'] === 'MSTeams') {
                await this.getAttendessMyportfolio(info.event.extendedProps['event_id'])
            }
        }
        // info.jsEvent.preventDefault(); // don't let the browser navigate

        // if (info.event.url) {
        //     window.open(info.event.url);
        // }
    }
