    handleDateSelect = (selectInfo) => {
        let title = prompt('Please enter a new title for your event')
        let calendarApi = selectInfo.view.calendar

        calendarApi.unselect() // clear date selection

        if (title) {
            calendarApi.addEvent({
                id: new Date(),
                title,
                start: selectInfo.startStr,
                end: selectInfo.endStr,
                allDay: selectInfo.allDay
            })
        }
    }
    joinButton = (event) => {
        if (event.event_type === "MSTeams") {
            console.log(event)
            return < a className="btn btn-primary join" href={event.event_link} style={{ color: 'white' }}> Join </a>
        } else if (event.integration) {
            if (event.event_type === "live_class" && event.integration === "microsoft_teams") {
                return < a className="btn btn-primary join" href={event.event_link} style={{ color: 'white' }}> Join </a>
            } else {
                return this.manageDeepLink(event)
            }
        } else {
            return this.manageDeepLink(event)

        }
    }
    manageDeepLink = (event) => {
        if (event.from === 'myportfolio')
            return < a className="btn btn-primary join myportfolio-link" onClick={() => this.deepLinkPrgm(event)} href={void (0)} style={{ color: 'white' }}> Join</a>
        if (event.from === 'mykademy')
            return < a className="btn btn-primary join mykademy-link" onClick={() => this.deepLinkCourse(event)} href={void (0)} style={{ color: 'white' }}> Join</a>
    }
    deepLinkCourse = (event) => {
        const mykademy_app_id = config.mykademy.APP_ID;
        const mykademy_baseUrl = config.mykademy.APP_BASEURL;
        const mykademy_entityId = "mycourses";
        const mykademy_entityLabel = "mycourses";
        console.log(event, 'event')
        if (event.event_link) {
            localStorage.setItem('event_link', event.event_link + '&callback=' + window.location.origin + '/course-list')
            let url = `https://teams.microsoft.com/l/entity/${mykademy_app_id}/${mykademy_entityId}?webUrl=${mykademy_baseUrl}course-list&label=${mykademy_entityLabel}`
            microsoftTeams.executeDeepLink(url);
        }
    }
    deepLinkPrgm = (event) => {
        const portfolio_app_id = config.myportfolio.APP_ID;
        const portfolio_baseUrl = config.myportfolio.APP_BASEURL;
        const portfolio_entityId = "myprograms";
        const portfolio_entityLabel = "myprograms";
        console.log(event, 'event')
        if (event.event_link) {
            localStorage.setItem('event_link', event.event_link + '&callback=' + window.location.origin + '/programe-list')
            // var encodedContext = encodeURI(`{"subEntityId":${id}}`);
            let url = `https://teams.microsoft.com/l/entity/${portfolio_app_id}/${portfolio_entityId}?webUrl=${portfolio_baseUrl}programe-list&label=${portfolio_entityLabel}`
            microsoftTeams.executeDeepLink(url);
        }
    };
    format(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('hh:mmA')
    }
    formatDMD(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('dddd MMM DD')
    }
    formatHHMM(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('hh:mmA')
    }
    formatDate(utc) {
        var gg = moment.utc(utc).local().format();
        return moment(gg).format('DD/MM/YYYY')
    }
