import React, { Component, PureComponent } from 'react';
import { userData, appToken } from "../../mykademy/services/common.service";
import { config } from "../../../config";
import * as microsoftTeams from "@microsoft/teams-js";

const apiService = require('../../mykademy/services/api.service');
const mykademy_app_id = config.mykademy.APP_ID;
const mykademy_baseUrl = config.mykademy.APP_BASEURL;
const mykademy_entityId = "mycourses";
const mykademy_entityLabel = "mycourses";

export default class Course extends PureComponent {
    state = {
        upcomingCourses: [],
        topCourse: [],
        isLoggedIn: false
    }
    componentDidMount() {
        if (userData() !== null) {
            this.setState({ isLoggedIn: true });
            this.getCourses();
        } else this.setState({ isLoggedIn: false });
    }
    getCourses = async () => {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await apiService
            .jumpBackInCourses(headers)
            .then((response) => {
                console.log(response)
                let filterCourses = response.data.data.filter(course => course.cs_percentage !== "100");
                this.setState({
                    upcomingCourses: filterCourses.slice(1),
                    topCourse: filterCourses[0]
                })
                console.log(this.state.topCourse, 'courses dashborad')
            })
            .catch((error) => {
                console.log(error)
            });
    }
    deepLinkCourse = (id) => {
        if (id) {
            localStorage.setItem('course_id',id)
        }
        console.log('depplink',id)
        microsoftTeams.executeDeepLink(
            `https://teams.microsoft.com/l/entity/${mykademy_app_id}/${mykademy_entityId}?webUrl=${mykademy_baseUrl}course-list&label=${mykademy_entityLabel}`
        );
    }

    // deepLinkCourse = (id) => {
    //    console.log(id,'idddd')
    //     var encodedContext = encodeURI(`{"subEntityId":${id}}`);
    //     microsoftTeams.executeDeepLink(
    //       `https://teams.microsoft.com/l/entity/${mykademy_app_id}/${mykademy_entityId}?webUrl=${mykademy_baseUrl}/course-list&label=${mykademy_entityLabel}` + '&context=' + encodedContext
    //     );
    // }
    buttonText = (progress,id) => {
        if(isNaN(progress) ){
          return <button style={{ cursor: 'pointer' ,color:'white' }} class="resume-btn resumebtn" onClick={() => this.deepLinkCourse(id)}>
          Start
        </button>
        } else if (progress == ""){
          return <button style={{ cursor: 'pointer' ,color:'white' }} class="resume-btn resumebtn" onClick={() => this.deepLinkCourse(id)}>
          Start
        </button>
        } else if(parseInt(progress) === 0){
          return <button style={{ cursor: 'pointer' ,color:'white' }} class="resume-btn resumebtn" onClick={() => this.deepLinkCourse(id)}>
          Start
        </button>
        }
        else {
           return <button style={{ cursor: 'pointer',color:'white' }} class="resume-btn resumebtn" onClick={() => this.deepLinkCourse(id)}>
          Resume
        </button>
        }
        // parseInt(this.state.topProgramme?.progress ?? 0) === 0 ? 'Start' : 'Resume'
      }
    render() {
        return (
            <div class="col-lg-6">
                <div class="common-shadow-box">
                    {/* <a href="#" class="three-dots-dropdown">
                        <img src="assets/images/landing/three-dots.svg" alt="dot" />
                    </a> */}

                    <div class="welcome-text-wrap">
                        <h4>Courses</h4>
                        <p>Your recent courses are displayed below</p>
                    </div>
                    {
                        this.state.isLoggedIn ?

                            <>
                                {this.state.topCourse ? (
                                    <>
                                        <div class="common-media common-media-box">
                                            <a style={{ cursor: 'unset' }} class="img-wrap details-img-wrap">
                                                <img src={this.state.topCourse?.cb_image} alt="courses" class="img-rounded flex-shrink-0" />
                                            </a>
                                            <div class="common-list-wrap">
                                                <span class="common-list-text">Jump back in</span>
                                                <h4>{this.state.topCourse?.cb_title}</h4>
                                                <div class="common-media-foo">
                                                    {this.buttonText(this.state.topCourse?.cs_percentage,this.state.topCourse?.course_id)}
                                                    {/* <a onClick={() => this.deepLinkCourse(this.state.topCourse?.course_id)} style={{ cursor: 'pointer' }} class="resume-btn resumebtn ">{parseInt(this.state.topCourse?.cs_percentage) === 0 ? 'Start' : 'Resume'}</a> */}
                                                    <div class="progress-wrap">
                                                        <span class="progress-completed">Progress: {parseInt(this.state.topCourse?.cs_percentage) === 0 ? '0' : this.state.topCourse?.cs_percentage}%</span>
                                                        <div class="progress common-progress">
                                                            <div class="progress-bar progress-bar-blue" role="progressbar" style={{ width: this.state.topCourse?.cs_percentage + "%", ariaValuenow: '25', ariaValuemin: "0", ariaValuemax: "100" }}></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div class="table-responsive sessions-table-list">
                                            <table class="table">
                                                <thead>
                                                    <tr>
                                                        <th scope="col">Name</th>
                                                        <th scope="col">Progress</th>
                                                        <th scope="col"></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.upcomingCourses.map((val, i) => {
                                                        return (
                                                            <tr key={i}>
                                                                <td scope="row">
                                                                    <div class="info-wrap">
                                                                        <div class="svg-wrap">
                                                                            <svg width="12" height="12" viewBox="0 0 12 12" xmlns="http://www.w3.org/2000/svg">
                                                                                <path d="M8.5 6C8.5 6.18008 8.40317 6.34624 8.24651 6.43502L5.61976 7.92351C5.11978 8.20683 4.5 7.84566 4.5 7.27099V4.72907C4.5 4.1544 5.11977 3.79323 5.61975 4.07654L8.2465 5.56499C8.40317 5.65377 8.5 5.81993 8.5 6ZM6 0C2.68629 0 0 2.68629 0 6C0 9.31371 2.68629 12 6 12C9.31371 12 12 9.31371 12 6C12 2.68629 9.31371 0 6 0ZM1 6C1 3.23858 3.23858 1 6 1C8.76142 1 11 3.23858 11 6C11 8.76142 8.76142 11 6 11C3.23858 11 1 8.76142 1 6Z" />
                                                                            </svg>
                                                                        </div>
                                                                        {val.cb_title}
                                                                    </div>
                                                                </td>
                                                                <td><div class="info-wrap">{val?.cs_percentage ? val?.cs_percentage : ''} %</div></td>
                                                                <td className="dashbordtag">
                                                                    <a onClick={() => this.deepLinkCourse(val?.course_id)} class="view-btn">{parseInt(val?.cs_percentage) === 0 ? 'Start' : 'Resume'}</a>
                                                                </td>
                                                            </tr>

                                                        )
                                                    })
                                                    }</tbody>
                                            </table>
                                        </div>

                                    </>
                                ) : (
                                        <div class="nothing-block">
                                            <img src="assets/images/landing/courses-img.png" alt="Courses" />
                                            <p>At the moment, you don’t have any enrolments.</p>
                                        </div>

                                    )}
                                <span className="dashbordtag" onClick={() => this.deepLinkCourse(null)}>
                                    <a class="view-table-link ">
                                        View All Courses
                        </a>
                                </span>
                            </> : <div class="nothing-block">
                                <img src="assets/images/landing/courses-img.png" alt="Portfolio" />
                                <p>You are not Authorized in Mykademy.</p>
                            </div>
                    }
                </div>
            </div>

        )
    }
}