import React from 'react';
import { userData } from '../services/common.service';
import NotFound from "../../shared_components/notFound";
import * as microsoftTeams from "@microsoft/teams-js";

const apiService = require('../services/api.service')
let arrowstyle = {
    marginRight: '8px',
    fontSize: '14px',
    color: '#9b9b9b'
}
export default class CourseDetail extends React.Component {

    state = {
        isLoggedIn: false,
        courseDetailUrl: ''
    }

    async componentDidMount() {
        localStorage.removeItem('course_id')
        if (userData() !== null) {
            let course_id = this.props.match.params.course_id
            if (course_id === 'event') {
                let event_link = localStorage.getItem('event_link')
                if (event_link !== null) {
                    this.setState({ courseDetailUrl: event_link })
                    localStorage.removeItem('event_link')
                } else {
                    console.log('no event_link')
                    window.location.href = "/course-list"
                }
            }
            else if (course_id) {
                let user = userData();
                let courseDetailUrl = await apiService.getcourseDetailsUrl(user.access_token, course_id)

                // this.setState({ courseDetailUrl: courseDetailUrl + '&callback=' + window.location.origin + '/course-list' })

                this.setState({ courseDetailUrl: courseDetailUrl + '&hide_exit=1' })
            }
            this.setState({ isLoggedIn: true })
        }
        else {
            this.setState({ isLoggedIn: false })
        }
    }

    gotResponse(val) {
        if (val === 'success')
            this.componentDidMount();
    }

    render() {
        return (

            <div>

                {!this.state.isLoggedIn ?

                    <NotFound domain="mykademy" response={(val) => this.gotResponse(val)} message="Sorry, you need to login to MyKademy LMS to access your courses" />
                    :
                    <React.Fragment>
                        <div className="filter-bar-block">

                            <a href={window.location.origin + '/course-list'}>
                                <span style={arrowstyle}>

                                    <i class="fas fa-chevron-left"></i>
                                </span>back</a>
                            <div className="filter-bar-holder">

                            </div>
                        </div>
                        <div className="container-fluid" style={{ paddingTop: '60px' }}>
                            <iframe
                                src={this.state.courseDetailUrl}
                                title="description"
                                style={{ width: '100%', height: '91vh', borderWidth: '0px' }}
                            ></iframe>

                        </div>

                    </React.Fragment>
                }
            </div>
        )
    }
}
