import React, {Component} from 'react'
import { Notification } from '../../../../services/common.service';
import { appToken, userData } from "../../../mykademy/services/common.service"
import {portfolioUser,getAuthtoken} from "../../../myportfolio/services/common.service"
const contrydata = require('../../../../services/countries_with_phone');
const mykademyApiservice = require('../../../mykademy/services/api.service')
const myportfolioApiservice = require('../../../myportfolio/services/api.service')
let error_style = {
    marginBottom: '15px',
    paddingLeft: '13px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: '1.33',
    letterSpacing: 'normal',
    textAlign: 'left',
    color: '#c4314b',
}
let inputErr = {
    border: ' solid 1px #c4314b',
    marginBottom: '5px',
    background: 'none',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'right calc(.375em + .1875rem) center',
    backgroundSize: 'calc(.75em + .375rem) calc(.75em + .375rem)'
}
 export default class EditVleProfile extends Component{

    constructor(props) {
		super(props)
		this.state = {
            fields: {
                fname: this.props.fname,
                lname: this.props.lname,
                email: this.props.email,
                image_url: this.props.image_url,
                image_url64:this.props.image_url64,
                country_code: this.props.country_code,
                mobile_number: this.props.phone,
                country:this.props.country
            },
            errors: {},
            basic_errors: {},
            countries: contrydata.contries_with_phone_code,
            base64Img: null,
            mkupdateddata:{},
            mpupdateddata:{}

        }
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);
    }
    handleValidation() {
        let fields = this.state.fields;
        let basic_errors = {};
        let formIsValid = true;

        //Name
        if (!fields["fname"]) {
            formIsValid = false;
            basic_errors["fname"] = "First Name cannot  be empty";
        }
        //lname
        if (!fields["lname"]) {
            formIsValid = false;
            basic_errors["lname"] = "Last Name cannot  be empty";
        }
        //mobile_number
        if (!fields["mobile_number"]) {
            formIsValid = false;
            basic_errors["mobile_number"] = "Phone Number cannot  be empty";
        }
        if (isNaN(fields["mobile_number"])) {
            formIsValid = false;
            basic_errors["mobile_number"] = "Enter a valid Phone Number";
        }
         if (!fields["mobile_number"].match(/^[0-9]{4,12}$/)) {
            formIsValid = false;
            basic_errors["mobile_number"] = "Enter a valid Phone Number"
        }

        //Email
        if (!fields["email"]) {
            formIsValid = false;
            basic_errors["email"] = "cannot be empty";
        }
        if (typeof fields["email"] !== "undefined") {
            let lastAtPos = fields["email"].lastIndexOf('@');
            let lastDotPos = fields["email"].lastIndexOf('.');

            if (!(lastAtPos < lastDotPos && lastAtPos > 0 && fields["email"].indexOf('@@') == -1 && lastDotPos > 2 && (fields["email"].length - lastDotPos) > 2)) {
                formIsValid = false;
                basic_errors["email"] = "Email is not valid";
            }
        }
        //country_code
        if (!fields["country_code"]) {
            formIsValid = false;
            basic_errors["country_code"] = "Country Name cannot be empty";
        }

        this.setState({ basic_errors: basic_errors });
        return formIsValid;
    }
    handleChange(field, e) {
        let fields = this.state.fields;
        let basic_errors = this.state.basic_errors

        console.log('hereee',field,e.target.value)
        if (field === "country_code") {
            const dial_code = e.target.value;
            const seletedCountry = this.state.countries.find(
              (u) => u.dial_code === dial_code
            );
            console.log(seletedCountry,'get counrty')
                fields['country'] = seletedCountry.code
                fields['country_code'] = seletedCountry.dial_code
            
          }
        basic_errors[field] = ""
        
        fields[field] = e.target.value;

        this.setState({ fields, basic_errors });
    }
    close = () => {
        this.props.updateState({ show_vle_profile: true })
        this.props.updateState({ open_edit_vlepersonal_section: false })
    }
    update = async(e)=>{
        e.preventDefault()
        const isValid = this.handleValidation(true);
        let mkupdateddata =false
        let mpupdateddata =false
        if(isValid){
                this.setState({ apiLoader: true })

             mkupdateddata= await this.mkProfileupdate()
             mpupdateddata = await this.mpProfileupdate()
            if(mkupdateddata && mpupdateddata){
                this.stateUpdate()
                this.mpupLocalstorageUpdate()
                this.mkupLocalstorageUpdate()


            }else if(mkupdateddata){
                this.stateUpdate()
                this.mkupLocalstorageUpdate()


            } else if(mpupdateddata){
                this.stateUpdate()
                this.mpupLocalstorageUpdate()


            }else{
            Notification("not updated!", "warning", "Warning")

            }

            console.log(this.state.fields,'get fieldssss')

        }
        else {
            Notification("Please fill required fields!", "warning", "Warning")
        }
        

    }
    mpProfileupdate = async()=>{
        
        let formData = new FormData()
        formData.append('first_name', this.state.fields.fname)
        formData.append('last_name', this.state.fields.lname)
        formData.append('email', this.state.fields.email)
        formData.append("country_code", this.state.fields.country_code)
        formData.append("mobile_number", this.state.fields.mobile_number)
        formData.append("_token", decodeURIComponent(portfolioUser().data._token));
        if(this.state.base64Img !==null){

            formData.append("image", this.state.fields.image_url64);
        }
        const headers = {};
        let portfolio_authtoken = getAuthtoken();
        if (portfolio_authtoken !== null) {
            headers["Authorization"] = "Bearer " + portfolio_authtoken;
        }
        return await myportfolioApiservice.updatePersonalData(headers, formData)
        .then((response) => {
            this.setState({ apiLoader: false })
            console.log(response)
            if (response.status === 200) {
                if (response.data.status === "error") {
                    Notification(response?.data?.message, "danger", "Warning")
                    return false
                }else{
                    let fields = this.state.fields
                    this.setState({mpupdateddata:response.data.data})
                    fields['image_url64'] = response.data.data.image_url;

                    return true
                }
              
            }
        })
        .catch((e) => {
            this.setState({ apiLoader: false })
            console.log(e)
        })
    }
    mkProfileupdate = async()=>{
        // this.setState({ apiLoader: true })

        let formData = new FormData()
        formData.append('name', this.state.fields.fname+ " " + this.state.fields.lname)
        formData.append('email', this.state.fields.email)
        formData.append('phone', this.state.fields.mobile_number)
        formData.append("us_country_code", this.state.fields.country_code)
        formData.append("us_country", this.state.fields.country)
        formData.append("file", this.state.fields.image_url);

        const headers = {
            Authorization: appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
       return await mykademyApiservice
            .updateProfile(headers, formData)
            .then((response) => {
                // this.setState({ apiLoader: false })

                if (response.status === 200) {
                    if (response.data.metadata.error === 1) {
                        Notification(response.data.metadata.message, "warning", "Warning");
                        return false
                    }
                    // if(response.data.metadata.status_code === 400){
                    // Notification('Image Format is not support', "warning", "Warning");
                    //     return
                    // }

                    console.log(response.data.data.user.us_image, 'response')
                    let fields = this.state.fields;
                    fields['image_url'] = response.data.data.user.us_image;
                    this.setState({mkupdateddata:response.data.data})
                    return true
                    // this.setState({ fields }, () => this.setremainingdatas(response.data.data.token));




                }
            })
            .catch((err) => {
                console.log(err)
            })
    }
    stateUpdate = ()=>{
        this.setState({ apiLoader: false })

        this.props.updateState({ image_url: this.state.fields.image_url })
        this.props.updateState({ image_url64: this.state.fields.image_url64 })

        this.props.updateState({ show_vle_profile: true })
        this.props.updateState({ open_edit_vlepersonal_section: false })

        this.props.updateState({ fname: this.state.fields.fname })
        this.props.updateState({ lname: this.state.fields.lname })

        this.props.updateState({ phone: this.state.fields.mobile_number })
        this.props.updateState({ email: this.state.fields.email })

        this.props.updateState({ country_code: this.state.fields.country_code })

        this.props.updateState({ country: this.state.fields.country })
        Notification("Personal data updated successfully", "success", "Success");
    }
    mkupLocalstorageUpdate = ()=>{
        let user = userData()
        if (user !== null) {
            console.log(this.state.fields)

            user.user_data['us_name'] = this.state.fields.fname + " "+this.state.fields.lname
            // user.user_data['country_code'] = this.state.fields.lname
            user.user_data['us_country_code'] = this.state.fields.country_code
            user.user_data['us_country'] = this.state.fields.country
            user.user_data['us_email'] = this.state.fields.email
            user.user_data['us_phone'] = this.state.fields.mobile_number
            user.user_data['us_image'] = this.state.fields.image_url

            user.access_token = this.state.mkupdateddata.token
            localStorage.setItem('mykademy_user', JSON.stringify(user))


        }
    }
    mpupLocalstorageUpdate = ()=>{
        let user = portfolioUser()
        if (user !== null) {
          
            user.data.member_details['image_url'] = this.state.fields.image_url64

            user.data.member_details['first_name'] = this.state.fields.fname
            user.data.member_details['last_name'] = this.state.fields.lname
            user.data.member_details['email'] = this.state.fields.email
            user.data.member_details['country_code'] = this.state.fields.country_code
            user.data.member_details['mobile_number'] = this.state.fields.mobile_number
            localStorage.setItem('portfolio_user', JSON.stringify(user))
        }

    }
    showFileUpload() {
        this.fileUpload.current.click()
    }
    onChange = e => {
        e.preventDefault();
        var extension = e.target.files[0].name.split('.').pop().toLowerCase()
        if (extension) {
            
            let fields = this.state.fields;
            fields['image_url']= e.target.files[0]
            this.setState({ supported: true })
            var reader = new FileReader();
            reader.readAsDataURL(e.target.files[0]);

            reader.onload = () => {
                let base64Data = reader.result.split(',')
                console.log(base64Data)
            fields['image_url64'] = base64Data[1];
            this.setState({ base64Img: reader.result,fields:fields })
            };
            reader.onerror = (error) => {
                Notification("Error in Image upload!", "warning", "Warning")
                console.log('Error: ', error);
            };
        }
        else {
            Notification("File format not Supported!", "warning", "Warning")
            return
    }
    }
    componentDidMount (){
        console.log(this.state.fields)
    }

    render(){
        return(
            <div id="personalstep" class="step active">
            {this.state.apiLoader &&
                <div class="loading">foregroung loader&#8230;</div>
            }
            <div class="profile-header">
                <div class="img-wrap">
                    <img src={this.state.base64Img !== null ? this.state.base64Img : this.state?.fields?.image_url} alt="" />
                    <i onClick={this.showFileUpload} style={{ cursor: 'pointer' }} class=" editpencil_class fas fa-pencil-alt"></i>

                    <input onChange={e => this.onChange(e)} type="file" accept=".png, .jpg, .jpeg" id="my_file" style={{ display: "none" }} ref={this.fileUpload} />
                </div>
                <h4>Profile</h4>
            </div>

            <div class="profile-update">
                <span>Personal</span>
            </div>
            <form class="profile-form profile-edit" action="#" style={{ maxHeight: '59vh', overflow: 'scroll' }}>
                <div class="input-wrap">
                    <div class="form-group">
                        <label for="fname">First Name</label>
                        <input value={this.state?.fields?.fname} type="text" onChange={this.handleChange.bind(this, "fname")} class="form-control" style={this.state.basic_errors['fname'] ? inputErr : {}} name="fname" placeholder="First Name" required />
                        {this.state.basic_errors['fname'] &&
                            <p style={error_style}>{this.state.basic_errors['fname']}</p>
                        }
                    </div>
                    <div class="form-group">
                        <label for="lname">Last Name</label>
                        <input value={this.state?.fields?.lname} type="text" onChange={this.handleChange.bind(this, "lname")} class="form-control" style={this.state.basic_errors['lname'] ? inputErr : {}} name="lname" placeholder="Last Name" required />
                        {this.state.basic_errors['lname'] &&
                            <p style={error_style}>{this.state.basic_errors['lname']}</p>
                        }
                    </div>

                    <div class="form-group">
                        <label for="">Email</label>
                        <input value={this.state?.fields?.email} type="email" onChange={this.handleChange.bind(this, "email")} class="form-control" style={this.state.basic_errors['email'] ? inputErr : {}} name="email" placeholder="Email" required />
                        {this.state.basic_errors['email'] &&
                            <p style={error_style}>{this.state.basic_errors['email']}</p>
                        }
                    </div>
                    <div class="form-group">
                        <label for="">Phone</label>
                        <input value={this.state?.fields?.mobile_number} maxlength="15" type="text" onChange={this.handleChange.bind(this, "mobile_number")} class="form-control" style={this.state.basic_errors['mobile_number'] ? inputErr : {}} name="mobile_number" placeholder="Phone number" required />
                        {this.state.basic_errors['mobile_number'] &&
                            <p style={error_style}>{this.state.basic_errors['mobile_number']}</p>
                        }
                    </div>
                    <div className="form-group">
                        <label >Country Name</label>
                        <select
                            style={this.state.basic_errors['country_code'] ? inputErr : {}}
                            name="country_code"
                            onChange={this.handleChange.bind(this, "country_code")}
                            className="form-control"
                            value={this.state.fields.country_code}
                        >
                            <option value="">Choose country...</option>
                            {this.state.countries.map((country, i) => (
                                <option value={country.dial_code}>
                                    {country.name + " (" + country.dial_code + ")"}
                                </option>
                            ))}
                        </select>

                        <p style={error_style}>{this.state.basic_errors.country_code}</p>
                    </div>
                    {/* {this.state.custom_fields_data.map((item, i) => {
                        return <div class="form-group">
                            <label for={item.name}>{item.name}</label>
                            <input value={item.value} type="text" onChange={(e) => this.handleDynamicFieldChange(e, item, i)} class="form-control" style={this.state.errors[item.name] ? inputErr : {}} name={item.name} required={item.required ? true : false} />
                            {this.state.errors[item.name] &&
                                <p style={error_style}>{this.state.errors[item.name]}</p>
                            }
                        </div>
                    })} */}
                </div>

                <div class="profile-modal-foo">
                    <button onClick={this.close} type="button" class="btn common-course-btn btn-secondary" data-tab="profile-step">Close</button>
                    <button onClick={this.update} type="button" class="btn common-course-btn btn-primary" data-tab="profile-step">Update</button>
                </div>
            </form>
        </div>   
        )
                }

 }