import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
// import './custom_assets/js/bootstrap5-beta3.bundle.min.js'
import { NavLink, useLocation } from "react-router-dom";

import App from './components/App';
import { Provider, RetryIcon, themes } from '@fluentui/react-northstar' //https://fluentsite.z22.web.core.windows.net/quick-start
import Zendesk from "react-zendesk";

const ZENDESK_KEY = process.env.REACT_APP_ZENDESK_KEY;
const setting = {
    color: {
        theme: "#000"
    },
    launcher: {
        chatLabel: {
            "en-US": ""
        }
    },
    contactForm: {
        fields: [
            // { id: "description", prefill: { "*": "My pre-filled description" } }
        ]
    },
    webWidget: {
        chat: {
            departments: {
                enabled: []
            }
        }
    }
};

const loadZendesk = () => {
    const paths = ['/auth', '/auth/signup','/mykademy-admin/admin-course-management'];
    if (!paths.includes(window.location.pathname)) {
        return <Zendesk defer zendeskKey={ZENDESK_KEY} {...setting} onLoaded={() => console.log('is loaded')} />
    }
}

ReactDOM.render(
    <Provider theme={themes.teams}>
        <App />
        {loadZendesk()}
    </Provider>, document.getElementById('root')
);
