  componentDidMount() {

  }
  /**
   * myportfolio authentication start
   */
  openPortfolioLogin() {
    let that = this
    this.setState({ login: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth-portfolio',
      width: 500,
      height: 504,
      successCallback: function (result) {
        that.setState({ login: false })
        that.props.response('success')
      },
      failureCallback: function (reason) {
        that.setState({ login: false })
      }
    });
  }
  /**
  * myportfolio authentication end
  */

  /**
   * mykademy authentication start
   */
  openMykademyLogin() {
    let that = this
    this.setState({ login: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth-mykademy',
      width: 500,
      height: 504,
      successCallback: function (result) {
        that.setState({ login: false })
        that.props.response('success')

      },
      failureCallback: function (reason) {
        that.setState({ login: false })
      }
    });
  }
