import React from "react";
import { userData, getAuthtoken } from "../../services/common.service";
import * as moment from "moment";

var Chart = require('chart.js');
const api_service = require("../../services/api.service");

export default class VcLineChart extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            user_token: '',
            isApiLoading: true
        }
    }
    componentDidMount() {
        if (userData() !== null) {

            this.setState({ user_token: userData().data._token },
                () => {
                    this.getVcLineChartData();
                })
        }

    }
    async getVcLineChartData() {
        let fromDate = moment(new Date()).format("YYYY-MM-DD");
        let todate = moment(fromDate, "YYYY-MM-DD").add(6, 'days').format("YYYY-MM-DD")
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(this.state.user_token))
        formData.append('type', 'MSTeams')
        formData.append('fromdate', fromDate)
        formData.append('todate', todate)

        const headers = {
            'Content-Type': 'multipart/form-data'
        }
        let portfolio_authtoken = getAuthtoken()
        if (portfolio_authtoken !== null) {
            headers['Authorization'] = 'Bearer ' + portfolio_authtoken
        }
        await api_service.getVcLineChartData(headers, formData)
            .then((response) => {
                this.setState({ isApiLoading: false }, () => {
                    console.log(response.data.data.eventscount, 'eventscount')
                    let ObjectData = response.data.data.eventscount;
                    let Datas = [], label = [];
                    Object.keys(ObjectData).forEach(key => {
                        console.log('key', key);
                        label.push(moment(key).format('MMM D'));

                        console.log('value', ObjectData[key]);
                        Datas.push(ObjectData[key]);

                    });

                    console.log(label, 'labelss')
                    var ctx = document.getElementById('myLineChart');
                    var myLineChart = new Chart(ctx, {
                        type: 'line',
                        data: {
                            labels: label,
                            datasets: [{
                                label: 'Number Of Live Classes', // Name the series
                                data: Datas, // Specify the data values array
                                fill: false,
                                borderColor: '#7B83EB', // Add custom color border (Line)
                                backgroundColor: '#7B83EB', // Add custom color background (Points and Fill)
                                borderWidth: 1 // Specify bar border width
                            }]
                        },
                        options: {
                            scales: {
                               
                                yAxes: [{
                                    ticks: {
                                        beginAtZero: true,
                                        callback: function(value) {if (value % 1 === 0) {return value;}}
                                    }
                                }]
                            },

                            responsive: true,
                            maintainAspectRatio: false,
                            tooltips: {
                                enabled: true,
                                mode: 'label',
                                callbacks: {
                                    title: function (tooltipItems, data) {
                                        var idx = tooltipItems[0].index;
                                        return 'Title: ' + data.labels[idx];
                                    },
                                }
                            }
                        }
                    });
                })

            })
            .catch((err) => {
                console.log(err)
                this.setState({ isApiLoading: false })
            })

    }
    render() {
        return (
            <div className="chat-wrap">
                <div className="bar-chat-height">
                    <div className="chartjs-size-monitor">
                        <div class="chartjs-size-monitor-expand">
                            <div class="">
                            </div>
                        </div>
                        <div class="chartjs-size-monitor-shrink">
                            <div class="">
                            </div>
                        </div>
                    </div>
                    {this.state.isApiLoading &&
                        <div className="step step-3 active">
                            <div className="d-flex justify-content-center align-items-center spinner-content">
                                <div
                                    style={{ marginTop: "45%" }}
                                    className="spinner-border"
                                    role="status"
                                >
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                            </div>
                        </div>
                    }
                    <canvas id="myLineChart" className="chartjs-render-monitor" style={{ display: 'block', color: '#569ff7', width: '473px', height: '300px' }}></canvas>
                </div>

            </div>

        )


    }

}