import React from 'react';
import RevoCalendar from 'revo-calendar'
import 'revo-calendar/dist/index.css'
import { userData} from "../mykademy/services/common.service";
import { portfolioUser } from '../myportfolio/services/common.service'
import NotFound  from '../shared_components/notFound'
/**
 * This component is used to display the calendar events
 */
class Calendar extends React.Component {

    state = {
        events: [],
        isLoggedIn: false,

    }
    componentDidMount() {
        console.log('calenderrrrrrrrrrrrrrrrrr')
        if (userData() !== null || portfolioUser() !== null) {
            console.log('hereeeeeeeeeeee')
            this.setState({ isLoggedIn: true})
            this.arrangeEvents(this.props.events)
        }else{

            this.setState({ isLoggedIn: false})
        }

    }
    checkIsLive(val) {
        if (val.event_live_status)
            if (val.event_live_status === '1') {

                return true
            } else {
                return false
            }
    }
    arrangeEvents(events) {
        let calender_array = []
        events.map((val, i) => {
            let data = {}
            data = {
                name: <span className="capitalize">{val.event_name}</span>,
                date: +new Date(val.event_time)
            }
            if (val.event_type === "live_class") {
                if (this.checkIsLive(val)) {
                    data.extra = {
                        // icon:
                        //     'M20.822 18.096c-3.439-.794-6.64-1.49-5.09-4.418 4.72-8.912 1.251-13.678-3.732-13.678-5.082 0-8.464 4.949-3.732 13.678 1.597 2.945-1.725 3.641-5.09           4.418-3.073.71-3.188 2.236-3.178 4.904l.004 1h23.99l.004-.969c.012-2.688-.092-4.222-3.176-4.935z',
                        text: <div>
                            <div>Duration : {val.event_duration} m</div>
                            (no description)
                            <div style={{ marginTop: '10px' }} className="button_wrapper">
                                <button style={{ fontFamily: 'revert', height: '25px', padding: '6px 14px' }} className=" btn-live live red">LIVE</button>
                                {/* <button style={{ fontFamily: 'revert', height: '25px', padding: '6px 14px' }} className="btn-event-secondary">Join</button> */}
                            </div>
                        </div>
                    }
                }
            }
            calender_array.push(data)
        })
        this.setState({ events: calender_array })
    }
    render() {
        return (
            <div>
                {
                    !this.state.isLoggedIn ? (

                        <RevoCalendar
                            events={this.state.events}
                            style={{ borderRadius: "5px", border: "5px solid var(--primaryColor)" }}
                            highlightToday={true}
                            lang="en"
                            primaryColor="#111111"
                            secondaryColor="#f7f8f8"
                            todayColor="#979593"
                            textColor="#110101"
                            indicatorColor="#eea012"
                            animationSpeed={300}
                            sidebarWidth={180}
                            detailWidth={280}
                            showDetailToggler={true}
                            showSidebarToggler={true}
                            onePanelAtATime={false}
                            allowDeleteEvent={false}
                            allowAddEvent={false}
                            openDetailsOnDateSelection={true}
                            timeFormat24={false}
                            showAllDayLabel={true}
                            detailDateFormat="DD/MM/YYYY"
                        // deleteEvent={deleteEvent}
                        // addEvent={addEvent}
                        />
                    ):(
                        <NotFound domain="mykademy" response={(val) => this.gotResponse(val)} message="Sorry, you need to login to MyKademy LMS to access your Courses" />

                    )
                }
            </div>
        );
    }
}

export default Calendar;
