import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";

export default class switchAcc extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      domain: props.match.params.domain
    }
  }
  componentDidMount() {
  }
  openLogin() {
    if (this.state.domain === 'Mykademy') {
      this.openMykademyLogin()
    }
    if (this.state.domain === 'MyPortfolio') {
      this.openPortfolioLogin()
    }
  }
  openMykademyLogin() {
    let that = this
    this.setState({ login: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth-mykademy',
      width: 500,
      height: 504,
      successCallback: function (result) {
        window.location.href = '/tab'
      },
      failureCallback: function (reason) {

      }
    });
  }
  openPortfolioLogin() {
    let that = this
    this.setState({ login: true })
    microsoftTeams.authentication.authenticate({
      url: window.location.origin + '/auth-portfolio',
      width: 500,
      height: 504,
      successCallback: function (result) {
        window.location.href = '/tab'
      },
      failureCallback: function (reason) {

      }
    });
  }
  backtoDashboard = () => {
    window.location.href = '/tab'

  }

  render() {
    return (
      <main class="form-signin" style={{ display: 'block' }}>
        <img class="mb-4" src={window.location.origin + "/assets/images/brand/olive-group-icon.svg"} alt="" height="57" />
        <h3 class="my-2 lms-text">Sorry you are not logged in to {this.state.domain} as an Administrator.
            Please click the below button to sign into {this.state.domain} as an Administrator</h3>
        <button class="btn btn-lg btn-primary sign-btn" onClick={() => this.openLogin()}>Sign in</button>

        <a class="btn btn-lg btn-dashboard" href="javascript:void(0);"
          onClick={() => this.backtoDashboard()}>
          <img src={window.location.origin + "/assets/images/home.svg"} alt="home" />
              Back to Dashboard
            </a>
      </main>
    )
  }
}