import React, { PureComponent } from 'react'
import { appToken } from "../services/common.service";

var Chart = require('chart.js');
const api_service = require("../services/api.service");

export default class DoughNut extends PureComponent {
    state = ({
        notStarted: 0,
        completed: 0,
        inProgress: 0,
        loader: true
    })
    componentDidMount = () => {
        this.doughNutData();
    }
    async doughNutData() {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await api_service
            .getDoughNutData(headers)
            .then((response) => {
                let doughtNutData = [];
                if (response?.data?.data !== null) {
                    this.setState({
                        notStarted: response.data.data.not_started,
                        completed: response.data.data.completed,
                        inProgress: response.data.data.in_progress,
                        loader: false
                    })
                    doughtNutData = [
                        this.state.completed,
                        this.state.inProgress,
                        this.state.notStarted
                    ]
                } else {
                    this.setState({
                        loader: false
                    })
                    doughtNutData = [
                        this.state.completed,
                        this.state.inProgress,
                        this.state.notStarted
                    ]
                }
                var config = {
                    type: 'doughnut',
                    data: {
                        datasets: [{
                            data: doughtNutData,
                            backgroundColor: [
                                "#13A10E",
                                "#6264A7",
                                "#D9DBDB",
                                "#C4314B",
                            ],
                            label: 'Expenditures'
                        }],
                        labels: [
                            "Passed",
                            "In Progress",
                            "Not Started"
                        ]
                    },
                    options: {
                        responsive: true,
                        legend: {
                            position: 'right',
                            labels: {
                                boxWidth: 11,
                                boxHeight: 8,
                                fontSize: 11,
                                color: '#252424'
                            }
                        },
                        title: {
                            display: false,
                            text: 'Chart.js Doughnut Chart'
                        },
                        animation: {
                            animateScale: true,
                            animateRotate: true
                        },
                        cutoutPercentage: 70,
                        tooltips: {
                            callbacks: {
                                label: function (tooltipItem, data) {
                                    var dataset = data.datasets[tooltipItem.datasetIndex];
                                    var total = dataset.data.reduce(function (previousValue, currentValue, currentIndex, array) {
                                        return previousValue + currentValue;
                                    });
                                    var currentValue = dataset.data[tooltipItem.index];
                                    var precentage = Math.floor(((currentValue / total) * 100) + 0.5);
                                    return precentage + "%";
                                }
                            }
                        }
                    }
                };

                const chat2 = document.getElementById("chart-area").getContext("2d");

                var myDoughnutChart = new Chart(chat2, config);

            })
            .catch((error) => {
                console.log(error);
                this.setState({
                    loader: false
                })
            });
    }
    render() {
        return (
            <div class="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                <div class="course-thumb-list">
                    {this.state.loader ? (
                        <div className="bg-main">
                            <div style={{ textAlign: 'center' }}>
                                <img alt="view more" style={{ width: '45px', height: '40vh' }} src="custom_assets/loader.svg" />
                            </div>
                        </div>
                    ) : (
                        <div class="detail-text-wrap">
                            <div class="course-title">
                                <h4 class="same-height">Enrolment Summary</h4>
                            </div>
                            <div class="chart-area-holder">
                                <canvas id="chart-area"></canvas>
                            </div>
                        </div>
                    )}

                </div>
            </div>
        )
    }
}
