
import React, { useState, useEffect, useContext } from "react"
import * as moment from "moment";
import sidebarContext from './sidebarContext'
import { sidebarRightUserLoader } from "../../contentLoader/vcEvent";
import { getVcToken } from "../../../services/common.service";

import img_calendar_svg from '../../../custom_assets/images/calendar.svg'
import img_video_marketing_svg from '../../../custom_assets/images/icon/video-marketing.svg'
import img_default_user from '../../../custom_assets/default_user.jpg'
import img_trash from '../../../custom_assets/trash.png'

const apiService = require("../services/api.service");
const myportfolioCommonService = require("../services/common.service");
// const imgurl = 'custom_assets/default_user.jpg'
const Adminvideoclassroom_sidebar = (props) => {
  const [attendees, setAttendees] = useState([]);
  const { event_time, event_duration, event_link, event_name, event_id } = props
  const sidebar = useContext(sidebarContext)
  const [accesstoken, setAccesstoken] = useState('');
  const [show, setShow] = useState(false);

  useEffect(() => {
    console.log(props)
    if (props.event_id !== undefined) {
      setShow(false)

      getAttendess(props.event_id)
    }

  }, [props.event_id])
  function formatDMD(utc) {
    var gg = moment.utc(utc).local().format();
    return moment(gg).format('dddd MMM DD')
  }

  function formatHHMM(utc) {
    var gg = moment.utc(utc).local().format();
    return moment(gg).format('hh:mmA')
  }

  async function getAttendess(id) {
    if (myportfolioCommonService.userData() !== null) {
      setAccesstoken(myportfolioCommonService.userData().data._token)
    }
    let formData = new FormData();
    formData.append('_token', decodeURIComponent(myportfolioCommonService.userData().data._token))
    formData.append('current_page', 1)
    formData.append('per_page', 50)
    formData.append('eventid', id)
    const headers = {
      'Content-Type': 'multipart/form-data',
    }
    let portfolio_authtoken = myportfolioCommonService.getAuthtoken()
    if (portfolio_authtoken !== null) {
      headers['Authorization'] = 'Bearer ' + portfolio_authtoken
    }
    console.log(headers)
    for (var pair of formData.entries()) {
      console.log(pair[0] + " - " + pair[1]);
    }
    await apiService
      .getAttendeeList_liveclass_data(headers, formData)
      .then((data) => {
        if (data.data.status === 200) {

          setAttendees(data.data.data.event_attendees)
          setShow(true)
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }
  function openEdit() {

    console.log(attendees, 'attendees')
    console.log(props, 'props')
    if (attendees) {
      let evnt_data = {}
      evnt_data['data'] = props
      evnt_data['attendees'] = attendees
      localStorage.setItem('mp_event_edit', props.event_id)


      if (getVcToken()) {
        let ttl = new Date(getVcToken().ttl)
        let end = ttl.setMinutes(ttl.getMinutes() + 50)
        let currentTime = new Date()
        if (new Date(currentTime) < new Date(end)) {
          sidebar.ToggleBack({})
        }
      }
      props.vc_sidebar_callback('')


    }
  }
  function imgUrl(img_url) {
    if (img_url === "" || undefined || null) {
      return <img src={img_default_user} alt="user img" />
    } else {
      return <img src={img_url} alt="usrr img" />
    }

  }
  function del(){
    props.deleteEvent(props.event_id,event_name)
  }
  return (
    <div style={{ marginTop: '63px' }} class={`event-content-sidebar ${sidebar.sidebarState ? 'active' : null}`} >
      <a href="javascript:void(0)" class="event-close-btn" onClick={() => sidebar.ToggleBack({})}>
        <i class="fa fa-close"></i>
      </a>
      <h4>{event_name}</h4>
      <img src={img_trash} alt="trash" className="trash-event-edit" onClick={() => del()} />
      <ul class="event-sidebar-list">
        <li>
          <div class="event-video-icon">
            <img src={img_calendar_svg} alt="event icon" />
          </div>
          <div class="event-info">
            <span class="event-info-title">{formatDMD(event_time)}</span>
            <span class="event-info-subtitle">{formatHHMM(event_time)}</span>
            {/* Duration:<span class="event-info-title">{event_duration}</span> */}
            {/* <span class="event-info-subtitle">{event_duration}</span> */}
          </div>
        </li>
        <li>
          <div class="event-video-icon">
            <img src={img_video_marketing_svg} alt="event icon" />
          </div>

          <div class="event-info">
            <span class="event-info-title">Url</span>
            <a class="event-info-subtitle" href={event_link} target="_blank">{event_link}</a>
          </div>
        </li>
        <li>
          <div class="event-info" style={{ display: 'flex', justifyContent: 'space-between' }}>
            <a className="btn btn-primary join" target="_blank" href={event_link}>Join</a>
            <button onClick={(e) => { openEdit() }} className="btn btn-primary edit-meeting"  >Edit</button>
          </div>
        </li>
      </ul>
      <div class="participants-block">
        <span>Participants</span>
        {
          !show ?
            // <img alt="view more" style={{ marginLeft:'40%', marginTop: '16%', marginBottom: '27%' }} src="custom_assets/loader.svg" />
            <div>{sidebarRightUserLoader()}  </div>
            :
            <ul class="event-attendees-list scrollbar-wrap">
              {attendees && attendees.map((item, index) => {
                if (index < 7)
                  return <li key={index}>
                    <a href="javascript:void(0);">
                      <div className="img-wrap">
                        {
                          imgUrl(item.image_url)
                        }
                      </div>
                      <span>{item?.name}</span>
                    </a>
                  </li>
              })}
              {attendees &&
                <li class="event-attendees-count"><a href="#">{attendees.length < 7 ? '' : `+${attendees.length - 7}`}</a></li>
              }
            </ul>
        }
      </div>
    </div>
  )
}
export default Adminvideoclassroom_sidebar