import React, { PureComponent } from 'react'
import { _ } from "lodash";
var Chart = require('chart.js');

export default class HalfDoughNut extends PureComponent {
    state = {
        progressDetails: this.props.progressDetails,
        value: ''
    }
    dataValue = (c, s, n) => {
        console.log(c, s, n)
        let completed = c;
        let started = s;
        let not_started = n;
        let array
        if (completed === 0 && started == 0 && not_started !== 0) {
            return array = [completed, not_started]
        }
        else if (completed === 0 && started !== 0 && not_started === 0) {
            return array = [completed, started]
        }
        else if (completed !== 0 && started === 0 && not_started === 0) {
            return array = [completed, not_started]
        }
        else if (completed !== 0 && started !== 0 && not_started === 0) {
            return array = [completed, started]
        }
        else if (completed !== 0 && started === 0 && not_started !== 0) {
            return array = [completed, not_started]
        }
        else if (completed === 0 && started !== 0 && not_started !== 0) {
            return array = [completed, not_started]
        }
        else if (completed !== 0 && started !== 0 && not_started !== 0) {
            return array = [completed, not_started]
        }
        else {
            return array = [1, 0]
        }

    }
    labels = (c, s, n) => {
        let completed = c;
        let started = s;
        let not_started = n;
        let array
        if (completed === 0 && started == 0 && not_started !== 0) {
            return array = ["Completed", "Not Started"]
        }
        else if (completed === 0 && started !== 0 && not_started === 0) {
            return array = ["Completed", "In Progress"]
        }
        else if (completed !== 0 && started === 0 && not_started === 0) {
            return array = ["Completed", "Not Started"]
        }
        else if (completed !== 0 && started !== 0 && not_started === 0) {
            return array = ["Completed", "In Progress"]
        }
        else if (completed !== 0 && started === 0 && not_started !== 0) {
            return array = ["Completed", "Not Started"]
        }
        else if (completed === 0 && started !== 0 && not_started !== 0) {
            return array = ["Completed", "Not Started"]
        }
        else if (completed !== 0 && started !== 0 && not_started !== 0) {
            return array = ["Completed", "Not Started"]
        }
        else {
            return array = ["Completed", "Not Started"]
        }

    }
    colorValue = (c, s, n, t) => {
        console.log(c, s, n, t, 'with total')
        let completed = c;
        let started = s;
        let not_started = n;
        let total = t;
        let colorArray;
        if (completed === 0 && started == 0 && not_started !== 0) {
            return colorArray = ["#6264A7", "#D9DBDB"]
        }
        else if (completed === 0 && started !== 0 && not_started === 0) {
            return colorArray = ["#6264A7", "#D9DBDB"]
        }
        else if (completed !== 0 && started === 0 && not_started === 0) {
            let color = (completed === total) ? "#13A10E" : "#6264A7"
            return colorArray = [color, "#D9DBDB"]
        }
        else if (completed !== 0 && started !== 0 && not_started === 0) {
            let color = (completed === total) ? "#13A10E" : "#6264A7"

            return colorArray = [color, "#D9DBDB"]
        }
        else if (completed !== 0 && started === 0 && not_started !== 0) {
            let color = (completed === total) ? "#13A10E" : "#6264A7"

            return colorArray = [color, "#D9DBDB"]
        }
        else if (completed === 0 && started !== 0 && not_started !== 0) {
            return colorArray = ["#6264A7", "#D9DBDB"]
        }
        else if (completed !== 0 && started !== 0 && not_started !== 0) {
            let color = (completed === total) ? "#13A10E" : "#6264A7"

            return colorArray = [color, "#D9DBDB"]
        } else {
            return colorArray = ["#D9DBDB", "#D9DBDB"]
        }
    }
    // "#6264A7",//blue
    // "#D9DBDB",//gray
    // "#13A10E"//green

    componentDidMount = () => {
        if (this.state.progressDetails.total !== 0) {

            var data = {
                datasets: [
                    {
                        data: this.dataValue(this.state.progressDetails.completed,
                            this.state.progressDetails.started,
                            this.state.progressDetails.not_started),
                        backgroundColor: this.colorValue(this.state.progressDetails.completed,
                            this.state.progressDetails.started,
                            this.state.progressDetails.not_started,
                            this.state.progressDetails.total),
                        hoverBackgroundColor: this.colorValue(this.state.progressDetails.completed,
                            this.state.progressDetails.started,
                            this.state.progressDetails.not_started,
                            this.state.progressDetails.total)
                    }],
                labels: this.labels(this.state.progressDetails.completed,
                    this.state.progressDetails.started,
                    this.state.progressDetails.not_started)
            };
            var ctx = document.getElementById("halfChart");
            var myDoughnutChart = new Chart(ctx, {
                type: 'doughnut',
                data: data,
                options: {
                    rotation: 1 * Math.PI,
                    circumference: 1 * Math.PI,
                    legend: {
                        display: false
                    },
                    tooltip: {
                        callbacks: {
                            label: function (tooltipItem, data) {
                                var dataset = data.datasets[tooltipItem.datasetIndex];
                                var total = dataset.data.reduce(function (previousValue, currentValue, currentIndex, array) {
                                    return previousValue + currentValue;
                                });
                                var currentValue = dataset.data[tooltipItem.index];
                                var precentage = Math.floor(((currentValue / total) * 100) + 0.5);
                                return precentage + "%";
                            }
                        }
                    },
                    cutoutPercentage: 50
                }
            });
        }


    }
    // highValue = () => {
    //     let object = {
    //         Complete: this.state.progressDetails.completed,
    //         Started: this.state.progressDetails.started,
    //         Not_started: this.state.progressDetails.not_started
    //     }
    //     var highest = Object.keys(object).reduce((a, b) => object[a] > object[b] ? a : b);
    //     this.setState({ value: highest })
    //     return object[highest]

    // }
    render() {
        return (
            <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                <div className="course-thumb-list">
                    <div className="detail-text-wrap">
                        <div className="course-title">
                            <h4 className="same-height">Total Progress</h4>
                            {/* <a href="#" className="three-dots-dropdown">
                            <img src="assets/images/landing/three-dots.svg" alt="dot"/>
                        </a> */}
                        </div>
                        {
                            this.state.progressDetails.total !== 0 ? (

                                <div class="half-chat-wrap">
                                    <canvas id="halfChart"></canvas>

                                    <React.Fragment>

                                        {
                                            this.state.progressDetails.total !== 0 ? (

                                                <span class="half-chat-percentage">{Math.round((this.state.progressDetails.completed * 100) / (this.state.progressDetails.total))}%</span>
                                            ) : (
                                                    <span class="half-chat-percentage">0%</span>

                                                )
                                        }
                                    </React.Fragment>
                                    <div class="half-chat-info">
                                        <span class="half-chat-count">0%</span>
                                        <span class="half-chat-progress">Complete</span>
                                        <span class="half-chat-count">100%</span>
                                    </div>
                                </div>
                            ) : (
                                    <p>No Data</p>
                                )
                        }
                    </div>
                </div>
            </div>
        )
    }
}
