import React, { PureComponent } from 'react';
import { appToken } from "../services/common.service";

const apiService = require("../services/api.service");

var page_limit = "", page = 1, search = "";
export default class ProgressBar extends PureComponent {
    state = ({
        finalCourseList: [],
        loader: true
    })
    componentDidMount = () => {
        this.courseLIst();
    }
    async courseLIst() {

        this.setState({ emptyData: false });
        const headers = {
            "Content-Type": "application/json",
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await apiService
            .listCourse(headers, page_limit, page, search)
            .then((response) => {
                console.log(response.data.data)
                if (response?.data?.data !== null) {
                    let courseList = response.data.data.filter(courses => courses.cs_percentage !== "100" && courses.cs_percentage !== "0");
                    courseList = courseList.slice(0, 3) //showing 3 courses
                    this.setState({
                        finalCourseList: courseList,
                        loader: false
                    })
                } else {
                    this.setState({
                        loader: false
                    })
                }

            })
            .catch((error) => {
                console.log(error)
                this.setState({
                    loader: false
                })
            });
    }
    render() {
        return (
            <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                <div className="course-thumb-list">
                    {this.state.loader ? (
                        <div className="bg-main">
                            <div style={{ textAlign: 'center' }}>
                                <img alt="view more" style={{ width: '45px', height: '40vh' }} src="custom_assets/loader.svg" />
                            </div>
                        </div>
                    ) : (
                            <div className="detail-text-wrap">
                                <div className="course-title">
                                    <h4 className="same-height">Courses Nearing Completion</h4>
                                </div>
                                <React.Fragment>
                                    {
                                        this.state.finalCourseList.length !== 0 ? (

                                            <React.Fragment>
                                                {this.state.finalCourseList.map((val, i) => {
                                                    return (
                                                        <div className="custom-progress-wrap">
                                                            <div className="progress-holder">
                                                                <div className="progress common-progress">
                                                                    <div className="progress-bar progress-bar-primary" role="progressbar" style={{ width: val.cs_percentage + '%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                                </div>
                                                                <div className="progress-value">
                                                                    {val.cs_percentage}%
                                    </div>
                                                            </div>
                                                            <span className="progress-info" title="Health and Safety in the Workplace: First Aid Awareness">{val.cb_title}</span>
                                                        </div>
                                                    )

                                                }
                                                )}
                                            </React.Fragment>
                                        ) : (
                                                <p>No Data</p>
                                            )
                                    }
                                </React.Fragment>


                            </div>
                        )}

                </div>
            </div>
        )
    }
}
