import React from 'react';
import { appToken } from "../../services/common.service";
import { userData } from "../../services/common.service";
import * as moment from "moment";
var Chart = require('chart.js');

const apiService = require("../../services/api.service");
export default class EnrollmentSummary extends React.Component{
  state = {
    isLoggedin: false,
    loader: false,
    token: '',
    completed:0,
    in_progress:0,
    not_started:0

  }
  componentDidMount = () => {
    if (userData() !== null) {
      this.setState({ isLoggedIn: true, token: userData().access_token }, 
      () => this.getEnrolmentSummary());
    } else this.setState({ isLoggedIn: false });
  }
  getEnrolmentSummary = async()=>{
    this.setState({
      loader: true
    })
    const headers = {
      Authorization: appToken(),

    };
    headers["Access-token"] = this.state.token;
    await apiService.getenrolmentSummary(headers,)
    .then((response) => {
      if (response.data.status === 200) {
        if(response.data.total !==0){

          this.setState({
            loader: false,
            completed: response.data.data.completed ===0 ? 0 :Math.round((response.data.data.completed * 100) / (response.data.total)),
            in_progress:response.data.data.in_progress === 0 ? 0 :Math.round((response.data.data.in_progress * 100) / (response.data.total)),
            not_started: response.data.data.not_started === 0 ? 0 :Math.round((response.data.data.not_started * 100) / (response.data.total)),
            total:response.data.total
          },()=>this.drawChart())
  
        }
         else{
          this.setState({
            loader:false,
            total :0
          })

        }
      }
    })
    .catch((err) => {
      console.log(err)
    })


  }
  drawChart = ()=>{
   var doughtNutData = [
      this.state.not_started,
      this.state.in_progress,
      this.state.completed
  ]
  var config = {
    type: 'doughnut',
    data: {
        datasets: [{
            data: doughtNutData,
            backgroundColor: [
                "#D9DBDB",//gray
                "#6264A7",//blue
                "#13A10E",//green
            ],
            label: 'Enrolment Summary'
        }],
        // labels: [
        //     "Passed",
        //     "In Progress",
        //     "Not Started"
        // ]
    },
    options: {
        responsive: true,
        // legend: {
        //     position: 'bottom',
        //     labels: {
        //         boxWidth: 11,
        //         boxHeight: 8,
        //         fontSize: 11,
        //         color: '#252424'
        //     }
        // },
        title: {
            display: false,
            text: 'Chart.js Doughnut Chart'
        },
        animation: {
            animateScale: true,
            animateRotate: true
        },
        cutoutPercentage: 70,
        tooltips: {
            callbacks: {
                label: function (tooltipItem, data) {
                    var dataset = data.datasets[tooltipItem.datasetIndex];
                    var total = dataset.data.reduce(function (previousValue, currentValue, currentIndex, array) {
                        return previousValue + currentValue;
                    });
                    var currentValue = dataset.data[tooltipItem.index];
                    var precentage = Math.floor(((currentValue / total) * 100) + 0.5);
                    return precentage + "%";
                }
            }
        }
    }
};

const chat2 = document.getElementById("chart-area").getContext("2d");

var myDoughnutChart = new Chart(chat2, config);

  }
    render(){
        return(
            <div class="col-xs-12 col-sm-12 col-md-6 col-lg-4 col-xl-4 mb-20">
                <div class="course-thumb-list">
                  <div class="detail-text-wrap">
                    <div class="course-title">
                      <h4 class="same-height">Enrolment Summary</h4>
                      {/* <a href="javascript:void(0);" class="three-dots-dropdown">
                        <img src="assets/images/landing/three-dots.svg" alt="dot" />
                      </a> */}
                    </div>
                    {
                      this.state.loader ? (
                        <div className="step step-3 active">
                  <div className="d-flex justify-content-center align-items-center spinner-content">
                    <div
                      style={{ marginTop: "30%" }}
                      className="spinner-border"
                      role="status"
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  </div>
                </div>
                      ):(
                        <React.Fragment>
                          {
                            this.state.total !== 0 ?(
                          <div class="chart-area-circle">
                      <div class="chart-area-holder enrolmentChart">
                      <canvas id="chart-area"></canvas>
                      </div>
                      <div class="chart-area-list">
                        <div class="chart-area-info">
                          <span class="progress-line progress-not-started"></span>
                          <div class="progress-text-wrap">
                            <span class="chart-area-count">{this.state.not_started}%</span>
                            <span class="chart-area-progress">Not started</span>
                          </div>
                        </div>

                        <div class="chart-area-info">
                          <span class="progress-line progress-primary"></span>
                          <div class="progress-text-wrap">
                            <span class="chart-area-count">{this.state.in_progress}%</span>
                            <span class="chart-area-progress">In Progress</span>
                          </div>
                        </div>

                        <div class="chart-area-info">
                          <span class="progress-line progress-success"></span>
                          <div class="progress-text-wrap">
                            <span class="chart-area-count">{this.state.completed}%</span>
                            <span class="chart-area-progress">Completed</span>
                          </div>
                        </div>
                      </div>
                    </div>
                            ):(
                              <p>No data Avilable</p>
                            )
                          }

                        </React.Fragment>

                      )
                    }
                  

                  </div>
                </div>
              </div>
        )
    }
}