const axios = require("axios");
axios.interceptors.response.use(null, function (err) {
  // console.log(err, 'errresponse')
  // console.log(err.response, 'err.response')
  // console.log(err.response.config.url)
  if (err.response.config.url.indexOf("/authenticate_user") > -1 ||
    err.response.config.url.indexOf("/calendars") > -1 ||
    err.response.config.url.indexOf("/validate_app") > -1 ||
    err.response.config.url.indexOf("/login") > -1 ||
    err.response.config.url.indexOf("/check_domain_exist") > -1 

  ) {
    console.log("/authenticate_user found")
  } else {
    if (err.response.status === 503) {
      // alert(err.response.data.message)
      // localStorage.clear()
      // window.location.reload()
      console.log('Service Unavailable')
    }
    if (err.response.status === 400) {
      // alert(err.response.data.message)
      // localStorage.clear()
      // window.location.reload()
      console.log('400 bad request')
    }
    if (err.response.status === 403) {
      // alert(err.response.data.message)
      // localStorage.clear()
      // window.location.reload()
      console.log('403 forbidden')
    }
    if (err.response.status === 500) {
      // alert(err.response.data.message)
      // localStorage.clear()
      // window.location.reload()
      console.log('500 internel server error')
    }
    if (err.response.status === 401) {
      alert(err.response.data.message + ' session expired')
      // localStorage.clear()
      // window.location.href = '/'
      // console.log('401 unauthorized')
    }
    if (err.response.status === 404) {
      // alert(err.response.data.message)
      // localStorage.clear()
      // window.location.reload()
      // console.log('404 not found')

    }

    return Promise.reject(err);
  }
});