import React, { useEffect, useRef } from "react";
import Flatpickr from 'react-flatpickr'
import * as moment from "moment";


const options = {
    inline: true,
    mode: "range",
    minDate: "today",
    dateFormat: "Y-m-d",
}

export default class VcDataRangeCalendar extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
        }
        this.calendarRef = React.createRef();

    }

    componentDidMount() {
        this.openCalendar()
    }
    openCalendar = () => {
        this.calendarRef.current.flatpickr.open()
    }
    ChangeDate = (e) => {
        console.log(e)
        if (e.length === 2)
            this.props.dateRangeCallback(e)
    }
    render() {
        return (
            <div>
                <Flatpickr
                    ref={this.calendarRef}
                    options={options}
                    onChange={(e) => this.ChangeDate(e)}
                    onDayCreate={(dObj, dStr, fp, dayElem) => {
                        let y = this.props.liveClasses.find((item) => {
                            if (moment(moment.utc(item.lc_start_time).toDate()).format('YYYY-MM-DD') === moment(dayElem.dateObj).format('YYYY-MM-DD')) {
                                return true
                            } 
                            else {
                                return false
                            }
                        })
                        if (y) {
                            dayElem.innerHTML += "<span class='event'></span>";

                        }
                    }}
                />
            </div>
        )
    }
}

