	componentDidMount = () => {
		if (userData() !== null) {
			this.setState(
				{
					user_token: userData().data._token, apprenticeList: [],
					auth_token: getAuthtoken(),
					loader: true

				},
				() => {
					this.getTutorList();
				}
			);
		}
	};
	checkArrayEmpty = (val) => {
		if (val === 0) {
			this.setState({ emptyData: true })
		} else {
			this.setState({ emptyData: false })

		}
	}
	toggleNavbar = () => {
		this.setState({ class: !this.state.class });
	};
	getTutorList = async () => {
		formData.append('_token', decodeURIComponent(this.state.user_token))
		formData.append('direction', 'desc')
		formData.append('is_admin', '1')
		formData.append('is_active', "1")
		formData.append('per_page', this.state.page_limit)
		formData.append('current_page', this.state.page)
		// formData.append('occupation_id', 18)


		const headers = {
			'Content-Type': 'multipart/form-data',
		}
		headers['Authorization'] = 'Bearer ' + this.state.auth_token
		await apiService.getTutorlist(headers, formData)
			.then((response) => {
				this.setState({ loader: false })
				
				if (response.data.status === "Success") {
					this.setState({
						trainers: this.state.trainers.concat(response.data.trainers),
						fetched_rows: response.data.fetched_rows,
						total_trainers: Number(response.data.total_trainers),
						loadmore_loader: false
					})
					this.checkArrayEmpty(response.data.fetched_rows)
				}else{
					this.setState({
						trainers: [],
						fetched_rows: 0,
						total_trainers: 0,
						loadmore_loader: false,
						loader: false
					})
					this.checkArrayEmpty(0)
				}
			})
			.catch((err) => {
				this.setState({
					trainers: [],
					fetched_rows: 0,
					total_trainers: 0,
					loadmore_loader: false,
					loader: false
				})
				this.checkArrayEmpty(0)

			})

	}
