import React, { Component } from 'react'
export default class VleProfile extends Component {
    constructor(props) {
		super(props)
		this.state = {
        }
    }
    editPersonalData = () => {
        this.props.updateState({ open_edit_vlepersonal_section: true })
        this.props.updateState({ show_vle_profile: false })
    }
    editSecurity = () => {
        this.props.updateState({ open_edit_vlesecurity_section: true })
        this.props.updateState({ show_vle_profile: false })
    }
    render(){
        return(
                <div id="profile-step" class="step active">
                    <div class="profile-header">
                        <div class="img-wrap">
                            <img src={this.props.image_url} alt="" />
                        </div>
                        <h4>Profile</h4>
                        <a href="javascript:void(0);" onClick={() => this.props.updateState({ open_modal: false })}><img src="assets/images/landing/cross-icon.svg" alt="" /></a>
                    </div>
    
                    <div class="profile-update">
                        <span>Personal</span>
                        <a data-toggle="tab" style={{cursor:'pointer'}} onClick={() => { this.editPersonalData() }}>Update</a>
                    </div>
    
                    <ul class="profile-info">
                        <li>
                            <span>First Name</span>
                            <p>{this.props.fname}</p>
                        </li>
                        {this.props.lname &&
                            <li>
                                <span>Last Name</span>
                                <p>{this.props.lname}</p>
                            </li>
                        }
                        <li>
                            <span>Email</span>
                            <p>{this.props.email}</p>
                        </li>
                        <li>
                            {/* <a href="javascript:void(0);">
                                <img src="assets/images/landing/upload-image-icon.svg" alt="" />
                                Update Profile Image
                            </a> */}
                        </li>
                    </ul>
    
                    <div class="profile-update" style={{ cursor: 'pointer' }}>
                        <span>Security</span>
                        <a onClick={() => this.editSecurity()} data-tab="security-step">Update</a>
                    </div>
    
                    <ul class="profile-info">
                        <li>
                            <span>Password</span>
                            <img src="assets/images/landing/password-icon.svg" alt="" />
                        </li>
                    </ul>
    
                    <div class="profile-modal-foo">
                        <button onClick={() => this.props.updateState({ open_modal: false })} type="button" class="btn common-course-btn btn-secondary">Close</button>
                    </div>
                </div>
            );
    }
}