import React from "react";
import { config } from "../../../config";
import * as microsoftTeams from "@microsoft/teams-js";
import { userData, getSubdomain } from "../services/common.service";
import "../portfolio.css";
import { connectableObservableDescriptor } from "rxjs/internal/observable/ConnectableObservable";
import NotFound from "../../shared_components/notFound";

const apiService = require("../services/api.service");
var getConfig = () => {
    let isLegacy = localStorage.getItem('legacy')
    var conf = config
    console.log(conf, 'isLegacy1')
    if (isLegacy !== null) {
      conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO_LEGACY
    } else {
      conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO
    }
    console.log(conf, 'isLegacy2')
    return conf
  }
const APP_BASEURL_IFRAME = getConfig().myportfolio.APP_BASEURL_IFRAME;
let arrowstyle = {
    marginRight: '8px',
    fontSize: '14px',
    color: '#9b9b9b'
}
class programeDetail extends React.Component {

    state = {
        token: '',
        url: '',
        portfolio_user: '',
        loginType: '',
        subdomain: '',
        isLoggedIn: false
    }

    componentDidMount() {
        if (userData() !== null) {
            this.setState({ isLoggedIn: true })
            let user_data = localStorage.getItem("portfolio_user");
            let portfolio_user = JSON.parse(user_data);
            this.setState({ token: portfolio_user.data._token }, () => {
                this.getUrl()
            });
        } else {
            this.setState({ isLoggedIn: false })

        }

    }
    getUrl = () => {
        this.setState({
            loginType: userData().data.loginType,
            token: decodeURIComponent(userData().data._token),
            subdomain: getSubdomain()
        }, () => {
            // if (this.state.loginType === "learner") {
            let programe_id = this.props.match.params.programe_id
            if (programe_id === 'event') {
                let event_link = localStorage.getItem('event_link')
                if (event_link !== null) {
                    this.setState({
                        finalUrl: event_link
                    })
                    localStorage.removeItem('event_link')
                } else {
                    console.log('no event_link')
                    window.location.href = "/programe-list"
                }
            } else {
                if (this.state.loginType === "learner") {
                    let url = `https://${this.state.subdomain}${APP_BASEURL_IFRAME}/learner/programs/${this.props.match.params.programe_id}/view?mst-token=${this.state.token}`
                    this.setState({
                        finalUrl: url
                    })
                }
            }
            // }
        })
        localStorage.removeItem('programe_id')
    }
    render() {
       return(
        <div>

        {!this.state.isLoggedIn ?

            <NotFound
                message="Sorry, you need to login to Appreticeship Application to access your Programmes"
                domain="myportfolio"
                response={(val) => this.gotResponse(val)}
            />
            :
            <React.Fragment>
                <div className="filter-bar-block">

                    <a href={window.location.origin + '/programe-list'}>
                        <span style={arrowstyle}>

                            <i class="fas fa-chevron-left"></i>
                        </span>back</a>
                    <div className="filter-bar-holder">

                    </div>
                </div>
                <div className="container-fluid" style={{ paddingTop: '60px' }}>
                    <iframe
                        src={this.state.finalUrl}
                        title="description"
                        style={{ width: '100%', height: '91vh', borderWidth: '0px' }}
                    ></iframe>

                </div>

            </React.Fragment>
        }
    </div>
       )
        // if (this.state.finalUrl) {
        //     return (
        //         <iframe
        //             src={this.state.finalUrl}
        //             title="Program Details"
        //             style={{ width: '100%', height: '97vh', position: 'absolute', borderWidth: '0px' }}
        //         ></iframe>
        //     )
        // } else {
        //     return (
        //         <p>loading</p>
        //     )
        // }
    }

}
export default programeDetail