import React from "react";
import { appToken } from "../../services/common.service";

const apiService = require("../../services/api.service");

class LiveclassAteendees extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      liveclass_id: this.props.Liveclass_id,
      attendees: [],
      attendessShow:false

    };
  }
  componentDidMount() {

    this.getAttendess();
  }
  async getAttendess() {
    let data = {
      event_id: this.state.liveclass_id,
      event_type: "live_class",
      integration: "microsoft_teams",
    };
    const headers = {
      "Content-Type": "application/json",
      Authorization: appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    }
    await apiService
      .getAttendeeList_liveclass(headers, data)
      .then((data) => {
        this.setState({ attendees: data.data.data.attendees }, () => {
          this.setState({attendessShow:true})
          
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  attendeeShow = () => {
    if (this.state.attendees.length === 0) {
    }
    else {
      return this.state.attendees.map((val, i) => {
        if (i < 3) {

          return (
            <li key={i}>
              <a href="javascript:void(0);">
                <img src={val.ll_learner_image_url} alt="event img" />
              </a>
            </li>
          );
        }
      });
    }
  };
  RemainngAttendees = () => {
    if (this.state.attendees.length > 3) {
      return (
        <li className="event-attendees-count">
          <a href="#">+{this.state.attendees.length - 3}</a>
        </li>
      );
    }
  };
  render() {
    return (
      <ul className="event-attendees-list">
           {
          !this.state.attendessShow ?
          <div className="load-more-loader"></div>:
          <>
        {this.attendeeShow()}
        {this.RemainngAttendees()}
        </>

        }
      </ul>
    );
  }
}
export default LiveclassAteendees;
