import React, { PureComponent } from 'react'
import { userData, appToken, getAuthtoken } from "../services/common.service";

const apiService = require("../services/api.service");

export default class ProgressBar extends PureComponent {
    state = {
        initial_loader: true,
        curentpage: 1,
        per_page: 3,
        sort_by: 'progress',
        direction: 'desc',
        programmes: []

    }
    componentDidMount() {
        this.getNearingCompletingPrgms()

    }
    getNearingCompletingPrgms = async () => {
        const headers = {
            "Content-Type": "multipart/form-data",
        };
        if (this.state.auth_token !== null) {
            headers["Authorization"] = "Bearer " + getAuthtoken();
        }
        let formData = new FormData();
        formData.append("_token", decodeURIComponent(userData().data._token));
        formData.append("current_page", this.state.curentpage);
        formData.append("per_page", this.state.per_page);
        formData.append("sort_by", this.state.sort_by);
        formData.append("direction", this.state.direction);
        formData.append("memberid", userData().data.member_details.id)
        await apiService.getNearingcompletingProgrammes(headers, formData)
            .then((response) => {
                this.setState({ programmes: response.data.member_details, initial_loader: false })
                console.log(response.data.member_details, 'get nering completing prgms')
            })
            .catch((err) => {
                console.log(err)
            })

    }
    render() {
        return (
            <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                <div className="course-thumb-list">
                    <div className="detail-text-wrap">
                        <div className="course-title">
                            <h4 className="same-height">Programmes Nearing Completion</h4>
                            {/* <a href="#" className="three-dots-dropdown">
                                <img src="assets/images/landing/three-dots.svg" alt="dot" />
                            </a> */}
                        </div>
                        {
                            this.state.initial_loader ? (
                                <div className="step step-3 active">
                                    <div className="d-flex justify-content-center align-items-center spinner-content">
                                        <div style={{ marginTop: '25%' }} className="spinner-border" role="status">
                                            <span className="visually-hidden">Loading...</span>
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <React.Fragment>
                                    {
                                        this.state.programmes.length !== 0 ? (
                                            <React.Fragment>
                                            {
                                                this.state.programmes.map((val, i) => {
                                                    return <div className="custom-progress-wrap" key={i}>
                                                        <div className="progress-holder">
                                                            <div className="progress common-progress">
                                                                <div className="progress-bar progress-bar-primary" role="progressbar"
                                                                    style={{ width: `${val?.program_progress ?? 0}%` }}
                                                                    aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                            </div>
                                                            <div className="progress-value">
                                                                {`${Math.round(val?.program_progress)}%`}
                                                    </div>
                                                        </div>
                                                        <span className="progress-info"
                                                            title="Health and Safety in the Workplace: First Aid Awareness">
                                                            {val['occupation name']}</span>
                                                    </div>
                                                })
                                            }
                                        </React.Fragment>
                                        ):(
                                            <p>No Data</p>
                                        )
                                    }
                                </React.Fragment>
                                   

                                )
                        }




                    </div>
                </div>
            </div>
        )
    }
}
