import React from "react";
import { appToken } from "../../services/common.service";
import * as moment from 'moment'

var Chart = require('chart.js');
const api_service = require("../../services/api.service");

export default class VcChart extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            eventsAnalytics: [],
            isApiLoading: true
        }
    }
    componentDidMount = () => {
        this.getEventAnalytics();
    }
    utcToLocal = (utc) => {
        var localTime = moment.utc(utc).toDate();

        return moment(localTime).format('MMM Do YYYY h:mm a')
    }
    async getEventAnalytics() {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        let params = {
            by_days: "0"
        }
        await api_service
            .eventAnalytics(headers, params)
            .then((response) => {
                this.setState({ isApiLoading: false }, () => {

                    var total_invities = [], titile = [], dates = [];
                    response.data.data.sort(function (a, b) {
                        var dateA = new Date(a.lc_start_time);
                        var dateB = new Date(b.lc_start_time);
                        return dateA - dateB;
                    });
                    response.data.data.map((val) => {
                        total_invities.push(val.ll_total_invited)
                        titile.push(val.lc_class_title)
                        dates.push(this.utcToLocal(val.lc_start_time))
                    })

                    var ctx = document.getElementById('myChart');
                    var option = {
                        scales: {
                            xAxes: [{
                                ticks: {
                                    callback: function (value) {
                                        if (value.length > 8) {
                                            return value.substr(0, 8) + '...';
                                        } else {
                                            return value
                                        }

                                    },
                                }
                            }],
                            yAxes: [{
                                ticks: {
                                    beginAtZero: true,
                                    callback: function(value) {if (value % 1 === 0) {return value;}}
                                }
                            }]
                        },
                        responsive: true,
                        maintainAspectRatio: false,
                        tooltips: {
                            enabled: true,
                            mode: 'label',
                            callbacks: {
                                title: function (tooltipItems, data) {
                                    var idx = tooltipItems[0].index;
                                    return 'Title: ' + data.labels[idx];
                                },
                                footer: function (tooltipItem) {
                                    return dates[tooltipItem[0].index];
                                }

                            }
                        },
                    };
                    var myBarChart = new Chart(ctx, {
                        type: 'bar',
                        data: {
                            labels: titile,
                            datasets: [{
                                label: 'Attendees',
                                data: total_invities,
                                backgroundColor: "#7B83EB"
                            }]
                        },
                        options: option
                    });
                });
            })
            .catch((error) => {
                console.log(error);
                this.setState({ isApiLoading: false })
            });
    }
    render() {
        return (
            <div className="chat-wrap">
                <div class="statistics-holder">
                    <div class="statistics-text-wrap">
                        <h4>Upcoming Popular Events</h4>
                       
                    </div>
                </div>
                <div className="line-chat-height">
                    <div className="chartjs-size-monitor">
                        <div class="chartjs-size-monitor-expand">
                            <div class="">
                            </div>
                        </div>
                        <div class="chartjs-size-monitor-shrink">
                            <div class="">
                            </div>
                        </div>
                    </div>
                    {this.state.isApiLoading &&
                        <div className="step step-3 active">
                            <div className="d-flex justify-content-center align-items-center spinner-content">
                                <div
                                    style={{ marginTop: "45%" }}
                                    className="spinner-border"
                                    role="status"
                                >
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                            </div>
                        </div>
                    }
                    <canvas id="myChart" className="chartjs-render-monitor" width="470" height="250" style={{ display: 'block', width: '473px', height: '250px', background: 'white' }}></canvas>
                </div>

            </div>
        )

    }

}