import React, { Component } from 'react';
import { config } from '../../../../config';
import * as microsoftTeams from "@microsoft/teams-js";
const calender_appid = config.index.APP_ID;
const calender_baseUrl = config.index.APP_BASEURL;
const calender_entityId = "calendar";
const calender_entityLabel = "calendar";
const mykademy_app_id = config.mykademy.APP_ID;
const mykademy_baseUrl = config.mykademy.APP_BASEURL;
const mykademy_entityId = "mycourses";
const mykademy_entityLabel = "mycourses";

const portfolio_app_id = config.index.APP_ID;
const portfolio_baseUrl = config.index.APP_BASEURL;
const portfolio_entityId = "myprograms";
const portfolio_entityLabel = "myprograms";
class deadlines extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPage: 1,
            perPage: 5,
            deadlineEvents: this.props.deadlineEvents
        }
    }

    componentWillMount() {
        console.log(this.props.deadlineEvents, 'get live events')


    }

    componentDidMount() {

    }

    componentWillReceiveProps(nextProps) {
        console.log(this.props)
    }

    handleClickplus = () => {
        this.setState({
            currentPage: this.state.currentPage + 1
        });
    };
    handleClickminus = () => {
        this.setState({
            currentPage: this.state.currentPage - 1
        });
    };

    componentWillUnmount() {

    }


    GetFormattedDateString(date) {
        if (date !== undefined && date !== "") {
            var myDate = new Date(date);
            var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][myDate.getMonth()];
            var week = ['Sun', 'Mon', 'Tues', 'Wed', 'Thu', 'Fri', 'Sat'][myDate.getDay()];
            var str = week + ", " + myDate.getDate() + " " + month
            return str;
        }
        return "";
    }
    viewCalender() {
        console.log('calender')
        microsoftTeams.executeDeepLink(
            `https://teams.microsoft.com/l/entity/${calender_appid}/${calender_entityId}?webUrl=${calender_baseUrl}programe-list&label=${calender_entityLabel}`
        );
    }
    deeplinkCommon = (link, from) => {
        if (from === 'myportfolio') {
            localStorage.setItem('event_link', link)
            microsoftTeams.executeDeepLink(
                `https://teams.microsoft.com/l/entity/${portfolio_app_id}/${portfolio_entityId}?webUrl=${portfolio_baseUrl}programe-list&label=${portfolio_entityLabel}`
            );

        } if (from === 'mykademy') {
            localStorage.setItem('event_link', link + '&callback=' + window.location.origin + '/course-list')
            microsoftTeams.executeDeepLink(
                `https://teams.microsoft.com/l/entity/${mykademy_app_id}/${mykademy_entityId}?webUrl=${mykademy_baseUrl}course-list&label=${mykademy_entityLabel}`
            );
        }

        console.log('depplink', link)

    }
    render() {
        const { deadlineEvents, currentPage, perPage } = this.state;
        const indexOfLastTodo = currentPage * perPage;
        const indexOfFirstTodo = indexOfLastTodo - perPage;
        const currentEvents = deadlineEvents.slice(indexOfFirstTodo, indexOfLastTodo);
        const renderEvents = currentEvents.map((event, i) => {
            return <tr key={i} className={event?.event_live_status === '1' ? "bg-green" : ''}>
                <td scope="row">
                    <div class="info-wrap capitalize">
                        <div class="svg-wrap">
                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
                                <path d="M9.354 6.396c.195.196.195.512 0 .708l-1.75 1.75c-.19.189-.494.195-.691.015l-.75-.685c-.204-.186-.218-.502-.032-.706.186-.204.502-.218.706-.032l.397.362 1.412-1.412c.196-.195.512-.195.708 0zm0 5.708c.195-.196.195-.512 0-.707-.196-.196-.512-.196-.708 0l-1.412 1.412-.397-.363c-.204-.187-.52-.172-.706.032-.186.204-.172.52.032.706l.75.685c.197.18.502.174.69-.015l1.75-1.75zm1.146.396c0-.276.224-.5.5-.5h2.5c.276 0 .5.224.5.5s-.224.5-.5.5H11c-.276 0-.5-.224-.5-.5zM11 7c-.276 0-.5.224-.5.5s.224.5.5.5h2.5c.276 0 .5-.224.5-.5s-.224-.5-.5-.5H11zM3 4.5C3 3.672 3.672 3 4.5 3h11c.828 0 1.5.672 1.5 1.5v11c0 .828-.672 1.5-1.5 1.5h-11c-.828 0-1.5-.672-1.5-1.5v-11zM4.5 4c-.276 0-.5.224-.5.5v11c0 .276.224.5.5.5h11c.276 0 .5-.224.5-.5v-11c0-.276-.224-.5-.5-.5h-11z" />
                            </svg>
                        </div>
                        {event?.event_name}
                    </div>
                </td>
                <td><div class="info-wrap"> {this.GetFormattedDateString(event?.event_time)}</div></td>
                {event?.event_link &&
                    <td className="dashbordtag"><a class="view-btn active" onClick={() => this.deeplinkCommon(event?.event_link, event?.from)}>View</a></td>
                }
                {/* href={event?.event_link + '&callback=' + window.location.origin + '/tab'} */}
            </tr>
        });

        return (
            <div className="tab-pane fade show active" id="live-sessions" role="tabpanel" aria-labelledby="sessions-tab">
                <div class="table-responsive sessions-table-list">
                    <table class="table">
                        <thead>
                            <tr>
                                <th scope="col">Name</th>
                                <th width="100px" scope="col">Date</th>
                                <th scope="col">Minutes</th>
                                <th scope="col"></th>
                            </tr>
                        </thead>
                        <tbody>
                            {renderEvents}
                        </tbody>
                    </table>
                </div>

                <div class="showing-pagination">
                    <a className={this.state.currentPage === 1 ? "opacity-arrow" : ''}
                        onClick={this.handleClickminus} >
                        <img src="assets/images/landing/arrow-left.svg" alt="arrow" />
                    </a>
                    <span>Showing {this.state.currentPage} of {" "}
                        {this.state.deadlineEvents.length / perPage > Math.round(this.state.deadlineEvents.length / 5)
                            ? Math.round(this.state.deadlineEvents.length / perPage) + 1
                            : Math.round(this.state.deadlineEvents.length / perPage)}
                    </span>
                    <a className={this.state.currentPage >= this.state.deadlineEvents.length / perPage ? "opacity-arrow" : ''}
                        onClick={this.handleClickplus}>
                        <img src="assets/images/landing/arrow-right.svg" alt="arrow" />
                    </a>
                </div>
                <span className="dashbordtag">

                    <a class="view-table-link" onClick={() => this.viewCalender()}>
                        <img src="assets/images/landing/calendar.svg" alt="calendar" />
                                        View Calender
                                        </a>
                </span>
            </div>
        );
    }
}

export default deadlines;