import { store } from 'react-notifications-component';
import * as moment from 'moment'
var notification = {
  title: "Success",
  message: "Leaner Registration successful",
  type: "success",
  insert: "top",
  dismiss: {
    duration: 5000,
    pauseOnHover: true
  },
  container: "top-right",
  animationIn: ["animate__animated animate__fadeIn"], // `animate.css v4` classes
  animationOut: ["animate__animated animate__fadeOut"] // `animate.css v4` classes
};


export const getUpcomingEvents = (Events) => {
  Events.sort(function (a, b) {
    var dateA = new Date(a.event_time);
    var dateB = new Date(b.event_time);
    return dateA - dateB;
  });
  let currentDate = new Date();
  let upcomingEvent = Events.filter(event => new Date(event.event_time) >= currentDate);
  // const final = upcomingEvent.slice(0, 10) //showing 10 upcoming events only
  return upcomingEvent;
}
export const getLiveSessions = (events) => {
  return events.filter((event) => {
    if (event.event_type === "MSTeams") {
      return event
    } else if (event.integration) {
      if (event.event_type === "live_class" && event.integration === "microsoft_teams")
        return event
    }
  })
}
export const getDeadlines = (events) => {
  return events.filter((event) => {
    if (event.event_type !== "live_class" && event.event_type !== "MSTeams") {
      return event
    }
  })
}
export const Notification = (message, type, title) => {
  notification.title = title
  notification.type = type;
  notification.message = message
  store.addNotification({
    ...notification,
    type: type
  })

}
export const getVcToken = () => {
  let vcTokenObj = localStorage.getItem('vc_auth')
  if (vcTokenObj !== null) {
    let tkn = JSON.parse(vcTokenObj)
    return tkn
  } else {
    return null
  }
}
export const getUpcomingEventsVideoClassroomPortfolio = (Events) => {
  Events.sort(function (a, b) {
    var dateA = new Date(a.event_time);
    var dateB = new Date(b.event_time);
    return dateA - dateB;
  });
  let currentDate = moment.utc(new Date()).format('YYYY-MM-DD hh:mm');
  let upcomingEvent = Events.filter(event => event.event_time >= currentDate);
  return upcomingEvent;
}
export const isAdminMykademy = () => {
  let users = localStorage.getItem('mykademy_user')
  if (users !== null) {
    let parsed = JSON.parse(users)
    if (parsed.user_data.us_role_id === "1")
     {
      return true;
    }else if(parsed.user_data.us_role_id === "6"){
      return true
    }
     else {
      return false;
    }
  } else {
    return false
  }
}

export const isAdminPortfolio = () => {
  let users = localStorage.getItem('portfolio_user')
  if (users !== null) {
    let parsed = JSON.parse(users)
    if (parsed.data.member_details.is_admin === 1) {
      return true;
    } else {
      return false;
    }
  } else {
    return false
  }
}

