import React from 'react';
import AdminNavbar from "../../shared_components/adminNavbar";
import { MyPortfolioAdminSidebarData, MyportfolioAdminDropdownContents, subdropdownData, MykademyAdminDropdownContents } from "../../../config";
import { userData, getAuthtoken, is_super_org_admin } from "../services/common.service";
import { NavLink } from "react-router-dom";
import { Notification } from "../../../services/common.service";
import { debounce } from "lodash";
import weeklyImg from '../../../assets/images/landing/weekly-delivery.svg'
import apprenticesDelivery from '../../../assets/images/landing/apprenticeship-delivery.svg'


const apiService = require("../services/api.service");
const mykademyCommonService = require("../../mykademy/services/common.service");
var page = 1, per_page = 6;
const myportfolioCommonService = require("../services/common.service")
let error_style = {
    marginBottom: '15px',
    paddingLeft: '13px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: '1.33',
    letterSpacing: 'normal',
    textAlign: 'left',
    color: '#c4314b',
}
let inputErr = {
    border: ' solid 1px #c4314b',
    marginBottom: '5px',
    background: 'none',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'right calc(.375em + .1875rem) center',
    backgroundSize: 'calc(.75em + .375rem) calc(.75em + .375rem)'
}
export default class apprenticesDetailed extends React.Component {

	constructor(props) {
		super(props);
		let _ids = JSON.parse(decodeURIComponent(atob(this.props.match.params.apprentice_id).replace(/&/g, "\",\"").replace(/=/g, "\":\"")))
		this.state = {
			apprenticeId: _ids[0].leaner_id,
			userPhone: _ids[0].phone,
			userEmployer: _ids[0].employer,
			userImage_url: _ids[0].image_url,
			userEmail: _ids[0].email,
			useName: _ids[0].name,
			userCountry: _ids[0].country,
			apprenticesData: [],
			total_count: "",
			clickCount: 0,
			loader: true,
			class: false,
			loadmore_loader: false,
			seletedprgm: [],
			per_page: 10,
			curentpage: 1,
			auth_token: '',
			initial_loader: false,
			emptyData: false,
			li_id: '',
			input: '',
			programs: [],
			isHour:false,
			hours:-1,
			show_jobHours:false,
			isSelect:false


		}
		
	}
	handleSearch = debounce((text) => {
		let query = text.trim();
		console.log(query,'get queryyyyyyyyyy')
		if (query === "") {
		  this.setState({
			programs: [],
			initial_loader: true,
			per_page: 10,
			curentpage: 1,
		  });
		  this.getAllProgrammes();
		} else {
		  this.setState({
			programs: [],
			initial_loader: true,
			per_page: 0,
			curentpage: 0,
		  });
		  this.searchPrograms(query);
		}
	  }, 500);
	
	componentDidMount() {
		if (userData() !== null) {

			this.setState({ user_token: userData().data._token, auth_token: getAuthtoken() },
				() => {
					this.getApprenticesDetails();
					this.getAllProgrammes();
				})
		}
	}

	searchPrograms = async (query)=> {
		const headers = {
			'Content-Type': 'multipart/form-data',
		}
		headers['Authorization'] = 'Bearer ' + this.state.auth_token

		if (is_super_org_admin()) {
		  await apiService
			.getProgrameList_superorg_admin_search(
			  headers,
			  query,
			  decodeURIComponent(this.state.user_token),
			  this.state.curentpage,
			  this.state.per_page
			)
			.then((response) => {
			  this.setState({
				programs: response.data.occupations_details,
				total_rows: response.data.total_rows,
				initial_loader: false,
			  },()=>this.list());
			  this.checkArray(response.data.total_occupations_count);
			})
			.catch((err) => {
			  this.setState({ initial_loader: false, loadmore_loader: false });
			  console.log(err)
			});
		} else {
	
		  await apiService
			.getProgrameList_admin_search(
			  headers,
			  query,
			  decodeURIComponent(this.state.user_token),
			  this.state.curentpage,
			  this.state.per_page
			)
			.then((response) => {
	
			  this.setState({
				programs:response.data.apprenticeships_details,
				total_rows: response.data.total_rows,
				initial_loader: false,
			  },()=>this.list(),console.log(this.state.programs,'get programme from search',response.data.apprenticeships_details));
			  this.checkArray(response.data.total_apprenticeship_count);
			})
			.catch((err) => {
			  this.setState({ initial_loader: false, loadmore_loader: false });
			  console.log(err)
			});
		}
	  }

	getApprenticesDetails = async () => {
		this.setState({loader:true})
		let formData = new FormData();
		formData.append('_token', decodeURIComponent(this.state.user_token))
		formData.append('member_id', this.state.apprenticeId)
		formData.append('per_page', per_page)
		formData.append('current_page', page)
		// formData.append('occupation_id', 18)


		const headers = {
			'Content-Type': 'multipart/form-data',
		}


		headers['Authorization'] = 'Bearer ' + this.state.auth_token
		// getLearnerList
		// getapprenticeStudents

		await apiService.getapprenticeStudents(headers, formData)
			.then((response) => {
				if (response.data.length != 0) {
					console.log('hereeeeresponse',response.data.status)
					if(response.data.status === 'error'){
						this.setState({
						
							ProgrammeData: [],
							total_count: 0, loader: false, 
						},
						)
					}else{

						this.setState({
							
							ProgrammeData: response.data.members_details,
							total_count: response.data.total_rows, loader: false, 
						},
						)
					}
				}


			}).catch((err) => {
				console.log(err)
			})
	}

	getEmployerName = (organizational_details) => {
		if (organizational_details != undefined) {

			let organizationalDetails = JSON.parse(organizational_details)

			return <span className="common-list-text">{organizationalDetails.name}</span>
		}
	}

	getProgressBar = (progressValue) => {

		var value = 0, progressClass = "";
		if (progressValue !== null) {
			value = parseInt(progressValue);
		} else {
			value = 0;
		}
		if (value >= 0 && value < 50) {
			progressClass = 'progress-bar-danger';
		} else if (value >= 50 && value < 100) {
			progressClass = 'progress-bar-blue';
		} else if (value === 100) {
			progressClass = 'progress-bar-success';
		}

		return (<>
			<div className="progress common-progress mt-10">
				<div className={progressClass + " " + "progress-bar"} role="progressbar" style={{ width: value + '%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
			</div>
			<span className="progress-completed mb-0">{value}% completed</span>
		</>
		)
	}

	getBackground = (banner_image) => {
		if (banner_image === null || undefined || "") {

			return <img src={window.location.origin + "/assets/images/img-default.png"} alt="user name" />
		} else {
			return <img src={banner_image} alt="banner_logo" />

		}
	}

	getLogo = (occupation_logo) => {
		if (occupation_logo === null || undefined || "") {

			return <img src="/./custom_assets/olive-group-favicon.png" alt="program logo" />
		} else {
			return <img src={occupation_logo} alt="logo" />

		}
	}

	getProfileUrl = (img_url) => {
		if (img_url === "" || undefined) {

			return <img src="/./custom_assets/default_user.jpg" alt="user name" className="img-rounded flex-shrink-0" />
		} else {
			return <img src={img_url} alt="user name" className="img-rounded flex-shrink-0" />

		}
	}
	loadMore = () => {
		this.state.clickCount++;

		page = this.state.clickCount + 1;
		this.setState({ loadmore_loader: true }, () => {

			this.getApprenticesDetails();
		})
	}

	toggleNavbar = () => {
		this.setState({ class: !this.state.class })
	}
	getCountryname = (data) => {

		if (data === undefined || null) {
			return <span style={{ padding: '5px' }} className="progress-completed mb-0">{"-"}</span>

		}

		return <span style={{ padding: '5px' }} className="progress-completed mb-0">{data}</span>
	}
	getAllProgrammes = async () => {
		this.setState({initial_loader:true})
		let formData = new FormData();
		formData.append("_token", decodeURIComponent(this.state.user_token));
		formData.append("current_page", this.state.curentpage);
		formData.append("per_page", this.state.per_page);
		const headers = {
			'Content-Type': 'multipart/form-data',
		}
		headers['Authorization'] = 'Bearer ' + this.state.auth_token
		if (is_super_org_admin()) {
			await apiService
				.getProgrameList_superorg_admin(headers, formData)
				.then((response) => {
					console.log(response.data.status,'statusssssssss')
					if(response.data.status === 'error'){
						this.setState({
							programs: [],
							initial_loader: false,
						},()=>this.list());

					}else{
						
						this.setState({
							programs: response.data.occupations_details,
							initial_loader: false,
						},()=>this.list());
						this.checkArray(response.data.total_occupations_count);
					}
				})
				.catch((err) => {
					this.setState({ initial_loader: false, loadmore_loader: false });
					console.log(err)
				});
		}
		else {

			await apiService
				.getProgrameList_admin(headers, formData)
				.then((response) => {
					if(response.data.status === 'error'){
						this.setState({
							programs: response.data.apprenticeships_details,
							initial_loader: false,
						},()=>this.list());
						this.checkArray(response.data.total_apprenticeship_count);
					}else{
						this.setState({
							programs: [],
							initial_loader: false,
						},()=>this.list());
					}
				})
				.catch((err) => {
					this.setState({ initial_loader: false, loadmore_loader: false });
					console.log(err)
				});
		}
	}
	checkArray = (val) => {

		if (val === "0") {
			this.setState({ emptyData: true });
		} else {
			this.setState({ emptyData: false });
		}

	};
	onChangeHandler = (e) => {
		this.setState({ show_jobHours:false})

        e.preventDefault()

		this.setState({
			input: e.target.value,
		},this.handleSearch(e.target.value))
	}
	onChangeHandlerHours = (e) => {
		e.preventDefault()

		let hour = e.target.value
		if(!isNaN(hour)){
			
			this.setState({isHour:false,hours:hour})
		} else{
			this.setState({isHour:true})

		}
	}
	list = () => {
		console.log(this.state.programs,'here get programmes',this.state.input)
		if(this.state.initial_loader){
			return(
				<ul>
					<li>Loading...</li>
				</ul>
			)
		}else{

			if (this.state.programs.length === 0) {
				return (
					<ul>
						<li>No Programme Found for Enrolment</li>
					</ul>
				)
			} else {
	
				return (<ul>{this.state.programs
					
					.map((d, index) => (
						<li key={index} value={d.id} onClick={this.inputhandle.bind(this, "course_name")}
							className={(d.id === this.state.li_id ? "liclr" : "")}
						>
							{/* <span>{d.has_weeks === 1 ?"1":"0"}</span> */}
							<img className="listImg" src={d.has_weeks === 1 ? weeklyImg : apprenticesDelivery} alt="" title={d.has_weeks === 1 ? 'Weekly Delivery':'Apprenticeship Delivery'}></img>
							{d.name}
						</li>
					))
				}
				</ul>
				)
			}
		}

	}
	inputhandle = (field, e) => {
		console.log(e.target.value,'iddddddd')
		if (field === "course_name") {
			const programme_id = e.target.value;
			const seletedProgramme = this.state.programs.find(
				(u) => u.id == programme_id
			);
			this.setState({
				seletedProgramme: seletedProgramme,
				input: seletedProgramme.name,
				li_id: seletedProgramme.id
			});
			console.log(seletedProgramme,'seleted')
			if(seletedProgramme.has_weeks === 0){
				this.setState({ show_jobHours:true,isHour:true})
			} else{
				this.setState({ show_jobHours:false,isHour:false})

			}
		}

	};
	modalOpen = () => {
		this.setState({ input: '', li_id: '', seletedProgramme: undefined ,hours:-1,show_jobHours:false})
		console.log(this.state.programs,'programmesssss',this.state.ProgrammeData)
		const result = this.state.programs.filter(e => !this.state.ProgrammeData.some(a => a.id == e.id));
		this.setState({ programs: result }, () => this.list())
	}
	enrolllearner = async() => {
		

			this.setState({loader:true,hours:-1,isSelect:false,isHour:false})
			console.log(this.state.hours ,'get hourssssssssss')
			let formData = new FormData();
			formData.append('_token', decodeURIComponent(this.state.user_token))
			formData.append('member_id', this.state.apprenticeId)
			formData.append('program_id', this.state.seletedProgramme.id)
			if(this.state.hours !== -1){
	
				formData.append('working_hours', this.state.hours)
			}
			const headers = {
				'Content-Type': 'multipart/form-data',
			}
			headers['Authorization'] = 'Bearer ' + this.state.auth_token
			await apiService.enrollLerner(headers,formData)
			.then((response)=>{
				if(response.data.status === 'Success'){
					Notification(
						"Progeamme Subscribed Successfully",
						"success",
						"Success"
					  );
					this.setState({loader:false},()=>{
						this.getApprenticesDetails()
					})
				}else{
					Notification(
						response.data.data,
						"info",
						"Info"
					  );
					  this.setState({loader:false},()=>{
						this.getApprenticesDetails()
					})
				}
				
				console.log(response)
			})
			.catch((err)=>{
				console.log(err)
					Notification(
						"Something Went Wrong",
						"danger",
						"Warning"
					  );
					  this.setState({loader:false},()=>{
						this.getApprenticesDetails()
					})
			})	
	}
	navigateTo = (id)=>{
		console.log(id)
		// window.location.href = "/myportfolio-admin/admin-apprentices/programme_detailed/" + id;
		return(

		<NavLink className="btn common-course-btn btn-primary" style={{float:'right',marginTop:"-20px",color:"white"}}
        to={`/myportfolio-admin/admin-apprentices/programme_detailed/${id}`}

      >
        View Details
      </NavLink>
		)
	}

	render() {
		if (!mykademyCommonService.isAdmin()) {
			MykademyAdminDropdownContents.one = "";
		}
		myportfolioCommonService.getRolls()

		return (

			<div>

				<div className="filter-bar-block">
					<a className="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
						<div className="bar1"></div>
						<div className="bar2"></div>
						<div className="bar3"></div>
					</a>
					<div className="header-btn-list">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							viewBox="0 0 14 14"
							width="11"
							height="11"
							style={{ enableBackground: "new 0 0 14 14" }}
							xmlSpace="preserve"
						>
							<path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
						</svg>
						<a style={{ cursor: 'pointer' }} onClick={() => this.modalOpen()}
							data-bs-toggle="modal" data-bs-target="#enrollnewprogramme">Enroll New Programme</a>


					</div>

					<div className="dropdown view-option common-option">
						<button className="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
							Add Option
                        </button>
						<ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
							<li>
								<a style={{ cursor: 'pointer' }} onClick={() => this.modalOpen()}
									data-bs-toggle="modal" data-bs-target="#enrollnewprogramme">Enroll New Programme</a>

							</li>
						</ul>
					</div>

				</div>
				<div className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>
					<AdminNavbar class={this.state.class} data={subdropdownData} DropdownData={MykademyAdminDropdownContents} />
					<div className="container-fluid courses-block">
									<>
										<div class="row flex-center" style={{ flexDirection: "unset" }}>
											<h4 class="heading-title">{myportfolioCommonService.getdynamicRolls().Learner} Details</h4>
										</div>
										<div className="common-detail-block">

											<div className="row flex-center" style={{ flexDirection: "unset" }}>
												<div className="col-sm-12 col-md-12 col-lg-4 col-xl-4">
													<div className="common-media">
														<a className="img-wrap details-img-wrap">
															{
																this.getProfileUrl(this.state.userImage_url)
															}

														</a>

														<div className="common-list-wrap">
															<h4>{this.state.useName}</h4>

															<span className="common-list-text">{this.state.userPhone}</span>
															<a className="common-list-text m-0 word-break" href={"mailto:" + this.state.userEmail}>{this.state.userEmail}</a>
														</div>

													</div>
												</div>
												<div className="col-sm-12 col-md-12 col-lg-4 col-xl-5">

													<div >
														{
															this.getCountryname(this.state.userCountry)
														}
													</div>
													<span style={{ padding: '5px' }} className="progress-completed mb-0">{this.state.userEmployer}</span>
												</div>
												<div className="col-sm-12 col-md-12 col-lg-4 col-xl-3">
													<div className="count-wrap">
														<span className="enrolled-count">{this.state.total_count}</span>
														<span className="common-list-text">{myportfolioCommonService.getdynamicRolls().Learner} Programmes</span>
													</div>
												</div>
											</div>
										</div>
									</>
									{
										this.state.loader ? (
											<div className="program-detail-block">
											<div style={{ textAlign: "center" }}>
												<img
													style={{ width: "45px", height: "100vh" }}
													src="/./custom_assets/loader.svg"
													alt="loader"
												/>
											</div>
										</div>
										):(
											<div className="program-detail-block">
											<h4>{this.state.useName}'s Programmes</h4>
											{
												this.state.total_count === 0 ? (
													<section className="m-t-20 results-section results--grid-null-item alert alert-danger">
														<div>No Programme Found!</div>
													</section>
	
												) : (
	
														<div className="row">
															{this.state.ProgrammeData.map(
																(val, i) => {
																	return (
																		<div key={i} className="col-xs-12 col-md-6 col-lg-4 col-xl-4 mb-20">
																			<div className="common-box-list">
																				<div className="img-wrap">
																					{
																						this.getBackground(val.banner_image)
																					}
	
																					<div className="program-logo">
																						{
																							this.getLogo(val.occupation_logo)
																						}
	
																					</div>
																				</div>
																				<div className="detail-text-wrap">
																					<h4>{val.apprenticeship_name}</h4>
																					{
																						this.getProgressBar(val.member_apprenticeship_progress)
																					}
																					{
																						this.navigateTo(val.member_apprenticeship_id)
																					}
                                                                                    {/* <button type="button" className="btn common-course-btn btn-primary" onClick={() => this.navigateTo(val.id)}>Start</button> */}

																				</div>
																			</div>
																		</div>
																	)
																}
															)}
	
	
														</div>
													)
											}
											{/* {this.state.apprenticesData.length === 0 || this.state.apprenticesData.length.toString() === this.state.total_count ? (
											<div className="program-listing__load-more load-more-wrap"></div>
										) : (
											<div className="program-listing__load-more load-more-wrap">
												<button className="olive-flat-button olive-accent" >
													{
														this.state.loadmore_loader ?
															<img style={{ width: '27px' }} alt="loading" src="/./custom_assets/Chasing arrows.gif" />
															:
															<span className="olive-button-wrapper" onClick={this.loadMore}> Load more </span>
													}
												</button>
											</div>
										)} */}
										</div>
											
										)
									}
								
								
					</div>

				</div>
				{/* modal start */}
				<div class="modal fade enrol-course-modal" id="enrollnewprogramme" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
					<div class="modal-dialog">
						<div class="modal-content">
							<div class="modal-header">
								<h5 class="modal-title" id="exampleModalLabel">Enroll New programme</h5>
								<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
							</div>
							<div class="modal-body">
							<label>Occupation Program</label>
							
								<div class="enrol-search">
									<input value={this.state.input}
										onChange={this.onChangeHandler.bind(this)}
										style={this.state.isSelect ? inputErr : {}}
										
										type="text" class="form-control enrol-search-input" placeholder="Search Occupation Program" />
									{this.list()}
									{this.state.isSelect &&
                                <p style={error_style}>Please Enter a Programme</p>
                            }
								</div>
								{
									this.state.show_jobHours &&
								<>
									<div class="enrol-search" style={{ marginTop: '20px' }}>
									<label>Off the Job Working Hours</label>

									<input type="text" class="form-control" placeholder="Job Working Hours"
										onChange={this.onChangeHandlerHours.bind(this)} 
										style={this.state.isHour ? inputErr : {}}
										autoFocus/>
								</div>
								{this.state.isHour &&
                                <p style={error_style}>Please Enter a number</p>
                            }
							</>
								}
							</div>
							<div class="modal-footer">
								<button disabled={this.state.seletedProgramme === undefined || this.state.isHour} type="button" class="btn enroll-now-btn" data-bs-dismiss="modal" onClick={() => this.enrolllearner()}>Enroll Now</button>
							</div>
						</div>
					</div>
				</div>
				{/* modal end */}

			</div>
		)
	}
}
