import React from "react";
import {
  userData,
  getAuthtoken,
  getSubdomain,
} from "../services/common.service";
import AdminNavbar from "../../shared_components/adminNavbar";
import {
  config,
  MykademyAdminDropdownContents,
  subdropdownData,
  MyportfolioAdminDropdownContents,
} from "../../../config";

const mykademyCommonService = require("../../mykademy/services/common.service");
const myportfolioCommonService = require("../services/common.service")
var getConfig = () => {
  let isLegacy = localStorage.getItem('legacy')
  var conf = config
  console.log(conf, 'isLegacy1')
  if (isLegacy !== null) {
    conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO_LEGACY
  } else {
    conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO
  }
  console.log(conf, 'isLegacy2')
  return conf
}
const APP_BASEURL_IFRAME = getConfig().myportfolio.APP_BASEURL_IFRAME;

export default class adminReporting extends React.Component {
  state = {
    finalUrl: "",
    loader: true,
    class: false,
    token: "",
  };

  componentDidMount() {
    if (userData() !== null && getSubdomain() !== null) {
      this.getdata();
    }
  }

  getdata = () => {
    let data = userData().data;
    this.setState(
      {
        loginType: data.loginType,
        token: decodeURIComponent(data._token),
        subdomain: getSubdomain(),
      },
      () => this.getUrl()
    );
  };

  getUrl = () => {
    this.setState({ loader: false });
    let url = `https://${this.state.subdomain}${APP_BASEURL_IFRAME}/${this.state.loginType}/reporting?mst-token=${this.state.token}`;
    this.setState({
      finalUrl: url,
    });
  };

  toggleNavbar = () => {
    this.setState({ class: !this.state.class });
  };

  render() {
    if (!mykademyCommonService.isAdmin()) {
      MykademyAdminDropdownContents.one = "";
    }
    myportfolioCommonService.getRolls()
    // if(myportfolioCommonService.getRolls())
    return (
      <div>
        <div class="filter-bar-block">
          <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
            <div class="bar1"></div>
            <div class="bar2"></div>
            <div class="bar3"></div>
          </a>
          {/* <div class="dropdown view-option common-option">
            <button
              class="btn dropdown-toggle"
              type="button"
              id="dropdownMenuButton1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              Add Option
            </button>
            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
              <li>
                <a class="dropdown-item" href="#">
                  Add Apprentice
                </a>
              </li>
              <li>
                <a class="dropdown-item" href="#">
                  Add Employers
                </a>
              </li>
              <li>
                <a class="dropdown-item" href="#">
                  Add Session
                </a>
              </li>
              <li>
                <a class="dropdown-item" href="#">
                  Add Programme
                </a>
              </li>
            </ul>
          </div> */}
        </div>
        <div className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>
          <AdminNavbar
            class={this.state.class}
            data={subdropdownData}
            DropdownData={MykademyAdminDropdownContents}
          />
          <div class="container-fluid courses-block" style={{ padding: '60px 0px 1px' }}>
            <div>
              {
                this.state.loader ?
                  <div className="row">
                    <div style={{ textAlign: "center" }}>
                      <img
                        style={{ width: "45px", height: "100vh" }}
                        src="custom_assets/loader.svg"
                        alt="loader"
                      />
                    </div>
                  </div> :
                  <iframe src={this.state.finalUrl} title="reporting"
                    style={{ width: '100%', height: '87vh' }}></iframe>
              }
            </div>
          </div>
        </div>

      </div>
    );
  }
}
