import React, { useState, useEffect } from "react"
import Course from "./course";
import Newsfeed from "./newsfeed";
import Portfolio from "./portfolio";
import Profile from "./profile";
import Upcoming from "./upcoming";
import { isAdmin ,isAnyOneAdmin} from '../../myportfolio/services/common.service'
import { userData } from '../../mykademy/services/common.service'

const Draggabilly = require('draggabilly');
const $ = window.$

const Dashboard = (props) => {
    const [isMykademyUserRole, setMykademyUserRole] = useState(false);
    const [isPortfolioAdmin, setPortfolioadmin] = useState(false)

    function updateDimensions() {
        console.log("width1", window.innerWidth)
        initDragDropAnimation()
    }

    function initDragDropAnimation() {
        if (window.innerWidth >= 800) {
            let interval = setTimeout(() => {
                var $grid = $('.grid-drag').packery({
                    itemSelector: '.col-lg-6',
                    columnWidth: $('.grid-drag').width() / 2,
                });
                $grid.packery();
                $grid.find('.col-lg-6').each(function (i, gridItem) {
                    var draggie = new Draggabilly(gridItem);
                    $grid.packery('bindDraggabillyEvents', draggie);
                });
            }, 5000)
        }
    }
    useEffect(() => {
        console.log("width", window.innerWidth)

        initDragDropAnimation()
        window.addEventListener('resize', updateDimensions);

        setPortfolioadmin(!isAnyOneAdmin())
        let mykademyUser = userData()

        if (mykademyUser !== null) {
            if (mykademyUser.user_data)
                if (mykademyUser.user_data.us_role_id === '2')
                    setMykademyUserRole(true)

        }

        return () => {
            //unsubscribe all
            window.removeEventListener('resize', updateDimensions);

        }
    }, [])


    return (
        <div class="container-fluid main-block">
            <div class="row grid-drag">
                <Profile />
                <Newsfeed />
                {isMykademyUserRole &&
                    <Course />
                }
                <Upcoming />
                {
                    isPortfolioAdmin &&
                        <Portfolio /> 
                }
            </div>
        </div>
    )
}

export default Dashboard