import React, { PureComponent } from 'react';
import { appToken } from "../services/common.service";
import * as moment from 'moment'

var Chart = require('chart.js');
const api_service = require("../services/api.service");

export default class HalfDoughNut extends PureComponent {
    state = ({
        inProgress: 0,
        total_completed: 0,
        completed: 0,
        loader: true,
        not_started: 0
    })
    componentDidMount = () => {
        this.halfDoughNutData();
    }
    async halfDoughNutData() {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await api_service
            .getDoughNutData(headers)
            .then((response) => {
                console.log(response.data.data, 'doughnut')
                let halfDoughtNutData = [];
                if (response?.data?.data !== null) {
                    let total_percentage = Math.round((response.data.data.completed * 100) / (response.data.total))
                    console.log(total_percentage)
                    this.setState({
                        inProgress: Math.round((response.data.data.in_progress*100)/(response.data.total)),
                        total_completed: total_percentage,
                        completed: Math.round((response.data.data.completed * 100) / (response.data.total)),
                        not_started: Math.round((response.data.data.not_started*100)/(response.data.total)),
                        loader: false
                    })
                    halfDoughtNutData = [
                        this.state.completed,
                        this.state.inProgress,
                        this.state.not_started
                        
                    ]
                } else {
                    this.setState({
                        loader: false
                    })
                    halfDoughtNutData = [
                        
                        this.state.completed,
                        this.state.inProgress,
                        this.state.not_started
                    ]
                }
                var data = {
                    datasets: [
                        {
                            data: halfDoughtNutData,
                            backgroundColor: [
                                this.state.total_completed===100?"#13A10E":"#6264A7",
                                "#D9DBDB",
                                "#D9DBDB"


                            ],
                            hoverBackgroundColor: [
                                this.state.total_completed===100?"#13A10E":"#6264A7",
                                "#D9DBDB",
                                "#D9DBDB"
                            ],
                            label: 'Expenditures'
                        }],
                    labels: [
                        
                        "Completed",
                        "In Progress",
                        "Not Started"
                        
                    ]
                };

                var ctx = document.getElementById("halfChart").getContext("2d");
                var myDoughnutChart = new Chart(ctx, {
                    type: 'doughnut',
                    data: data,
                    options: {
                        responsive: true,
                        rotation: 1 * Math.PI,
                        circumference: 1 * Math.PI,
                        legend: {
                            display: false
                        },
                        cutoutPercentage: 50,
                        animation: {
                            animateScale: true,
                            animateRotate: true
                        },
                        tooltips: {
                        callbacks: {
                            label: function(tooltipItem, data) {                                     
                                var idx = tooltipItem['index'];                                     
                                var caption = data.labels[idx];                                     
                                var captionValue = data['datasets'][0]['data'][idx];                                     
                                return  caption+':'+captionValue+'%';                                
                             }
                           
                        }
                    }
                        
                        
                    }
                
                });

            })
            .catch((error) => {
                console.log(error);
                this.setState({
                    loader: false
                })
            });
    }
    render() {
        return (
            <div class="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                <div class="course-thumb-list">
                    {this.state.loader ? (
                        <div className="bg-main">
                            <div style={{ textAlign: 'center' }}>
                                <img alt="view more" style={{ width: '45px', height: '40vh' }} src="custom_assets/loader.svg" />
                            </div>
                        </div>
                    ) : (
                        <div class="detail-text-wrap">
                            <div class="course-title">
                                <h4 class="same-height">Total Progress</h4>
                            </div>
                            <div class="half-chat-wrap">
                                <canvas id="halfChart"></canvas>
                                <span class="half-chat-percentage">{this.state.total_completed}%</span>
                                <div class="half-chat-info">
                                    <span class="half-chat-count">0%</span>
                                    <span class="half-chat-progress">Complete</span>
                                    <span class="half-chat-count">100%</span>
                                </div>
                            </div>
                        </div>
                    )}

                </div>
            </div>
        )
    }
}
