import React from "react";
import { appToken } from "../../services/common.service";
import * as moment from 'moment';

var Chart = require('chart.js');
const api_service = require("../../services/api.service");

export default class VcLineChart extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isApiLoading: true
        }
    }
    componentDidMount() {
        this.getEventAnalytics();

    }
    async getEventAnalytics() {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        let params = {
            by_days: "7"
        }
        await api_service
            .eventAnalytics(headers, params)
            .then((response) => {
                this.setState({ isApiLoading: false }, () => {

                    let total_events = [], dates = [], events = ["0", "0", "0", "0", "0", "0", "0"], dateArray = [], label = [];
                    let fromDate = moment(new Date()).format("YYYY-MM-DD");
                    response.data.data.map((val) => {
                        total_events.push(val.total_events)
                        dates.push(val.date)

                    })

                    for (let i = 0; i < params.by_days; i++) {
                        dateArray.push(moment(fromDate, "YYYY-MM-DD").add(i, 'days').format("YYYY-MM-DD"));
                        label.push(moment(fromDate, "YYYY-MM-DD").add(i, 'days').format("MMM D"));
                    }
                    for (let i = 0; i < dateArray.length; i++) {
                        let j = 0;
                        while (j < total_events.length) {

                            if (dateArray[i] === dates[j]) {

                                events[i] = total_events[j]

                            }
                            j++;
                        }

                    }
                    console.log(events,label ,'attendww')

                    var ctx = document.getElementById('myLineChart');
                    var myLineChart = new Chart(ctx, {
                        type: 'line',
                        data: {
                            labels: label,
                            datasets: [{
                                label: 'Number Of Live Classrooms', // Name the series
                                data: events, // Specify the data values array
                                fill: false,
                                borderColor: '#7B83EB', // Add custom color border (Line)
                                backgroundColor: '#7B83EB', // Add custom color background (Points and Fill)
                                borderWidth: 1 // Specify bar border width
                            }]
                        },
                        options: {
                            scales: {
                               
                                yAxes: [{
                                    ticks: {
                                        beginAtZero: true,
                                        callback: function(value) {if (value % 1 === 0) {return value;}}
                                    }
                                }]
                            },

                            responsive: true,
                            maintainAspectRatio: false,
                            tooltips: {
                                enabled: true,
                                mode: 'label',
                                callbacks: {
                                    title: function (tooltipItems, data) {
                                        var idx = tooltipItems[0].index;
                                        return 'Title: ' + data.labels[idx];
                                    },
                                }
                            }
                        }
                    });
                })


            })
            .catch((error) => {
                console.log(error);
                this.setState({ isApiLoading: false })
            });
    }
    render() {
        return (
            <div className="chat-wrap">
                <div className="bar-chat-height" >
                    <div className="chartjs-size-monitor">
                        <div class="chartjs-size-monitor-expand">
                            <div class="">
                            </div>
                        </div>
                        <div class="chartjs-size-monitor-shrink">
                            <div class="">
                            </div>
                        </div>
                    </div>
                    {this.state.isApiLoading &&
                        <div className="step step-3 active">
                            <div className="d-flex justify-content-center align-items-center spinner-content">
                                <div
                                    style={{ marginTop: "45%" }}
                                    className="spinner-border"
                                    role="status"
                                >
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                            </div>
                        </div>
                    }
                    <canvas id="myLineChart" className="chartjs-render-monitor" style={{ display: 'block', color: '#569ff7', width: '473px', height: '300px' }}></canvas>
                </div>

            </div>

        )


    }

}