import React from "react";
import { userData, appToken } from "../services/common.service";
import NotFound from "../../shared_components/notFound";
import { debounce } from "lodash";
import HalfDoughNut from "./halfDoughNut";
import DoughNut from "./doughNut";
import CourseJump from "./courseJump";
import ProgressBar from "./progressBar";
import * as microsoftTeams from "@microsoft/teams-js";

const apiService = require("../services/api.service");

var page_limit = 8, page = 1, search = "", total_courses = 0;

export default class CourseList extends React.Component {

    state = {
        courses: [],
        isLoggedIn: false,
        show_see_more_loader: false,
        emptyData: false,
        show_see_more: false,
        current_date: new Date(),
        result_section: 'results-section results--grid',
        initial_loader: true,
        gridView: true,
        empty_check: true,
        empty_check_search: false,
        search_loader: false
    };

    handleSearch = debounce((text) => {
        this.setState({
            search_loader: true, initial_loader: false
        })
        search = text.trim();
        if (search === "") {
            page_limit = 8;
            page = 1;
            this.setState({ courses: [], show_see_more: false, initial_loader: false, search_loader: true, empty_check_search: false });
            this.courseLIst();
        } else {
            this.setState({
                courses: [],
                show_see_more: true,
                show_see_more_loader: false,
            });
            page_limit = 0;
            page = 0;
            this.courseSearch();
        }
    }, 500);

    componentDidMount() {
        if (userData() !== null) {
            // microsoftTeams.initialize();
            // microsoftTeams.getContext((context) => {
            //     console.log("contexttttttttttttt", context);
            //     let course_id = context.subEntityId;
            //     // this.setState({ course_id: course_id })
            //     console.log("course_id", course_id);
            //     console.log("context.subEntityId", context["subEntityId"]);
            //     if (context.subEntityId) {
            //         this.navigateTo(context.subEntityId);
            //     }
            // });
            let course_id = localStorage.getItem('course_id')
            let event_link = localStorage.getItem('event_link')
            if (course_id !== null) {
                this.navigateTo(course_id);
            } else if (event_link !== null) {
                window.location.href = "course-detail/event"
            }

            this.setState({ isLoggedIn: true });
            this.courseLIst();
        } else this.setState({ isLoggedIn: false, initial_loader: false });
    }


    checkArrayEmpty(val) {
        if (val === 0) {
            this.setState({ emptyData: true });
        }
        if (page_limit > total_courses) {

            this.setState({ show_see_more: true });    // hiding load more

        }
    }


    async courseLIst() {
        //this.setState({ initial_loader: true })
        this.setState({ emptyData: false });
        const headers = {
            "Content-Type": "application/json",
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await apiService
            .listCourse(headers, page_limit, page, search)
            .then((response) => {
                if (response?.data?.data?.length !== 0) {
                    this.setState({ initial_loader: false, search_loader: false, empty_check: false })
                    total_courses = response.data.total;
                    if (this.state.show_see_more_loader !== "hide") {
                        this.setState({ show_see_more_loader: false });
                    }
                    this.checkArrayEmpty(response.data.total);
                    var joinedResponse = this.state.courses.concat(response.data.data);
                    this.setState({ courses: joinedResponse });

                } else {
                    this.setState({ initial_loader: false, empty_check: true, search_loader: false })
                }
            })
            .catch((error) => {
                this.setState({ initial_loader: false })
                this.setState({ courses: [], show_see_more_loader: false });
            });
    }

    navigateTo(course_id) {
        window.location.href = "course-detail/" + course_id;
    }

    loadMore = () => {
        if (page < Math.ceil(total_courses / page_limit)) {
            this.setState({ show_see_more_loader: true });
            page = page + 1;
        }
        if (page === Math.ceil(total_courses / page_limit)) {
            this.setState({ show_see_more_loader: true });
            setTimeout(() => {
                this.setState({ show_see_more: true });
            }, 1000);
        }
        this.courseLIst();
    };

    async courseSearch() {
        this.setState({ emptyData: false, search_loader: true });
        const headers = {
            "Content-Type": "application/json",
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }

        await apiService
            .listCourse(headers, page_limit, page, search)
            .then((response) => {
                if (response.data.data.length !== 0) {
                    total_courses = response.data.total;
                    this.checkArrayEmpty(response.data.total);
                    this.setState({
                        courses: response.data.data,
                        show_see_more_loader: false,
                        search_loader: false,
                        empty_check_search: false
                    });
                } else {
                    this.setState({
                        search_loader: false,
                        empty_check_search: true
                    });
                }
            })
            .catch((error) => {
                this.setState({ courses: [], show_see_more_loader: false });
            });
    }

    checkExpiredDate(val) {

        let end_date = new Date(val.cs_end_date +
            " " + new Date(val.cs_subscription_date).getHours() +
            ":" + new Date(val.cs_subscription_date).getMinutes() +
            ":" + new Date(val.cs_subscription_date).getSeconds()
        )
        if (end_date < this.state.current_date) {
            return (
                <button style={{ background: 'none', color: '#979593' }} className="btn btn-default block" title="Expired">
                    View Course
                </button>
            );
        } else {
            return (
                <button
                    onClick={() => this.navigateTo(val.course_id)}
                    className="btn btn-default"
                    style={{ background: 'none', color: '#00acf1', border: 'none' }}
                >
                    View Course
                </button>
            );
        }
    }

    gotResponse(val) {
        if (val === 'success')
            this.componentDidMount();
    }
    gridToList = () => {
        this.setState({
            gridView: true
        })
    }
    listToGrid = () => {
        this.setState({
            gridView: false
        })
    }
    render() {
        if (this.state.initial_loader)
            return <div className="bg-main">
                <div style={{ textAlign: 'center' }}>
                    <img alt="view more" style={{ width: '45px', height: '100vh' }} src="custom_assets/loader.svg" />
                </div>
            </div>
        else
            return (
                <div>
                    {!this.state.isLoggedIn ? (
                        <NotFound domain="mykademy" response={(val) => this.gotResponse(val)} message="Sorry, you need to login to MyKademy LMS to access your Courses" />
                    ) : (
                            <>
                                {!this.state.empty_check ? (
                                    <>
                                        <div className="filter-bar-block">

                                            <div className="filter-bar-holder">
                                                <div className="grid-wrap">
                                                    <button className={this.state.gridView ? 'grid-color btn-grid' : 'btn-grid'} onClick={this.gridToList} >
                                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" width="16" height="16" >
                                                            <path d="M5.5,9h-4C0.7,9,0,9.7,0,10.5v4C0,15.3,0.7,16,1.5,16h4C6.3,16,7,15.3,7,14.5v-4C7,9.7,6.3,9,5.5,9z M6,14.5C6,14.8,5.8,15,5.5,15h-4C1.2,15,1,14.8,1,14.5v-4C1,10.2,1.2,10,1.5,10h4C5.8,10,6,10.2,6,10.5V14.5z" />
                                                            <path d="M5.5,0h-4C0.7,0,0,0.7,0,1.5v4C0,6.3,0.7,7,1.5,7h4C6.3,7,7,6.3,7,5.5v-4C7,0.7,6.3,0,5.5,0z M6,5.5C6,5.8,5.8,6,5.5,6h-4C1.2,6,1,5.8,1,5.5v-4C1,1.2,1.2,1,1.5,1h4C5.8,1,6,1.2,6,1.5V5.5z" />
                                                            <path d="M14.5,0h-4C9.7,0,9,0.7,9,1.5v4C9,6.3,9.7,7,10.5,7h4C15.3,7,16,6.3,16,5.5v-4C16,0.7,15.3,0,14.5,0z M15,5.5C15,5.8,14.8,6,14.5,6h-4C10.2,6,10,5.8,10,5.5v-4C10,1.2,10.2,1,10.5,1h4C14.8,1,15,1.2,15,1.5V5.5z" />
                                                            <path d="M14.5,9h-4C9.7,9,9,9.7,9,10.5v4c0,0.8,0.7,1.5,1.5,1.5h4c0.8,0,1.5-0.7,1.5-1.5v-4C16,9.7,15.3,9,14.5,9z M15,14.5c0,0.3-0.2,0.5-0.5,0.5h-4c-0.3,0-0.5-0.2-0.5-0.5v-4c0-0.3,0.2-0.5,0.5-0.5h4c0.3,0,0.5,0.2,0.5,0.5V14.5z" />
                                                        </svg>
                                                    </button>
                                                    <button className={!this.state.gridView ? 'grid-color btn-grid' : 'btn-grid'} onClick={this.listToGrid}>
                                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 13.1" width="16" height="16" >
                                                            <path d="M0.8,1.5h10.4l0.1,0c0.4,0,0.6-0.4,0.6-0.7c0-0.4-0.3-0.8-0.8-0.8H0.8L0.6,0C0.3,0.1,0,0.4,0,0.8C0,1.2,0.3,1.5,0.8,1.5z" />
                                                            <path d="M15.2,6H0.8l-0.1,0C0.3,6.1,0,6.4,0,6.8c0,0.4,0.3,0.8,0.8,0.8h14.5l0.1,0c0.4,0,0.6-0.4,0.6-0.7C16,6.4,15.6,6,15.2,6z" />
                                                            <path d="M10.2,11.6H0.8l-0.1,0c-0.4,0-0.6,0.4-0.6,0.7c0,0.4,0.3,0.8,0.8,0.8h9.4l0.1,0c0.4,0,0.6-0.4,0.6-0.7C10.9,11.9,10.6,11.6,10.2,11.6z" />
                                                        </svg>
                                                    </button>
                                                </div>

                                                <div className="input-group flex-nowrap filter-search">
                                                    <input type="text" className="form-control" placeholder="Find" onChange={(e) => this.handleSearch(e.target.value)} aria-label="Find" aria-describedby="addon-wrapping" />
                                                    <span className="input-group-text" id="addon-wrapping">
                                                        <img src="assets/images/landing/search.svg" alt="search" />
                                                    </span>
                                                </div>

                                                <a className="mobile-search-icon" href="#"><img src="assets/images/landing/mobile-search.svg" alt="search" /></a>
                                            </div>
                                        </div>

                                        <div className="container-fluid courses-block">
                                            <div className="row mb-20">
                                                <HalfDoughNut />
                                                <DoughNut />
                                                <ProgressBar />
                                                <CourseJump />
                                            </div>
                                            {!this.state.search_loader ? (
                                                <>
                                                    {!this.state.empty_check_search ? (
                                                        <>
                                                            {this.state.gridView ?
                                                                (
                                                                    <div className="row">
                                                                        {this.state.courses.map((val, i) => {
                                                                            return (
                                                                                <div key={i} className="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">
                                                                                    <div className="course-thumb-list">
                                                                                        <div className="img-wrap">
                                                                                            <img src={val?.cb_image} alt="thumb images" />
                                                                                        </div>
                                                                                        <div className="detail-text-wrap">
                                                                                            <div className="course-title">
                                                                                                <h4 className="same-height">{val?.cb_title}</h4>
                                                                                                {/* <a href="#" className="three-dots-dropdown">
                                                                                <img src="assets/images/landing/three-dots.svg" alt="dot" />
                                                                            </a> */}
                                                                                            </div>
                                                                                            {parseInt(val?.cs_percentage) === 100 ? (
                                                                                                <>
                                                                                                    <span className="progress-completed">Progress: {val?.cs_percentage}%</span>
                                                                                                    <div className="progress common-progress">
                                                                                                        <div className="progress-bar progress-bar-success" role="progressbar" style={{ width: val?.cs_percentage + '%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                                                                    </div>
                                                                                                    <div className="course-thumb-btn-wrap">
                                                                                                        {/* <button type="button" className="btn common-course-btn btn-primary">Certificate</button> */}
                                                                                                        <button type="button" className="btn common-course-btn btn-secondary" onClick={() => this.navigateTo(val.course_id)}>Review</button>
                                                                                                        <span className="course-status">Passed</span>
                                                                                                    </div>
                                                                                                </>
                                                                                            ) : (
                                                                                                    <>
                                                                                                        <span className="progress-completed">Progress:  {val?.cs_percentage}%</span>
                                                                                                        <div className="progress common-progress">
                                                                                                            <div className="progress-bar progress-bar-primary" role="progressbar" style={{ width: val?.cs_percentage + '%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                                                                        </div>
                                                                                                        <div className="course-thumb-btn-wrap">
                                                                                                            <button type="button" className="btn common-course-btn btn-primary" onClick={() => this.navigateTo(val.course_id)}>{parseInt(val?.cs_percentage) === 0 ? 'Start' : 'Resume'}</button>
                                                                                                            <span className="course-status">{parseInt(val?.cs_percentage) === 0 ? 'Not Started' : 'In Progress'}</span>
                                                                                                        </div>
                                                                                                    </>
                                                                                                )}


                                                                                        </div>
                                                                                    </div>

                                                                                </div>
                                                                            );
                                                                        })}
                                                                        {!this.state.show_see_more &&
                                                                            !this.state.emptyData &&
                                                                            this.state.courses.length !== 0 && (
                                                                                <div className="program-listing__load-more">
                                                                                    {this.state.show_see_more_loader ? (
                                                                                        <div className="load-more-loader"></div>
                                                                                    ) : (
                                                                                            <button
                                                                                                onClick={() => this.loadMore()}
                                                                                                className="olive-flat-button olive-accent"
                                                                                            >
                                                                                                <span className="olive-button-wrapper">
                                                                                                    Load more
                                                                </span>
                                                                                            </button>
                                                                                        )}
                                                                                </div>
                                                                            )}
                                                                    </div>
                                                                ) : (
                                                                    <div className="course-list-block">
                                                                        <h4>Courses</h4>
                                                                        <div className="table-responsive sessions-table-list">
                                                                            <table className="table">
                                                                                <thead>
                                                                                    <tr>
                                                                                        <th scope="col">Name</th>
                                                                                        <th width="300" scope="col">Progress</th>
                                                                                        <th width="90" scope="col">Status</th>
                                                                                        <th width="240" scope="col"></th>
                                                                                    </tr>
                                                                                </thead>
                                                                                <tbody>
                                                                                    {this.state.courses.map((val, i) => {
                                                                                        return (
                                                                                            <tr key={i}>
                                                                                                <td scope="row">
                                                                                                    <div className="info-wrap">
                                                                                                        <img src={val?.cb_image} alt="" />
                                                                                                        {val?.cb_title}
                                                                                                    </div>
                                                                                                </td>
                                                                                                <td>
                                                                                                    <div className="custom-progress-wrap">
                                                                                                        <div className="progress-holder">
                                                                                                            <div className="progress common-progress">
                                                                                                                <div className="progress-bar progress-bar-primary" role="progressbar" style={{ width: parseInt(val?.cs_percentage) + '%' }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                                                                                                            </div>
                                                                                                            <div className="progress-value">
                                                                                                                {parseInt(val?.cs_percentage)}%
                                                                                                            </div>
                                                                                                        </div>
                                                                                                    </div>
                                                                                                </td>
                                                                                                {parseInt(val?.cs_percentage) === 100 ? (
                                                                                                    <>
                                                                                                        <td><div className="info-wrap">Passed</div></td>
                                                                                                        <td>
                                                                                                            <div className="course-thumb-btn-wrap">
                                                                                                                <button type="button" className="btn common-course-btn btn-secondary" onClick={() => this.navigateTo(val.course_id)}>Review</button>
                                                                                                                {/* <button type="button" className="btn common-course-btn btn-primary">Certificate</button> */}
                                                                                                                {/* <a href="#" className="three-dots-dropdown">
                                                                                                <img src="assets/images/landing/three-dots.svg" alt="dot" />
                                                                                            </a> */}
                                                                                                            </div>
                                                                                                        </td></>
                                                                                                ) : (
                                                                                                        <>
                                                                                                            <td><div className="info-wrap">{parseInt(val?.cs_percentage) === 0 ? 'Not Started' : 'In Progress'}</div></td>
                                                                                                            <td>
                                                                                                                <div className="course-thumb-btn-wrap">
                                                                                                                    <button type="button" className="btn common-course-btn btn-primary" onClick={() => this.navigateTo(val.course_id)}>{parseInt(val?.cs_percentage) === 0 ? 'Start' : 'Resume'}</button>
                                                                                                                    {/* <a href="#" className="three-dots-dropdown">
                                                                                                <img src="assets/images/landing/three-dots.svg" alt="dot" />
                                                                                            </a> */}
                                                                                                                </div>
                                                                                                            </td>
                                                                                                        </>
                                                                                                    )}

                                                                                            </tr>
                                                                                        )
                                                                                    })
                                                                                    }
                                                                                </tbody>
                                                                            </table>
                                                                        </div>
                                                                        {!this.state.show_see_more &&
                                                                            !this.state.emptyData &&
                                                                            this.state.courses.length !== 0 && (
                                                                                <div className="program-listing__load-more">
                                                                                    {this.state.show_see_more_loader ? (
                                                                                        <div className="load-more-loader"></div>
                                                                                    ) : (
                                                                                            <button
                                                                                                onClick={() => this.loadMore()}
                                                                                                className="olive-flat-button olive-accent"
                                                                                            >
                                                                                                <span className="olive-button-wrapper">
                                                                                                    Load more
                                                                </span>
                                                                                            </button>
                                                                                        )}
                                                                                </div>
                                                                            )}

                                                                    </div>
                                                                )}
                                                        </>
                                                    ) : (

                                                            <div className="empty-course">
                                                                <div className="img-wrap">
                                                                    <img src="assets/images/landing/courses-img.png" alt="courses" />
                                                                </div>

                                                            </div>

                                                        )}
                                                </>
                                            ) : (
                                                    <div className="bg-main">
                                                        <div style={{ textAlign: 'center' }}>
                                                            <img alt="view more" style={{ width: '45px', height: '50vh' }} src="custom_assets/loader.svg" />
                                                        </div>
                                                    </div>
                                                )}
                                        </div>
                                    </>
                                ) : (
                                        <div class="empty-block">
                                            <div class="img-wrap">
                                                <img src="assets/images/landing/courses-img.png" alt="courses" />
                                            </div>
                                            <h4>At the moment, you don’t have any enrolments</h4>
                                            <p>Please speak with your administrator to start learning.</p>
                                        </div>
                                    )}

                            </>
                        )}
                </div>
            );
    }
}