import React, { PureComponent } from 'react'
import { appToken } from "../services/common.service";

const api_service = require("../services/api.service");
export default class CourseJump extends PureComponent {
    state = ({
        recent_courses: [],
        loader: true
    })
    componentDidMount = () => {
        this.jumpBackIn();
    }
    async jumpBackIn() {
        const headers = {
            "Authorization": appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await api_service
            .jumpBackInCourses(headers)
            .then((response) => {
                if (response?.data?.data !== null) {
                    this.setState({
                        recent_courses: response.data.data.slice(0,3),
                        loader: false
                    })
                } else {
                    this.setState({

                        loader: false
                    })
                }


            })
            .catch((error) => {
                console.log(error);
                this.setState({
                    loader: false
                })
            });
    }
    navigateTo(course_id) {
        window.location.href = "course-detail/" + course_id;
    }
    render() {
        return (

            <div class="col-xs-12 col-sm-6 col-md-6 col-lg-4 col-xl-3 mb-20">

                <div class="course-thumb-list">
                    {this.state.loader ? (
                        <div className="bg-main">
                            <div style={{ textAlign: 'center' }}>
                                <img alt="view more" style={{ width: '45px', height: '40vh' }} src="custom_assets/loader.svg" />
                            </div>
                        </div>
                    ) : (
                        <div class="detail-text-wrap">
                            <div class="course-title">
                                <h4 class="same-height">Jump back in</h4>
                            </div>
                            {this.state.recent_courses.map((val, i) => {
                                return (
                                    <div key={i} class="course-media-list">
                                        <div class="media-wrap">
                                            <div class="svg-wrap">
                                                <svg width="12" height="12" viewBox="0 0 12 12" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M8.5 6C8.5 6.18008 8.40317 6.34624 8.24651 6.43502L5.61976 7.92351C5.11978 8.20683 4.5 7.84566 4.5 7.27099V4.72907C4.5 4.1544 5.11977 3.79323 5.61975 4.07654L8.2465 5.56499C8.40317 5.65377 8.5 5.81993 8.5 6ZM6 0C2.68629 0 0 2.68629 0 6C0 9.31371 2.68629 12 6 12C9.31371 12 12 9.31371 12 6C12 2.68629 9.31371 0 6 0ZM1 6C1 3.23858 3.23858 1 6 1C8.76142 1 11 3.23858 11 6C11 8.76142 8.76142 11 6 11C3.23858 11 1 8.76142 1 6Z" />
                                                </svg>
                                            </div>
                                            <p>{val.cb_title}</p>
                                        </div>
                                        <a type="button" className="view-btn" onClick={() => this.navigateTo(val.course_id)}>{parseInt(val?.cs_percentage) === 0 ? 'Start' : 'Resume'}</a>
                                    </div>
                                )
                            })
                            }


                        </div>
                    )}
                </div>
            </div>
        )
    }
}
