import React, { Component } from 'react';
import Dashboard from './shared_components/dashboard/index';
import { userData } from "./mykademy/services/common.service";
import { portfolioUser } from './myportfolio/services/common.service';
import NotFound from './shared_components/notFound';

class HomeDashboard extends Component {

    state = {
        isLoggedIn: false
    }

    componentDidMount() {
        if (userData() !== null || portfolioUser() !== null) {
            this.setState({ isLoggedIn: true })
        } else {
            this.setState({ isLoggedIn: false })
        }
    }
    render() {
        return (
            <div>
                {this.state.isLoggedIn ?
                    <Dashboard></Dashboard>
                    :
                    <NotFound />
                }
            </div>
        );
    }
}

export default HomeDashboard;