const axios = require("axios");

var getConfig = () => {
  let isLegacy = localStorage.getItem('legacy')
  var conf = config
  console.log(conf, 'isLegacy1')
  if (isLegacy !== null) {
    conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO_LEGACY
    conf.myportfolio.API_URL = process.env.REACT_APP_MYPORTFOLIO_API_URL_LEGACY
    conf.myportfolio.STATIC_CLIENT_SECRET = process.env.REACT_APP_MYPORTFOLIO_STATIC_CLIENT_SECRET_LEGACY
    conf.myportfolio.STATIC_TOKEN = process.env.REACT_APP_MYPORTFOLIO_STATIC_TOKEN_LEGACY
    conf.myportfolio.NEWSFEED_CLIENT_ID = process.env.REACT_APP_MYPORTFOLIO_NEWSFEED_CLIENT_ID_LEGACY
    conf.myportfolio.NEWSFEED_CLIENT_SECRET = process.env.REACT_APP_MYPORTFOLIO_NEWSFEED_CLIENT_SECRET_LEGACY
    conf.myportfolio.REACT_APP_NEWSFEED_API_URL = process.env.REACT_APP_NEWSFEED_API_URL_LEGACY
    conf.myportfolio.REACT_APP_PORTFOLIO_AUTH_STATIC_CLIENT_ID = process.env.REACT_APP_PORTFOLIO_AUTH_STATIC_CLIENT_ID_LEGACY
    conf.myportfolio.REACT_APP_PORTFOLIO_GRANT_TYPE = process.env.REACT_APP_PORTFOLIO_GRANT_TYPE_LEGACY

    conf.index.TENANTID = process.env.REACT_APP_TENANTID_LEGACY
  } else {
    conf.myportfolio.APP_BASEURL_IFRAME = process.env.REACT_APP_BASEURL_IFRAME_MYPORTFOLIO
    conf.myportfolio.API_URL = process.env.REACT_APP_MYPORTFOLIO_API_URL
    conf.myportfolio.STATIC_CLIENT_SECRET = process.env.REACT_APP_MYPORTFOLIO_STATIC_CLIENT_SECRET
    conf.myportfolio.STATIC_TOKEN = process.env.REACT_APP_MYPORTFOLIO_STATIC_TOKEN
    conf.myportfolio.NEWSFEED_CLIENT_ID = process.env.REACT_APP_MYPORTFOLIO_NEWSFEED_CLIENT_ID
    conf.myportfolio.NEWSFEED_CLIENT_SECRET = process.env.REACT_APP_MYPORTFOLIO_NEWSFEED_CLIENT_SECRET
    conf.myportfolio.REACT_APP_NEWSFEED_API_URL = process.env.REACT_APP_NEWSFEED_API_URL
    conf.myportfolio.REACT_APP_PORTFOLIO_AUTH_STATIC_CLIENT_ID = process.env.REACT_APP_PORTFOLIO_AUTH_STATIC_CLIENT_ID
    conf.myportfolio.REACT_APP_PORTFOLIO_GRANT_TYPE = process.env.REACT_APP_PORTFOLIO_GRANT_TYPE

    conf.index.TENANTID = process.env.REACT_APP_TENANTID
  }
  console.log(conf, 'isLegacy2')
  return conf
}

