export const generatePortfolioStaticToken = async () => {
  let formData = new FormData();
  formData.append("grant_type", getConfig().myportfolio.REACT_APP_PORTFOLIO_GRANT_TYPE);
  formData.append("client_id", getConfig().myportfolio.REACT_APP_PORTFOLIO_AUTH_STATIC_CLIENT_ID);
  formData.append("client_secret", getConfig().myportfolio.STATIC_CLIENT_SECRET);
  const headers = {
    "Content-Type": "multipart/form-data",
    Authorization: "Bearer " + getConfig().myportfolio.STATIC_TOKEN,
  };
  await axios
    .post(getConfig().myportfolio.API_URL + "/oauth/token", formData, {
      headers: headers,
    })
    .then((response) => {
      localStorage.setItem("portfolio_authtoken", JSON.stringify(response.data.access_token));
    })
    .catch((error) => {
      console.log(error);
    });
}

