import React, { Component } from 'react';
import AdminNavbar from "./adminNavbar";
import { subdropdownData, MyportfolioAdminDropdownContents, } from "../../config";
import { userData, appToken } from "../mykademy/services/common.service";
import { NavLink } from "react-router-dom";
import { Notification } from "../../services/common.service";
import { assignWith } from 'lodash';

const contrydata = require('../../services/countries_with_phone');
const mykademyApiservice = require('../mykademy/services/api.service');
const emailApi = require('../../services/common_api.service')
const myportfolioCommonService = require("../myportfolio/services/common.service");
const myportfolioApiService = require('../myportfolio/services/api.service')
const commonApiService = require('../../services/common_api.service')

var organisation_logo = ''

let error_style = {
    paddingLeft: '13px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: '1.33',
    letterSpacing: 'normal',
    textAlign: 'left',
    color: '#c4314b',
}
let inputErr = {
    border: ' solid 1px #c4314b',
    marginBottom: '5px',
    background: 'none',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'right calc(.375em + .1875rem) center',
    backgroundSize: 'calc(.75em + .375rem) calc(.75em + .375rem)'
}
class AdminCommonCreateTutor extends Component {
    state = {
        class: false,
        url: '',
        countries: contrydata.contries_with_phone_code,
        fields: {
            learner_image: null,
            learner_Fname: null,
            learner_phone: null,
            country_name: null,
            learner_email: null,
            learner_password: null,
            learner_Lname: null
        },
        errors: {},
        form_data: {},
        apiLoader: false,
        org_id:'',
        mk_member_id:'',
        mp_member_id:'',
        domain:''


    }

    componentDidMount = () => {
        let myportfoliouser = myportfolioCommonService.userData()
        if (userData() !== null) {
            this.setState({ isLoggedIn: true });
            if (myportfoliouser !== null) {
                organisation_logo = myportfoliouser.data.member_details.organisation_details?.branding_logo_url
            }
            this.getOrg()
        }
    }
    toggleNavbar = () => {
        this.setState({ class: !this.state.class });
    };
    getOrg = async()=>{
        if(localStorage.getItem('subdomain') !== null){
            let domain = localStorage.getItem('subdomain')
            let data = {
                domain: domain
              }
              console.log(data, 'organisation data')
              await commonApiService.getOrg(data)
                .then((response) => {
          
          
                  if (response.data.status === "success") {
                      console.log(response.data)
                    this.setState({ org_id: response.data.response._id,domain:domain })
                  } if (response.data.status === "error") {
                    alert('organisation not found')
                   
                  }
                })
                .catch((err) => {
          
                  console.log(err, 'err getttttt')
                })
        }
    }
    validateFields = () => {
        let fields = this.state.form_data;
        let errors = this.state.errors;
        let formIsValid = true;
        if (!fields["learner_image"]) {
            formIsValid = false;
            errors["learner_image"] = "please selete image";
        }
        if (!fields["learner_Fname"]) {
            formIsValid = false;
            errors["learner_Fname"] = "First name cannot be empty";
        }
        if (!fields["learner_Lname"]) {
            formIsValid = false;
            errors["learner_Lname"] = "Last name cannot be empty";
        }
        if (!fields["learner_phone"]) {
            formIsValid = false;
            errors["learner_phone"] = " Phone Number cannot be empty";
        }

        if (typeof fields["learner_phone"] !== "undefined") {
            if (isNaN(fields["learner_phone"])) {
                formIsValid = false;
                errors["learner_phone"] = "Enter a valid Phone Number";
            }
            if (!fields["learner_phone"].match(/^[0-9]{9,12}$/)) {
                formIsValid = false;
                errors["learner_phone"] = "Enter the number In between 9 to 12 digits"
            }
        }

        if (!fields["country_name"]) {
            formIsValid = false;
            errors["country_name"] = "Country cannot be empty";
        }



        if (!fields["learner_password"]) {
            formIsValid = false;
            errors["learner_password"] = "password cannot be empty";
        } else if (!fields['learner_password'].match(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/)) {
            formIsValid = false;
            errors['learner_password'] = 'Password must contain 8 characters with at least a lowercase, uppercase ,number and a special character'
        }
        if (!fields["learner_email"]) {
            formIsValid = false;
            errors["learner_email"] = "Email cannot be empty";
        }
        if (typeof fields["learner_email"] !== "undefined") {
            let lastAtPos = fields["learner_email"].lastIndexOf("@");
            let lastDotPos = fields["learner_email"].lastIndexOf(".");
            if (
                !(
                    lastAtPos < lastDotPos &&
                    lastAtPos > 0 &&
                    fields["learner_email"].indexOf("@@") === -1 &&
                    lastDotPos > 2 &&
                    fields["learner_email"].length - lastDotPos > 2
                )
            ) {
                formIsValid = false;
                errors["learner_email"] = "Enter a valid Email";
            }
        }
        // if (showErrMessage) {
        this.setState({ errors: errors });
        // }
        console.log(formIsValid, this.state.errors, 'get errrorsssssssssss')
        return formIsValid;
    };
    inputhandle = (field, e) => {
        e.preventDefault();
        if (field === "country_name") {
            const country_name = e.target.value.split(' (')[0];
            const seletedCountry = this.state.countries.find(
                (u) => u.name === country_name
                );
                
                console.log(country_name,'gettttttttt')
            this.setState({
                seletedCountry: seletedCountry,
            });
        }

        let fields = this.state.form_data;
        let errors = this.state.errors;
        errors[field] = ''
        fields[field] = e.target.value;
        this.setState({ fields, errors });


    };
    fileChange(e) {
        let fields = this.state.form_data;
        var reader = new FileReader();
        reader.onload = (e) => {
            console.log(e.target.result,'imageeee')
            fields['learner_image'] = e.target.result;
            document.getElementById('imagePreview').style.backgroundImage = 'url(' + e.target.result + ')'

            this.setState({ fields });
        };
        reader.readAsDataURL(e.target.files[0]);
        reader.onerror = (error) => {
            Notification("Error in Image upload!", "warning", "Warning")
            console.log('Error: ', error);
        };
    }
    formSubmit = async (e) => {
        e.preventDefault();
        const isValid = this.validateFields(true);
        console.log(this.state.org_id,this.state.domain)
        if (isValid) {
            this.setState({ apiLoader: true })

            let mykademyTutorsignup = false
            let myportfolioTutorSignup = false


            mykademyTutorsignup = await this.mykademySignup()
            myportfolioTutorSignup = await this.myportfolioSignup()
            if(myportfolioTutorSignup || mykademyTutorsignup){
                await this.saveTutor()
                if (mykademyTutorsignup && myportfolioTutorSignup) {
                    this.setState({ apiLoader: false })
                    await this.sendMail()
                    Notification("Vle Tutor creation successfully competed", "success", "Success")
                    // window.location.href = this.props.RedirectTo
    
                } else if (myportfolioTutorSignup) {
                    this.setState({ apiLoader: false })
                    Notification("Mykademy tutor creation failed !", "warning", "Warning")
                    window.location.href = this.props.RedirectTo
                    this.setState({ apiLoader: false })
    
                } else {
                    this.setState({ apiLoader: false })
                    Notification("Myportfolio tutor creation failed !", "warning", "Warning")
                    window.location.href = this.props.RedirectTo
                    this.setState({ apiLoader: false })
    
                }
                
            }else{
                this.setState({ apiLoader: false })

                Notification(" Tutor creation failed !", "warning", "Warning")

            }


        } else {

            Notification("Please fill all the required field!", "warning", "Warning")
        }

    }
    mykademySignup = async () => {
        const headers = {
            "Authorization": appToken(),
        };
        headers["Access-token"] = userData().access_token;
        let data = {
            name: this.state.fields.learner_Fname + " " + this.state.fields.learner_Lname,
            country: this.state.seletedCountry.code,
            phone: this.state.fields.learner_phone,
            email: this.state.fields.learner_email,
            phone_country_code: this.state.seletedCountry.dial_code,
            password: this.state.fields.learner_password,
            image: this.state.fields.learner_image

        }
        return await mykademyApiservice.createTutor(headers, data)
            .then((response) => {
                if (response.data.status === 200) {
                    this.setState({ mk_member_id: response.data.data.learner_id })
                    return true
                } else {
                    Notification(response.data.message, "warning", "Warning")
                    return false


                }
            })
            .catch((err) => {
                console.log(err)
            })
        console.log(data, 'data')
    }
    myportfolioSignup = async () => {
        console.log(this.state.fields.learner_image)
        let myportfoliouser = myportfolioCommonService.userData()
        let accessTonen = myportfoliouser.data._token;
        let Authorization_token = myportfolioCommonService.getAuthtoken()
        let formData = new FormData();
        formData.append('_token', decodeURIComponent(accessTonen))
        formData.append('first_name', this.state.fields.learner_Fname)
        formData.append('last_name', this.state.fields.learner_Lname)
        formData.append('email', this.state.fields.learner_email)
        formData.append('password', this.state.fields.learner_password)
        formData.append('image', this.state.fields.learner_image.split(',')[1])
        formData.append('is_admin', "1")
        formData.append('country_code', this.state.seletedCountry.dial_code)
        formData.append('mobile_number',this.state.fields.learner_phone)




        const headers = {
            'Content-Type': 'multipart/form-data',
        }
        headers['Authorization'] = 'Bearer ' + Authorization_token
        return await myportfolioApiService.createtutor(headers, formData)
            .then((response) => {
                if (response.data.status === "Success") {
                    this.setState({mp_member_id:response.data.member_details.id})
                    return true
                } else {
                    return false
                }
                console.log(response)
            })
            .catch((err) => {
                console.log(err)
            })

    }
    sendMail = async () => {
        let data_param = {
            "mk_user_id": this.state.mk_member_id.toString(),
            "mp_user_id": this.state.mp_member_id.toString(),
            "email": this.state.fields.learner_email,
            "email_body": "You have been successfully registered in OLIVE VLE as a Learner.",
            "email_subject": "Registration Successfull",
            "domain": localStorage.getItem('subdomain'),
            "password": this.state.fields.learner_password,
            // "logo_url": organisation_logo ? organisation_logo : "https://d2xduyqs25ssfe.cloudfront.net/uploads/default/user/default.jpg"
        }
        if (organisation_logo) {
            data_param["logo_url"] = organisation_logo
        }
        //not getting any image url from mk or mp
        await emailApi.sendMailToUser(data_param)
            .then((response) => {
                this.setState({ apiLoader: false })
                window.location.href = this.props.RedirectTo
                if(response.data.status === "success"){
                    // Notification("Email send to Vle Tutor successfully ", "success", "Success")

                }else{
                // Notification("Email send to Vle Tutor failed !", "warning", "Warning")

                }
                console.log(response.data, 'email response')
            })
            .catch((err) => {
                Notification("Email send to Vle Tutor failed !", "warning", "Warning")

                console.log(err)
            })
    }
    saveTutor = async()=>{
        let data = {
            account_id: this.state.org_id.toString(),
            mk_member_id: this.state.mk_member_id.toString(),
            mp_member_id: this.state.mp_member_id.toString(),
            domain: this.state.domain,
            role: 'vle_tutor'
          }
          await commonApiService.saveMember(data)
            .then((response) => {

                if(response.data.status == 'success'){
                    // window.location.href = this.props.RedirectTo
                }else{
            Notification("Tutor not saved in vle database!", "warning", "Warning")

                    // window.location.href = this.props.RedirectTo

                }
            })
            .catch((err) => {
                Notification("Tutor not saved in vle database!", "warning", "Warning")

                // window.location.href = this.props.RedirectTo
              console.log(err)
            })
    }

    render() {
        return (
            <React.Fragment>
                {this.state.apiLoader &&
                    <div class="loading">foregroung loader&#8230;</div>
                }
                <div class="filter-bar-block">
                    <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
                        <div class="bar1"></div>
                        <div class="bar2"></div>
                        <div class="bar3"></div>
                    </a>
                    <div class="header-btn-list">
                        <NavLink to="/mykademy-admin/admin-tutor">
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                viewBox="0 0 14 14"
                                width="11"
                                height="11"
                                style={{ enableBackground: "new 0 0 14 14" }}
                                xmlSpace="preserve"
                            >
                                <path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
                            </svg>
                             back to Tutor
                        </NavLink>
                    </div>
                    <div class="dropdown view-option common-option">
                        <button
                            class="btn dropdown-toggle"
                            type="button"
                            id="dropdownMenuButton1"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                        >
                            Add Option
                        </button>
                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">

                            <NavLink className="dropdown-item"
                                to="/mykademy-admin/admin-tutor">

                                back to Tutor
                        </NavLink>

                        </ul>
                    </div>

                </div>
                <div
                    className={
                        this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"
                    }
                >
                    <AdminNavbar
                        class={this.state.class}
                        data={subdropdownData}
                        DropdownData={MyportfolioAdminDropdownContents}
                    />
                    <div class="container-fluid courses-block">
                        <div className="col-lg-12">
                            <form
                                className="learner-form"
                                className="learner-form"
                                id="register-learner"
                                onSubmit={this.formSubmit}
                            >
                                <h2 class="program-listing__program-counter">
                                    Create Tutor
                                </h2>
                                <div className="avatar-upload">
                                    <div className="avatar-edit">
                                        <input
                                            type="file"
                                            id="imageUpload"
                                            accept=".png, .jpg, .jpeg"
                                            onChange={(e) => this.fileChange(e)}
                                        />
                                        <label for="imageUpload"></label>
                                    </div>
                                    <div className="avatar-preview">
                                        <div
                                            id="imagePreview"
                                            style={{
                                                backgroundImage: `url('https://d2xduyqs25ssfe.cloudfront.net/uploads/default/user/default.jpg')`,
                                            }}
                                        ></div>
                                    </div>
                                    {this.state.errors['learner_image'] &&
                                        <p style={error_style}>{this.state.errors['learner_image']}</p>
                                    }
                                </div>
                                <div className="row g-3">
                                    <div className="col-xs-12 col-md-6 form-group">
                                        <label for="fName" className="form-label">
                                            First Name
                                        </label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            name="learner_Fname"
                                            placeholder="Type your first name"
                                            style={this.state.errors['learner_Fname'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "learner_Fname")}
                                        />
                                        {this.state.errors['learner_Fname'] &&
                                            <p style={error_style}>{this.state.errors['learner_Fname']}</p>
                                        }
                                    </div>
                                    <div className="col-xs-12 col-md-6 form-group">
                                        <label for="lName" className="form-label">
                                            Last Name
                                    </label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            name="learner_Lname"
                                            placeholder="Type your last name"
                                            style={this.state.errors['learner_Lname'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "learner_Lname")}
                                        />
                                        {this.state.errors['learner_Lname'] &&
                                            <p style={error_style}>{this.state.errors['learner_Lname']}</p>
                                        }
                                    </div>
                                 
                                    <div className="col-xs-12 col-md-6">
                                        <label className="form-label">Country</label>
                                        <select
                                            name="country_name"
                                            style={this.state.errors['country_name'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "country_name")}
                                            className="form-control"
                                        >
                                            <option value="">Select the Country</option>
                                            {this.state.countries.map((country, i) => (
                                                <option key={i} data-value={country}>
                                                    {country.name + " (" + country.dial_code + ")"}
                                                </option>
                                            ))}
                                        </select>
                                        {this.state.errors['country_name'] &&
                                            <p style={error_style}>{this.state.errors['country_name']}</p>
                                        }
                                    </div>
                                    <div className="col-xs-12 col-md-6">
                                        <label className="form-label">Phone Number</label>
                                        <input
                                            type="numner"
                                            className="form-control"
                                            name="learner_phone"
                                            placeholder="Type your number"
                                            style={this.state.errors['learner_phone'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "learner_phone")}
                                        />
                                        {this.state.errors['learner_phone'] &&
                                            <p style={error_style}>{this.state.errors['learner_phone']}</p>
                                        }
                                    </div>
                                    <div className="col-xs-12 col-md-6">
                                        <label className="form-label">Email</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            name="learner_email"
                                            placeholder="Type your email"
                                            style={this.state.errors['learner_email'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "learner_email")}
                                        />
                                        {this.state.errors['learner_email'] &&
                                            <p style={error_style}>{this.state.errors['learner_email']}</p>
                                        }
                                    </div>
                                  
                                    <div className="col-xs-12 col-md-6">
                                        <label className="form-label">Password</label>
                                        <input
                                            type="password"
                                            className="form-control"
                                            name="learner_password"
                                            placeholder="Type your password"
                                            style={this.state.errors['learner_password'] ? inputErr : {}}
                                            onChange={this.inputhandle.bind(this, "learner_password")}
                                        />
                                        {this.state.errors['learner_password'] &&
                                            <p style={error_style}>{this.state.errors['learner_password']}</p>
                                        }
                                    </div>
                                    <div className="col-12">
                                        <button type="submit" className="btn btn-submit mt-20 submitbtn">
                                            Submit
                                    </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default AdminCommonCreateTutor;