  getCustomFields = async () => {
    this.setState({ apiLoader: true })
    let formData = new FormData();
    let myportfoliouser = myportfolioCommonService.userData()
    let accessTonen = myportfoliouser.data._token;
    let Authorization_token = myportfolioCommonService.getAuthtoken()
    formData.append("_token", decodeURIComponent(accessTonen));
    const headers = {};
    headers["Authorization"] = "Bearer " + Authorization_token;

    await myportfolioApiService
      .getCustomFields(headers, formData)
      .then((response) => {
        this.setState({ apiLoader: false })
        if (response.status === 200) {
          response.data.data.map((val) => {
            val['value'] = ""
            return val
          })
          //     this.concatWithLocalData(response.data.data)
          //     console.log(response.data.data)
          this.setState({ custom_fields_data: response.data.data })
          console.log(response, 'response for custon fields')
        }
      })
      .catch((err) => {
        this.setState({ apiLoader: false })
        console.log(err)
      });
  }
  handleDynamicFieldChange = (e, val, index) => {
    console.log(e, val, index)
    let custom_fields_data = this.state.custom_fields_data
    console.log(custom_fields_data[index].name)

    let errors = this.state.errors
    errors[custom_fields_data[index].name] = ""

    custom_fields_data[index].value = e.target.value
    this.setState({ custom_fields_data, errors })
  }
