import React from 'react';
import AdminNavbar from "../../shared_components/adminNavbar";
import {subdropdownData,MyportfolioAdminDropdownContents,} from "../../../config";
import { userData } from "../services/common.service";
import { appToken } from "../services/common.service";
const commonService = require('../services/common.service')

const myportfolioCommonService = require("../../myportfolio/services/common.service");

export default class AdminCourseManagement extends React.Component {
  state = {
    class: false,
    url: ''
  }
  componentDidMount = () => {
    if (userData() !== null) {
      this.setState({ isLoggedIn: true });
      let user = localStorage.getItem("mykademy_user");
      let urlDynamic=`https://${commonService.getMykademySubdomain()}.mykademy.com/admin/course?ms_auth_token=${JSON.parse(user).access_token}`;
      this.setState({
        url: urlDynamic
      })
      

    } else this.setState({ isLoggedIn: false });
   
  }
  componentWillUnmount = ()=>{
    window.location.reload()

  }
  toggleNavbar = () => {
    this.setState({ class: !this.state.class });
  };

  render() {
    if (!myportfolioCommonService.isAnyOneAdmin()) {
      MyportfolioAdminDropdownContents.two = "";
    }
		myportfolioCommonService.getRolls()

    return (
      <React.Fragment>
        <div class="filter-bar-block">
          <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
            <div class="bar1"></div>
            <div class="bar2"></div>
            <div class="bar3"></div>
          </a>

        </div>
        <div
          className={
            this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"
          }
        >
          <AdminNavbar
            class={this.state.class}
            data={subdropdownData}
            DropdownData={MyportfolioAdminDropdownContents}
          />
          <div class="container-fluid courses-block" style={{ padding: '60px 0px 0px' }}>
            <iframe src={this.state.url} title="Auth by ms user token" width='100%' scrolling='vertical'
              style={{ height: "87vh", width: "100%", border: 'none' }}></iframe>
          </div>
        </div>
      </React.Fragment>
    )
  }


}
