import React from "react";
import { portfolioUser, getSubdomain } from "../myportfolio/services/common.service";
import { appToken ,userData } from "../mykademy/services/common.service"
import Notfound_withoutlogin from "./Notfound_withoutlogin";
import NotFound from "../shared_components/notFound"
export default class MarketPlace extends React.Component {
    state = {
        finalUrl: '',
        isLoggedIn: false,
        notLearnerLogged: false
    }
    componentDidMount() {
        if (userData() !== null && portfolioUser() !== null) {
            this.setState({ isLoggedIn: true });
        } else {
            this.setState({ isLoggedIn: false });
        }
    }
    render(){
        return(
            <React.Fragment>
                         {
                    this.state.isLoggedIn ?
                        <>
                           <Notfound_withoutlogin message="Coming soon" />
                        </> :
                        <NotFound message="Sorry, you need to login to Appreticeship Application to access your Programmes" domain="myportfolio" response={(val) => this.gotResponse(val)} />
                }
            

            </React.Fragment>
        )

    }
}
