  async LeanersList() {
    this.setState({ emptyData: false });

    const headers = {
      Authorization: appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    }
    let data = {
      page_limit: page_limit,
      page: page,
      search_keywords: this.state.searchKeyWord,
      orderby_item: "id",
      orderby_direction: "DESC",
    };
    await apiService
      .adminListLeaners(headers, data)
      .then((response) => {
        this.setState({
          leaners: this.state.leaners.concat(response.data.data),
          clickLimit: response.data.users_count,
          loadmore_loader: false,
          loader: false,
        });
        this.checkArrayEmpty(response.data.users_count);
      })
      .catch((error) => {
        console.log(error);
      });
  }
  checkArrayEmpty(val) {
    if (val === 0) {
      this.setState({ emptyData: true });
    } else {
      this.setState({ emptyData: false });
    }
  }

  loadMore = () => {
    page++;
    this.setState(
      { loadmore_loader: true, clickLimit: this.state.clickLimit + 1 },
      () => {
        this.LeanersList();
      }
    );
  };
  showingDropdown = () => {
    this.setState({ showDropdown: !this.state.showDropdown });
  };
  handleSearch = debounce((searchQuery) => {
    this.setState({ loader: true });
    search = searchQuery.trim();

    if (search === "") {
      page = 1;
      this.setState({ leaners: [], searchKeyWord: "", show_see_more: false });
    } else {
      this.setState({
        leaners: [],
        searchKeyWord: search,
        show_see_more: true,
      });
      page = 1;
    }
    this.LeanersList();
  }, 500);
