
import { config } from '../config';
const axios = require("axios");
const Base_url = config.vle.REACT_APP_TEAMS_API_URL
export async function sendMailToUser(data) {
  return await axios.post(Base_url + "api/sendmailto_user", data);
}
export async function talkToSales(data) {
  return await axios.post(Base_url + "api/talk_to_sales", data);
}
export async function getTeamsEventById(event_id, from) {
  return await axios.get(Base_url + "api/events/get_teams_event_by_eventID?event_id=" + event_id + "&type=" + from);
}

export async function saveTeamsMeetingToVLE(data) {
  return await axios.post(Base_url + "api/events/save_teams_meeting_metadata", data);
}
export async function saveOrganisation(data) {
  return await axios.post(Base_url+"api/save_organisation", data)
}
export async function saveMember(data) {
  return await axios.post(Base_url+"api/save_member", data)
}
export async function getOrg(data) {
  return await axios.post(Base_url+"api/get_org", data)
}
export async function getMember(data) {
  return await axios.post(Base_url+"api/get_member", data)
}

export async function deletemember(data) {
  return await axios.post(Base_url+"api/delete_member", data)
}
