import React from "react";
import AdminNavbar from "../../shared_components/adminNavbar";
import { NavLink } from "react-router-dom";
import { userData, getAuthtoken } from "../services/common.service";
import { MykademyAdminDropdownContents, subdropdownData } from "../../../config";

import { debounce } from "lodash";
import personimg from "../../../assets/img/landing/person.svg";
import Employerimg from "../../../assets/img/landing/employer.svg"
import callImg from "../../../assets/img/landing/call.svg";
import mailImg from "../../../assets/img/landing/mail.svg";
import locationImg from "../../../assets/img/landing/location.svg";
import country from "../../mykademy/country.json";


const apiService = require("../services/api.service");
const mykademyCommonService = require("../../mykademy/services/common.service");
const myportfolioCommonService = require("../services/common.service")

var page = 1,
	per_page = 10;

export default class adminApprentices extends React.Component {
	state = {
		user_token: "",
		apprenticeList: [],
		totalStudentCount: "0",
		clickCount: 1,
		search: "",
		show_see_more: false,
		loader: true,
		class: false,
		loadmore_loader: false,
		limit: 0,
		emptyData: false,
		countriesFlagsData: country.data,

	};

	componentDidMount = () => {
		if (userData() !== null) {
			this.setState(
				{ user_token: userData().data._token, apprenticeList: [], auth_token: getAuthtoken() },
				() => {
					this.getStudentList();
				}
			);
		}
	};

	checkArrayEmpty(val) {
		if (val === 0) {
			this.setState({ emptyData: true });
		} else {
			this.setState({ emptyData: false });
		}
	}

	getCountryFlag = (countryname) => {
		console.log(countryname,'get countryyyyy')
		const flagurl = this.state.countriesFlagsData
			.filter((b) => {
				return b.name === countryname;
			})
			.map((b) => {
				return b.flag;
			});
			console.log(flagurl,'bbbbb')
		if (flagurl.length === 0) {
			return (
				<div class="info-wrap fs-12">
					<span
						class="flag-icon"

					></span>
					<span>{"-"}{countryname}</span>

				</div>
			);
		}
		return (
			<div class="info-wrap fs-12">
				<img
					class="flag-icon"
					src={flagurl[0]}
					alt="United Kingdom"
				/>
				{countryname}
			</div>
		);
	};

	async getStudentList() {
		this.setState({ emptyData: false });

		let formData = new FormData();
		formData.append("_token", decodeURIComponent(this.state.user_token));
		formData.append("current_page", this.state.clickCount);
		formData.append("per_page", per_page);
		if (this.state.search !== "") {
			formData.append("search", this.state.search);
		}

		const headers = {
			"Content-Type": "multipart/form-data",
		};
		headers["Authorization"] = "Bearer " + this.state.auth_token;

		await apiService
			.getLearnerList(headers, formData)
			.then((response) => {
				this.checkArrayEmpty(response.data.total_rows);

				if (response.data.total_rows !== "0") {
					this.setState({
						apprenticeList: this.state.apprenticeList.concat(
							response.data.members_details
						),
						totalStudentCount: response.data.total_rows,
						loader: false,
						loadmore_loader: false,
						limit: response.data.members_details.length,
					});
				} else {
					this.checkArrayEmpty(response.data.total_rows);
					this.setState({
						apprenticeList: [],
						totalStudentCount: 0,
						show_see_more: false,
						loader: false,
						loadmore_loader: false,
						emptyData: true
					});
				}
			})
			.catch((err) => {
				this.setState({ loader: false, loadmore_loader: false });
				console.log(err);
			});
	}

	loadMore = () => {
		this.setState(
			{ clickCount: this.state.clickCount + 1, loadmore_loader: true },
			() => {
				this.getStudentList();
			}
		);
	};

	getProgressBar = (progressValue) => {
		var value = 0,
			progressclassName = "";
		if (progressValue !== null) {
			value = parseInt(progressValue);
		} else {
			value = 0;
		}
		if (value >= 0 && value < 50) {
			progressclassName = "progress-bar-danger";
		} else if (value >= 50 && value < 100) {
			progressclassName = "progress-bar-blue";
		} else if (value === 100) {
			progressclassName = "progress-bar-success";
		}

		return (
			<>
				<span className="progress-completed">{value}% completed</span>
				<div className="progress common-progress">
					<div
						className={progressclassName + " " + "progress-bar"}
						role="progressbar"
						style={{ width: value + "%" }}
						aria-valuenow="25"
						aria-valuemin="0"
						aria-valuemax="100"
					></div>
				</div>
			</>
		);
	};

	conversion = (id, phone, employer, image_url, name, email, country) => {
		page = 1;

		let data = [
			{
				leaner_id: id,
				phone: phone,
				employer: employer,
				image_url: image_url,
				name: name,
				email: email,
				country: country
			},
		];
		let encoded = btoa(encodeURIComponent(JSON.stringify(data)));

		return (

			<NavLink
				to={`/myportfolio-admin/admin-apprentices/detailed/${encoded}`}
				className="dropdown-item">
				View
			</NavLink>

		);
	};

	render_profileImg = (Img_url) => {
		if (Img_url === "" || undefined || null) {
			return (
				<img
					src="/./custom_assets/default_user.jpg"
					alt="user"
				/>
			);
		} else {
			return (
				<img src={Img_url} alt="user" />
			);
		}
	};

	searchApprentices = debounce((query) => {
		this.setState({ clickCount: 1 });
		per_page = 10;
		if (query === "") {
			this.setState({ apprenticeList: [], search: "", loader: true });
			this.getStudentList();
		} else {
			this.setState({
				search: query,
				apprenticeList: [],
				loader: true,
			});
			this.getStudentList();
		}
	}, 500);

	toggleNavbar = () => {
		this.setState({ class: !this.state.class });
	};
	editMember = (id) => {
		// let encoded = btoa(JSON.stringify(id));
		return (
			<NavLink className="dropdown-item"
				to={`/myportfolio-admin/admin-apprentices/edit/${id}`}

				className="dropdown-item">
				Edit Profile
			</NavLink>
		);
	}
	render() {
		if (!mykademyCommonService.isAdmin()) {
			MykademyAdminDropdownContents.one = "";
		}
		myportfolioCommonService.getRolls()

		return (
			<>
				<div className="filter-bar-block">
					<a className="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
						<div className="bar1"></div>
						<div className="bar2"></div>
						<div className="bar3"></div>
					</a>
					<div className="header-btn-list">
						<NavLink to="/myportfolio-admin/admin-apprentices/register">
							<svg
								xmlns="http://www.w3.org/2000/svg"
								viewBox="0 0 14 14"
								width="11"
								height="11"
								style={{ enableBackground: "new 0 0 14 14" }}
								xmlSpace="preserve"
							>
								<path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
							</svg>
              Add  {myportfolioCommonService.getdynamicRolls().Learner}
						</NavLink>
					</div>

					<div className="dropdown view-option common-option">
						<button className="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
							Add Option
                        </button>
						<ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
							<li>
								<NavLink className="dropdown-item" to="/myportfolio-admin/admin-apprentices/register">
									Add {myportfolioCommonService.getdynamicRolls().Learner}
								</NavLink>
								{/* <a className="dropdown-item" href="#">
									Add {myportfolioCommonService.getdynamicRolls().Learner}
                                </a> */}
							</li>
						</ul>
					</div>

					<div className="filter-bar-holder">

						<div className="input-group flex-nowrap filter-search">
							<input type="text" className="form-control" onChange={(e) => this.searchApprentices(e.target.value)} placeholder="Find" aria-label="Find" aria-describedby="addon-wrapping" />
							<span className="input-group-text" id="addon-wrapping">
								<img src="/./assets/images/landing/search.svg" alt="search" />
							</span>
						</div>
						<a className="mobile-search-icon" >
							<img src="/./assets/images/landing/mobile-search.svg" alt="search" />
						</a>
					</div>
				</div>
				<div className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>
					<AdminNavbar class={this.state.class} data={subdropdownData} DropdownData={MykademyAdminDropdownContents} />


					<>
						{this.state.loader ? (
						<div className="table-responsive common-table-list">
							<div style={{ textAlign: "center" }}>
								<img
									style={{ width: "45px", height: "100vh" }}
									src="/./custom_assets/loader.svg"
									alt="loader"
								/>
							</div>
								</div>) : (
								<div className="container-fluid courses-block">
									{!this.state.emptyData ? (
										<div className="course-list-block">
											<h4>{myportfolioCommonService.getdynamicRolls().Learner}</h4>
											<div className="table-responsive sessions-table-list apprentices-table-list" style={{ overflow: 'hidden',minHeight:'35vh' }}>
												<table className="table">
													<thead>
														<tr>
															<th scope="col">
																<div className="thead-img-wrap">
																	<img
																		src={personimg}
																		alt="person"
																	/>
                                                         			Name
                                                    			</div>
															</th>
															<th scope="col" width="150">
																<div className="thead-img-wrap">
																	<img
																		src={callImg}
																		alt="Employer"
																	/>
                                                       				phone
                                               				 </div>
															</th>
															<th scope="300">
																<div className="thead-img-wrap">
																	<img
																		src={mailImg}
																		alt="Programme"
																	/>
                                                            		email
                                                				</div>
															</th>
															<th width="col" scope="col">
																<div className="thead-img-wrap">
																	<img
																		src={locationImg}
																		alt="Progress"
																	/>
                                                   					location
                                                				</div>
															</th>
															<th width="col" scope="col">
																<div className="thead-img-wrap">
																	<img
																		src={Employerimg}
																		alt="Progress"
																	/>
                                                   					Employer
                                                				</div>
															</th>
															<th width="50" scope="col"></th>
														</tr>
													</thead>
													<tbody>
														{this.state.apprenticeList.map(
															(val, i) => {
																return (
																	<tr key={i}>
																		<td scope="row">
																			<div className="info-wrap">

																				{this.render_profileImg(val.image_url)}

																				{val.first_name} {" "} {val.last_name}
																			</div>
																		</td>
																		<td>
																			<div className="info-wrap fs-12">
																				{val?.country_code === '' ? "-" : val?.country_code}{!val?.mobile_number ? "-" : val?.mobile_number}
																				{/* {val?.country_code ?? "-"}{val?.mobile_number ?? "-"} */}
																			</div>
																		</td>
																		<td>
																			<div className="info-wrap fs-12 three-dot" style={{ display: 'block' }}>
																				{val.email ?? "-"}
																			</div>
																		</td>
																		<td>
																			{this.getCountryFlag(val?.member_address?.country)}
																		</td>
																		<td>
																			<div className="info-wrap fs-12">
																				{val?.organisation}
																			</div>
																		</td>
																		<td>
																			<div className="dropdown tool-dropdown-wrap apprentices-table-dropdown">
																				<a
																					className="dropdown-toggle three-dots-dropdown"
																					href="#"
																					role="button"
																					id="dropdownMenuLink"
																					data-bs-toggle="dropdown"
																					aria-expanded="false"
																				>
																					<img
																						src="/./assets/images/landing/three-dots.svg"
																						alt="dot"
																					/>
																				</a>

																				<ul
																					className="dropdown-menu"
																					aria-labelledby="dropdownMenuLink"
																				>
																					<li>
																						{this.conversion(val.id, val?.country_code + val?.mobile_number, val?.organisation, val?.image_url, val.first_name + " " + val?.last_name, val?.email, val?.member_address?.country)}

																					</li>
																					<li>
																						{this.editMember(val.id)}
																					</li>



																				</ul>
																			</div>
																		</td>
																	</tr>
																)
															}
														)}
													</tbody>
												</table>
											</div>
											{this.state.loader || this.state.apprenticeList.length.toString() == this.state.totalStudentCount
												|| (this.state.limit < 10) ? (
													<div className="program-listing__load-more load-more-wrap"></div>
												) : (
													<div className="program-listing__load-more load-more-wrap">
														{
															this.state.loadmore_loader ?
																(<button className="olive-flat-button olive-accent" >
																	<img style={{ width: '27px' }} alt="loading" src="/./custom_assets/Chasing arrows.gif" />
																</button>) : (
																	<button className="olive-flat-button olive-accent" onClick={this.loadMore}>
																		<span className="olive-button-wrapper"> Load more </span>

																	</button>
																)

														}

													</div>
												)}
										</div>
									) : (
											<section className="m-t-20 results-section results--grid-null-item alert alert-danger">
												<div>No Records found!</div>
											</section>
										)}
								</div>



							)}
					</>

				</div>
			</>
		);
	}
}