import React, { Component } from 'react';
import { appToken, userData } from "../services/common.service";
import { Notification } from './../../../services/common.service'

const apiService = require('../services/api.service');

class MkViewProfile extends Component {

    constructor(props) {
        super(props)
        this.state = {
            seletedFile: null,
            imgLoader:false
        }
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);

    }

    editPersonalData = () => {
        this.props.updateState({ open_edit_personal_section: true })
        this.props.updateState({ show_profile: false })
    }
    editSecurity = () => {
        this.props.updateState({ open_edit_security_section: true })
        this.props.updateState({ show_profile: false })
    }
    splitName = (fullName) => {
        let names = fullName.split(" ")
        let fname = names[0];
        let lname = names[1];
        return (

            <> <li>
                <span>First Name</span>
                <p>{fname}</p>
            </li>
                <li>
                    <span>Last Name</span>
                    <p>{lname}</p>
                </li>
            </>
        )
    }
    showFileUpload() {
        this.fileUpload.current.click()
    }
    uploadFile = async () => {
        const formData = new FormData();
        console.log(this.props)
        // Update the formData object
        formData.append("file", this.state.seletedFile);
        formData.append('name', this.props.name)
        formData.append('email', this.props.email)
        formData.append('phone', this.props.phone)
        formData.append("us_country_code", this.props.country_code)
        formData.append("us_country", this.props.country)
        const headers = {
            Authorization: appToken(),
        };
        let user = localStorage.getItem("mykademy_user");
        if (user !== null) {
            headers["Access-token"] = JSON.parse(user).access_token;
        }
        await apiService
            .updateProfile(headers, formData)
            .then((response) => {
                console.log(response)
                if (response.status === 200) {
                    console.log(response.data.metadata.status_code,'status')
                    if(response.data.metadata.status_code === "400"){
                        Notification('Format is not support', "warning", "Warning");
                    this.setState({imgLoader:false})

                        return
                    }
                    this.props.updateState({ image_url: response.data.data.user.us_image })
                    Notification("Image Uploaded successfully", "success", "Success");
                    this.setState({imgLoader:false})
                    setTimeout(() => {
                        this.updateToLocalStorage(response.data.data.token, response.data.data.user.us_image)
                    }, 3000)

                }

            })
            .catch((err) => {
                console.log(err)
            })
    }
    updateToLocalStorage(token, img_url) {

        let user = userData()
        if (user !== null) {
            user.user_data['us_image'] = img_url

            user.access_token = token
            localStorage.setItem('mykademy_user', JSON.stringify(user))


        }
    }
    onChange = e => {
        e.preventDefault();
        this.setState({imgLoader:true})
        this.setState({ seletedFile: e.target.files[0] }, () => {
            this.uploadFile()
        })

    };
    render() {
        return (
            <div id="profile-step" class="step active">
                <div class="profile-header">
                    <div class="img-wrap">
                        <img src={this.props.image_url} alt="" />
                    </div>
                    <h4>Profile</h4>
                    <a onClick={() => this.props.updateState({ open_modal: false })} style={{cursor:'pointer'}}><img src="assets/images/landing/cross-icon.svg" alt="" /></a>
                </div>

                <div class="profile-update">
                    <span>Personal</span>
                    <a data-toggle="tab" style={{cursor:'pointer'}} onClick={() => { this.editPersonalData() }}>Update</a>
                </div>

                <ul class="profile-info">
                    {this.splitName(this.props.name)}

                    <li>
                        <span>Email</span>
                        <p>{this.props.email}</p>
                    </li>
                    <li>
                        {/* {
                            !this.state.imgLoader ?(
                                <React.Fragment>

                                    <a onClick={this.showFileUpload} style={{ cursor: 'pointer' }}>
                                        <input onChange={e => this.onChange(e)} type="file" accept="image/*" id="my_file" style={{ display: "none" }} ref={this.fileUpload} />
            
                                        <img src="assets/images/landing/upload-image-icon.svg" alt="" />
                                Update Profile Image
                            </a>
                            <span className="format">(Support only jpg, jpeg, png format)</span>
                                </React.Fragment>

                            ):(
                                <div className="step step-3 active">
                                <div className="d-flex  spinner-content">
                                    <div
    
                                        className="spinner-border"
                                        role="status"
                                    >
                                        <span className="visually-hidden">Loading...</span>
                                    </div>
                                </div>
                            </div>
                            )
                        } */}
                       
                    </li>
                </ul>

                <div class="profile-update">
                    <span>Security</span>
                    <a onClick={() => this.editSecurity()} data-tab="security-step">Update</a>
                </div>

                <ul class="profile-info">
                    <li>
                        <span>Password</span>
                        <img src="assets/images/landing/password-icon.svg" alt="" />
                    </li>
                </ul>

                <div class="profile-modal-foo">
                    <button onClick={() => this.props.updateState({ open_modal: false })} type="button" class="btn common-course-btn btn-secondary">Close</button>
                </div>
            </div>
        );
    }
}

export default MkViewProfile;