import React from "react";
import { NavLink, Redirect } from "react-router-dom";
import { config, MyportfolioAdminDropdownContents, subdropdownData } from "../../../config";

import { Notification } from "../../../services/common.service";
import AdminNavbar from "../../shared_components/adminNavbar";
import { appToken } from "../services/common.service"
import AlertBox from "../../shared_components/modals/alert";

const myportfolioCommonService = require("../../myportfolio/services/common.service");
const apiService = require("../services/api.service");
export default class adminEnrollList extends React.Component {
  constructor(props) {
    super(props);
    let decode_data = JSON.parse(atob(this.props.match.params.learner_id));
    this.state = {
      learnerdata: decode_data,
      showDropdown: false,
      enrolledCourses: [],
      redirect: "",
      Allcouses: [],
      emptyData: false,
      result_section: "results-section results--grid",
      Allleaners: [],
      lerner_id: decode_data.id,
      input: '',
      li_id: '',
      class: false,
      loading:true,
      show_popup:false,
      subcribeinfo:{}
    };
  }
  componentDidMount() {
    this.courseLIst(this.state.lerner_id);
    console.log(this.state.input,'inputtt')
    this.getAllcourses();
    this.getAlllearners();
  }
  async getAlllearners() {
    const headers = {
      "Authorization": appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    }
    await apiService
      .adminListLeanersAll(headers)
      .then((response) => {
        this.setState({
          Allleaners: response?.data.data,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }
  async getAllcourses() {
    const headers = {
      'Authorization': appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    }
    await apiService
      .adminAllcouselist(headers)
      .then((response) => {
        this.setState({ Allcouses: response.data.data });
      })
      .catch((error) => {
        this.setState({ Allcouses: [] });
      });
  }
  emptyCheck = (val) => {
    if (val === 0) {
      this.setState({ emptyData: true });
    } else {
      this.setState({ emptyData: false });
    }
  };
  async courseLIst(id) {

    const headers = {
      "Content-Type": "application/json",
      "Authorization": appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    }

    await apiService
      .enrolledCourseList(headers, id)
      .then((response) => {
        if (response.data.status === 200) {
          this.setState({
            enrolledCourses: response.data.data,
            loading:false

          });
          this.emptyCheck(response.data.total);
        }
      })
      .catch((error) => {
        this.setState({ enrolledCourses: [] });
      });
  }
  showingDropdown = () => {
    this.setState({ showDropdown: !this.state.showDropdown });
  };
  logout = () => {
    localStorage.clear();
    this.setState({ redirect: "/tab" });
  };
  openEnroll = () => {
    this.setState({ showDropdown: false });

  };

  openRegisterLearner() {
    window.location.href = window.location.origin + "/admin-learner-signup";
  }
  inputhandle = (field, e) => {
    if (field === "course_name") {
      const course_id = e.target.value;
      const seletedCourse = this.state.Allcouses.find(
        (u) => u.id == course_id
      );
      this.setState({
        seletedCourse: seletedCourse,
        input: seletedCourse.cb_title,
        li_id: seletedCourse.id
      });
    }
    if (field === "Learner_name") {
      const lerner_id = e.target.value;
      const seletedLearner = this.state.Allleaners.find(
        (u) => u.id === lerner_id
      );
      this.setState(
        {
          seletedLearner: seletedLearner,
        },
        () => {
          this.setState({ lerner_id: this.state.seletedLearner.id }, () => {
            this.courseLIst(this.state.lerner_id);
          });
        }
      );
    }
  };
  async enrolllearner() {

    if (this.state.seletedCourse === undefined) {
      return
    } else {
    this.setState({loading:true})

      let user_data = {
        user_ids: [this.state.lerner_id],
        course_id: this.state.seletedCourse.id,
      };
      const headers = {
        "Content-Type": "application/json",
        "Authorization": appToken(),
      };
      let user = localStorage.getItem("mykademy_user");
      if (user !== null) {
        headers["Access-token"] = JSON.parse(user).access_token;
      }
      await apiService
        .enrolllearnerstoCouses(headers, user_data)
        .then((response) => {
          if (response.status === 200) {
            if (!response.data.success) {
              // for toaster
              Notification("Course Alredy Subscribed", "info", "Info");
              // end
            } else {
              Notification(
                "Course Subscribed Successfully",
                "success",
                "Success"
              );
              this.courseLIst(this.state.lerner_id);
            }
          } else {
            alert(response.message);
          }
        })
        .catch((error) => { });
    }
  }
  totalEnrolls = () => {
    let total = this.state.enrolledCourses.length
    return total
  }
  gridListLayout(className) {
    if (className === " results--list") {
      document.getElementById("grid").classList.remove("selected");
      document.getElementById("list").classList.add("selected");
    } else {
      document.getElementById("grid").classList.add("selected");
      document.getElementById("list").classList.remove("selected");
    }
    this.setState({ result_section: "results-section" + className });
  }
  progressBar = (value) => {
    if (value == 0) {
      return null;
    } else if (value < 50) {
      return (
        <div
          class="progress-bar progress-bar-danger"
          role="progressbar"
          style={{ width: `${value}%` }}
          aria-valuenow="25"
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      );
    } else if (value < 100) {
      return (
        <div
          class="progress-bar progress-bar-blue"
          role="progressbar"
          style={{ width: `${value}%` }}
          aria-valuenow="25"
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      );
    } else if (value == 100) {
      return (
        <div
          class="progress-bar progress-bar-success"
          role="progressbar"
          style={{ width: `${value}%` }}
          aria-valuenow="25"
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      );
    }
  };
  onChangeHandler = (e) => {
    this.setState({
      input: e.target.value,
    })
  }
  componentWillUnmount (){
    this.setState({Allcouses:[],input:''})

  }
  list = () => {
    console.log(this.state.Allcouses,'herer')
    if(this.state.Allcouses.length === 0){
      return(
        <ul>
          <li>No Courses Found for Enrolment</li>
        </ul>
      )
    }else{

      return (<ul>{this.state.Allcouses
        .filter(d => this.state.input === '' || d.cb_title.includes(this.state.input))
        .map((d, index) => (
          <li value={d.id} onClick={this.inputhandle.bind(this, "course_name")}
            className={(d.id === this.state.li_id ? "liclr" : "")}
          >
            {d.cb_title}
          </li>
        ))
      }
      </ul>
      )
    }

  }
  toggleNavbar = () => {
    this.setState({ class: !this.state.class })
  }
  modalOpen = ()=>{
    console.log(this.state.Allcouses,this.state.enrolledCourses)
    // let array1 = this.state.Allcouses.filter(val => !this.state.enrolledCourses.includes(val));
    this.setState({input:'',li_id:'',seletedCourse:undefined})
    const result = this.state.Allcouses.filter(e => !this.state.enrolledCourses.some(a => a.course_id == e.id));
    this.setState({Allcouses:result},()=>this.list())

  }
  modalCallback = (status) => {
    this.setState({ show_popup: false})
    if (status) {
      this.endCourseSub()


    }
  }
  endSubscription = (course_id,user_id,course_name,user_name)=>{
    let data = {
      course_id:course_id,
      user_id:user_id,
      course_name:course_name,
      user_name:user_name
    }
    this.setState({show_popup:true,subcribeinfo:data})
  }
  endCourseSub = async()=>{
    this.setState({loading:true})
    const headers = {
      "Content-Type": "multipart/form-data",
      "Authorization": appToken(),
    };
    let user = localStorage.getItem("mykademy_user");
    if (user !== null) {
      headers["Access-token"] = JSON.parse(user).access_token;
    } 
    let formData = new FormData();
    formData.append("user_id",this.state.subcribeinfo.user_id );
    formData.append("course_id",this.state.subcribeinfo.course_id);
    formData.append("course_title", this.state.subcribeinfo.course_name);
    formData.append("student_name", this.state.subcribeinfo.user_name);
    await apiService.endCourseSubscription(headers,formData)
    .then((response)=>{
      if(response.status === 200){
        if(!response.data.error){
          Notification(
            "Course Unsubscribed Successfully",
            "success",
            "Success"
          );
          this.courseLIst(this.state.subcribeinfo.user_id)
        }else{
          Notification(
            "course unsudscription failed..!",
            "danger",
            "fail"
          );
          this.courseLIst(this.state.subcribeinfo.user_id)


        }
      }
    })
    .catch((err)=>{
      console.log(err)
    })

  }

  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }
    if (!myportfolioCommonService.isAnyOneAdmin()) {
      MyportfolioAdminDropdownContents.two = "";
    }
		myportfolioCommonService.getRolls()


    return (
      <div>
        <div class="filter-bar-block">
          <a class="nav-trigger signinBtn" onClick={() => this.toggleNavbar()}>
            <div class="bar1"></div>
            <div class="bar2"></div>
            <div class="bar3"></div>
          </a>
          <div class="header-btn-list">
            
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 14 14"
                width="11"
                height="11"
                style={{ enableBackground: "new 0 0 14 14" }}
                xmlSpace="preserve"
              >
                <path d="M13.9,6.6c-0.1-0.1-0.2-0.1-0.4-0.1h-6v-6c0-0.1,0-0.3-0.1-0.4S7.1,0,7,0S6.7,0,6.6,0.1S6.5,0.4,6.5,0.5v6h-6c-0.1,0-0.3,0-0.4,0.1S0,6.9,0,7s0,0.3,0.1,0.4c0.1,0.1,0.2,0.1,0.4,0.1h6v6c0,0.1,0,0.3,0.1,0.4C6.7,14,6.9,14,7,14s0.3,0,0.4-0.1c0.1-0.1,0.1-0.2,0.1-0.4v-6h6c0.1,0,0.3,0,0.4-0.1C14,7.3,14,7.1,14,7S14,6.7,13.9,6.6z" />
              </svg>
          <a  style={{cursor:'pointer'}} onClick={()=>this.modalOpen()} data-bs-toggle="modal" data-bs-target="#enrolNewCourse">Enroll New Course</a>
            
           
          </div>
          <div class="dropdown view-option common-option">
            <button
              class="btn dropdown-toggle"
              type="button"
              id="dropdownMenuButton1"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              Add Option
            </button>
            <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
              <li>
                {/* <a class="dropdown-item" href="#">
                  Add Learner
                </a> */}
          <a class="dropdown-item" style={{cursor:'pointer'}} onClick={()=>this.modalOpen()} data-bs-toggle="modal" data-bs-target="#enrolNewCourse">Enroll New Course</a>

              </li>
             
             
            </ul>
          </div>
        </div>
        <div  className={this.state.class ? "sidebar-holder hideSidebar" : "sidebar-holder"}>
        <AdminNavbar class={this.state.class} data={subdropdownData} DropdownData={MyportfolioAdminDropdownContents} />
          <div class="container-fluid courses-block" id="learners-detail">
            {
              this.state.show_popup &&
          <AlertBox
                btn1="Cancel"
                btn2="Delete"
                message="Are you sure wants to Unsubscribe this course for the learner..??"
                type="alert"
                callback={this.modalCallback}
                >
              </AlertBox>
            }
            <div class="row flex-center" style={{ flexDirection: "unset"}}>
                <h4 class="heading-title">Learners</h4>
            </div>
            <div class="common-detail-block">
              <div class="row flex-center" style={{ flexDirection: "unset",justifyContent:'normal'  }}>
                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4">
                  <div class="common-media">
                    <a
                      href="javascript:void(0);"
                      class="img-wrap details-img-wrap"
                    >
                      <img
                        src={this.state.learnerdata.image}
                        alt="user name"
                        class="img-rounded flex-shrink-0"
                      />
                    </a>
                    <div class="common-list-wrap">
                      <h4>{this.state.learnerdata.name}</h4>
                      <span class="common-list-text">
                        {this.state.learnerdata.phone}
                      </span>
                      <a
                        class="common-list-text m-0 word-break"
                        href="mailto:Shaun.Craig@organsiation.com"
                      >
                        {this.state.learnerdata.email}
                      </a>
                    </div>
                  </div>
                </div>
                <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3">
                  <span class="common-list-text">
                    {this.state.learnerdata.country_name}
                  </span>
                </div>
                <div class="col-sm-12 col-md-6 col-lg-2 col-xl-2">
                  <span class="enrolled-count">
                    {this.totalEnrolls()}
                  </span>
                  <span class="common-list-text">Courses Enrolled</span>
                </div>
                {/* <div class="col-sm-12 col-md-6 col-lg-3 col-xl-3">
                  <a href="javascript:void(0);" class="enroll-btn enroll-btnnew" data-bs-toggle="modal" data-bs-target="#enrolNewCourse">Enroll New Course</a>
                </div> */}
              </div>
            </div>
            <hr class="separater" />

            <div class="program-detail-block">
              <h4>{this.state.learnerdata.name}'s Courses</h4>
              {
                this.state.loading ? (
                  <div className="step step-3 active">
                  <div className="d-flex justify-content-center align-items-center spinner-content">
                    <div
                      style={{ marginTop: "15%" }}
                      className="spinner-border"
                      role="status"
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  </div>
                </div>
                ):(
              <React.Fragment>

              {
                this.state.enrolledCourses.length === 0 ?
                <section className="m-t-20 results-section results--grid-null-item alert alert-danger">
                <div>No Courses Found!</div>
              </section>:
                 <div class="row">
                 {this.state.enrolledCourses.map((val, i) => {
                   return (
                     <div
                       key={i}
                       class="col-xs-12 col-md-6 col-lg-4 col-xl-4 mb-20"
                     >
                       <div class="common-box-list">
                         <div class="img-wrap">
                           <img src={val.cb_image} alt="user name" />
                         </div>
                         <div class="detail-text-wrap">
                           <h4>{val.cb_title}</h4>
                           <div class="progress common-progress">
                             {this.progressBar(val.cs_percentage)}
                           </div>
                           <span class="progress-completed">
                             {val.cs_percentage}%completed
                             </span>
                           <span class="expiry-date">
                             <strong>Expiry date</strong>
                             {val.cs_end_date}
                             {/* <button type="button" className=" deletecourse btn common-course-btn "onClick={()=>{this.endSubscription(val?.course_id,val?.cs_user_id,val?.cb_title,val?.us_name)}} >Delete</button> */}

                           </span>
                    
                           {/* <span onClick={()=>{this.endSubscription(val?.course_id,val?.cs_user_id,val?.cb_title,val?.us_name)}} 
                           style={{float:"right"}}>delete</span> */}
                         </div>
                       </div>
                     </div>
                   );
                 })}
               </div>
              }
              </React.Fragment>

                )
              }
           
            </div>
          </div>
        </div>
        {/* modal start */}
        <div class="modal fade enrol-course-modal" id="enrolNewCourse" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">Enroll New Course</h5>
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div class="modal-body">
                <div class="enrol-search">
                  <input value={this.state.input}
                    onChange={this.onChangeHandler.bind(this)}
                    type="text" class="form-control enrol-search-input" placeholder="Search Course" />
                  {this.list()}
                </div>
              </div>
              <div class="modal-footer">
                <button disabled={this.state.seletedCourse === undefined} type="button" class="btn enroll-now-btn" data-bs-dismiss="modal" onClick={() => this.enrolllearner()}>Enroll Now</button>
              </div>
            </div>
          </div>
        </div>
        {/* modal end */}

      </div>
    );
  }
}
