import React, { Component } from 'react';
import { Notification } from './../../../services/common.service'
import { appToken, userData } from "../services/common.service"
const apiService = require('../services/api.service');

const contries_with_phone_code = require('../../../services/countries_with_phone')

let error_style = {
    marginBottom: '15px',
    paddingLeft: '13px',
    fontSize: '12px',
    fontWeight: 'normal',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: '1.33',
    letterSpacing: 'normal',
    textAlign: 'left',
    color: '#c4314b',
}
let inputErr = {
    border: ' solid 1px #c4314b',
    marginBottom: '5px',
    background: 'none',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'right calc(.375em + .1875rem) center',
    backgroundSize: 'calc(.75em + .375rem) calc(.75em + .375rem)'
}
class MkEditPersonalData extends Component {

    constructor(props) {
        super(props)
        this.state = {
            fields: {
                fname: this.props.name,
                phone: this.props.phone,
                email: this.props.email,
                image_url: this.props.image_url,
                country_code: this.props.country_code,
                country: this.props.country
            },
            countryJson: contries_with_phone_code.contries_with_phone_code.find(country => country.code === this.props.country),
            us_country: '',
            seletedFile: null,
            errors: {},
            countries: contries_with_phone_code.contries_with_phone_code,
            apiLoader: false
        }
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);
        console.log(this.props)
        console.log(this.state.countryJson)


    }
    componentDidMount = () => {
        if (this.state.countryJson) {
            let fields = this.state.fields
            fields['country_name'] = this.state.countryJson.name
            this.setState({ fields })
        }
        // this.getCounties();
        // this.setState({
        //     us_country: contries_with_phone_code.contries_with_phone_code.filter(x=>x.code===this.props.country)
        // })

    }
    getCounties = async () => {
        this.setState({ apiLoader: true })
        const headers = {
            Authorization: appToken(),
        };
        await apiService
            .getCountry_list(headers)
            .then((response) => {
                console.log(response.data.data)
                this.setState({ countries: response.data.data, apiLoader: false });
            })
            .catch((err) => {
                console.log(err);
            });
    }

    // update = (e) => {
    //     e.preventDefault()
    //     const isValid = this.handleValidation(true);
    //     if (isValid) {
    //         this.props.updateState({ show_profile: true })
    //         this.props.updateState({ open_edit_personal_section: false })
    //         Notification("Personal data updated successfully", "success", "Success");
    //     } else {
    //         Notification("Please fill required fields!", "warning", "Warning")
    //     }

    // }
    close = () => {
        this.props.updateState({ show_profile: true })
        this.props.updateState({ open_edit_personal_section: false })
    }
    handleValidation() {
        let fields = this.state.fields;
        let errors = {};
        let formIsValid = true;

        //Name
        if (!fields["fname"]) {
            formIsValid = false;
            errors["fname"] = " Name Cannot  be empty";
        }



        //Email
        if (!fields["email"]) {
            formIsValid = false;
            errors["email"] = "Email Cannot be empty";
        }

        if (typeof fields["email"] !== "undefined") {
            let lastAtPos = fields["email"].lastIndexOf('@');
            let lastDotPos = fields["email"].lastIndexOf('.');

            if (!(lastAtPos < lastDotPos && lastAtPos > 0 && fields["email"].indexOf('@@') == -1 && lastDotPos > 2 && (fields["email"].length - lastDotPos) > 2)) {
                formIsValid = false;
                errors["email"] = "Email is not valid";
            }
        }
        //country
        if (!fields["country_name"]) {
            formIsValid = false;
            errors["country_name"] = "Country cannot be empty";
        }

        //country
        if (!fields["phone"]) {
            formIsValid = false;
            errors["phone"] = "Phone number cannot be empty";

        }
        if (isNaN(fields["phone"])) {
            formIsValid = false;
            errors["phone"] = "Enter a valid Phone Number";
        }
        if (!fields["phone"].match(/^[0-9]{4,12}$/)) {
            formIsValid = false;
            errors["phone"] = "Enter a valid Phone Number"
        }


        this.setState({ errors: errors });
        return formIsValid;
    }
    handleChange(field, e) {
        let fields = this.state.fields;
        fields[field] = e.target.value;
        if (field === 'country_name') {

            var foundValue = this.state.countries.filter(obj => obj.name === e.target.value);
            fields['country_code'] = foundValue[0].dial_code
            fields['country'] = foundValue[0].code
            // let obj = this.state.countries.find(o =>  res[0]);
            console.log(fields, 'country')
        }
        this.setState({ fields });
    }
    updateUserData = async () => {
        const isValid = this.handleValidation(true);

        if (isValid) {
            this.setState({ apiLoader: true })

            let formData = new FormData()
            formData.append('name', this.state.fields.fname)
            formData.append('email', this.state.fields.email)
            formData.append('phone', this.state.fields.phone)
            formData.append("us_country_code", this.state.fields.country_code)
            formData.append("us_country", this.state.fields.country)
            if (this.state.seletedFile !== null) {
                formData.append("file", this.state.seletedFile);
            }

            const headers = {
                Authorization: appToken(),
            };
            let user = localStorage.getItem("mykademy_user");
            if (user !== null) {
                headers["Access-token"] = JSON.parse(user).access_token;
            }
            await apiService
                .updateProfile(headers, formData)
                .then((response) => {
                    this.setState({ apiLoader: false })

                    if (response.status === 200) {
                        if (response.data.metadata.error === 1) {
                            Notification(response.data.metadata.message, "warning", "Warning");
                            return
                        }
                        // if(response.data.metadata.status_code === 400){
                        // Notification('Image Format is not support', "warning", "Warning");
                        //     return
                        // }

                        console.log(response.data.data.user.us_image, 'response')
                        let fields = this.state.fields;
                        fields['image_url'] = response.data.data.user.us_image;
                        this.setState({ fields }, () => this.setremainingdatas(response.data.data.token));




                    }
                })
                .catch((err) => {
                    console.log(err)
                })
        }
    }
    setremainingdatas = (token) => {
        this.updateToLocalStorage(token)
        this.props.updateState({ image_url: this.state.fields.image_url })
        this.props.updateState({ show_profile: true })
        this.props.updateState({ open_edit_personal_section: false })

        this.props.updateState({ fname: this.state.fields.fname })
        this.props.updateState({ phone: this.state.fields.phone })
        this.props.updateState({ email: this.state.fields.email })

        this.props.updateState({ country_code: this.state.fields.country_code })

        this.props.updateState({ country: this.state.fields.country })
        Notification("Personal data updated successfully", "success", "Success");


    }
    updateToLocalStorage(token) {

        let user = userData()
        if (user !== null) {
            console.log(this.state.fields)

            user.user_data['us_name'] = this.state.fields.fname
            // user.user_data['country_code'] = this.state.fields.lname
            user.user_data['us_country_code'] = this.state.fields.country_code
            user.user_data['us_country'] = this.state.fields.country
            user.user_data['us_email'] = this.state.fields.email
            user.user_data['us_phone'] = this.state.fields.phone
            user.user_data['us_image'] = this.state.fields.image_url

            user.access_token = token
            localStorage.setItem('mykademy_user', JSON.stringify(user))


        }
    }
    update = async (e) => {
        e.preventDefault()
        this.updateUserData()
        console.log(this.state.fields)
    }
    showFileUpload() {
        this.fileUpload.current.click()
    }
    onChange = e => {
        e.preventDefault();
        var extension = e.target.files[0].name.split('.').pop().toLowerCase()
        if (extension === 'png') {
            this.setState({ seletedFile: e.target.files[0] })
            return

        } else if (extension === 'jpeg') {
            this.setState({ seletedFile: e.target.files[0] })
            return
        }
        else if (extension === 'jpg') {
            this.setState({ seletedFile: e.target.files[0] })
            return
        } else {
            Notification("Image Format is not support!", "warning", "Warning")
            this.setState({ seletedFile: null })

        }

    };
    render() {
        return (
            <div id="personalstep" class="step active">
                {this.state.apiLoader &&
                    <div class="loading">foregroung loader&#8230;</div>
                }
                <div class="profile-header">
                    <div class="img-wrap">
                        <img src={this.state.seletedFile !== null ? window.URL.createObjectURL(this.state.seletedFile) : this.state?.fields?.image_url} alt="" />
                        <i onClick={this.showFileUpload} style={{ cursor: 'pointer' }} class=" editpencil_class fas fa-pencil-alt"></i>

                        <input onChange={e => this.onChange(e)} type="file" accept="image/png, image/jpg, image/jpeg " id="my_file" style={{ display: "none" }} ref={this.fileUpload} />

                    </div>
                    <h4>Profile</h4>
                </div>

                <div class="profile-update">
                    <span>Personal</span>
                </div>
                <form class="profile-form" action="#">
                    <div class="input-wrap">
                        <div class="form-group">
                            <label for="fname">Name</label>
                            <input value={this.state?.fields?.fname} type="text" style={this.state.errors['fname'] ? inputErr : {}} onChange={this.handleChange.bind(this, "fname")} class="form-control" name="fname" placeholder="Georgia" required />
                            {this.state.errors['fname'] &&
                                <p style={error_style}>{this.state.errors['fname']}</p>
                            }
                        </div>

                        <div class="form-group">
                            <label for="">Email</label>
                            <input value={this.state?.fields?.email} type="email" style={this.state.errors['email'] ? inputErr : {}} onChange={this.handleChange.bind(this, "email")} class="form-control" name="email" placeholder="GHarrision@forwardmomentum.com" required />
                            {this.state.errors['email'] &&
                                <p style={error_style}>{this.state.errors['email']}</p>
                            }
                        </div>
                        <div class="form-group">
                            <label for="country">Country</label>
                            <select

                                style={this.state.errors['country_name'] ? inputErr : {}}
                                name="country_name"
                                onChange={this.handleChange.bind(this, "country_name")}
                                className="form-control"

                            >
                                {this.state.countryJson.name &&
                                    <option value={this.state.countryJson.name}>{this.state.countryJson.name}</option>
                                }
                                {this.state.countries.map((country, i) => (
                                    <option data-value={country} key={i}>
                                        {country.name}
                                        {/* {country.country_name + " (+" + country.calling_code + ")"} */}
                                    </option>
                                ))}
                            </select>
                            {/* calling_code: "91"
country_code: "IN"
country_name: "India"
id: "101" */}
                            {this.state.errors['country_name'] &&
                                <p style={error_style}>{this.state.errors['country_name']}</p>
                            }
                            {/* <input type="text" onChange={this.handleChange.bind(this, "country")} class="form-control" name="country" placeholder="country name" required /> */}
                        </div>
                        <div class="form-group">
                            <label for="">Phone</label>
                            <input value={this.state?.fields?.phone} type="text" style={this.state.errors['phone'] ? inputErr : {}} onChange={this.handleChange.bind(this, "phone")} class="form-control" name="phone" placeholder="eg:1224355" required />
                            {this.state.errors['phone'] &&
                                <p style={error_style}>{this.state.errors['phone']}</p>
                            }
                        </div>
                    </div>

                    <div class="profile-modal-foo">
                        <button onClick={() => this.close()} type="button" class="btn common-course-btn btn-secondary" data-tab="profile-step">Close</button>
                        <button onClick={this.update} type="button" class="btn common-course-btn btn-primary" data-tab="profile-step">Update</button>
                    </div>
                </form>
            </div>
        );
    }
}

export default MkEditPersonalData;